import React, { useState, useEffect } from 'react';
import { Redirect, Link } from 'react-router-dom';

// Modals
import { Modal, Button } from 'react-bootstrap';



export const MyTickets = () => {

  const [state, setState] = useState({tickets: null, limit: 15})
  const [errors, setErrors] = useState({});

  const [modal, setModal] = useState(false);
  const [modalState, setModalState] = useState({});
  
  // Redirect
  const [redirect, setRedirect] = useState({
    redirect: false, to: null, flash: null
  });

  useEffect(() => {
    // Check If User is Logged-in
    if (!localStorage.maidan_jwt_$m3){
      setRedirect({
        redirect: true,
        to: '/'
      });
    }

    // Fetch Current User Tickets
    fetch('/api/user/tickets', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setState({
            tickets: data.tickets,
            limit: 15
          });
        } else {
          setRedirect({
            redirect: true,
            flash: {
              type: 'error',
              title: '',
              message: '   '
            },
            to: '/'
          });
        }
      })
    })

  }, [])

  const getDate = (seconds) => {
    var date = new Date(1970, 0, 1);
    date.setSeconds(seconds);
    var sloppy_date = new Date(date);
    var tzOffset = sloppy_date.getTimezoneOffset();
    
    var target_date = new Date(sloppy_date.getTime() - tzOffset * 60 * 1000);

    var yy = target_date.getFullYear();
    var mm = target_date.getMonth() + 1;
    var dd = target_date.getDate();

    var h = target_date.getHours();
    var m = target_date.getMinutes();

    return `${yy}-${(mm>9?'':'0')+mm}-${(dd>9?'':'0')+dd} ${(h>9?'':'0')+h}:${(m>9?'':'0')+m}`
  }

  const getStatus = (status) => {
    let statuses = {
      open: ["  ", "info"],
      waiting: [" ", "success"],
      closed: ["", "dark"]
    }

    return statuses[status]
  }

  const loadTickets = () => {
    const tickets_patch = state.tickets && state.tickets.slice(0, state.limit)

    return (
      <tbody>
        {
          tickets_patch ? tickets_patch.map((ticket, index) => {

            let status = getStatus(ticket.status)
            return(
              <tr key={index}>
                <th className="table-header">
                  <Link className="text-primary" to={`/user/tickets/${ticket.id}`}>{ticket.title}</Link>
                </th>
                <th className="table-header">#{ticket.id}</th>
                <th className="table-header text-secondary">{ticket.type}</th>
                <th dir='ltr' className="table-header">{getDate(ticket.created)}</th>
                <th dir='ltr' className="table-header">{ticket.last_activity}</th>
                <th className={`table-header text-${status[1]}`}>{status[0]}</th>
              </tr>
              )
          }) : null
        }
      </tbody>
    )
  }
  
  const loadErrors = () => {
    if (Object.keys(errors).length !== 0 && errors.constructor === Object){
      return (
        <div>
          {errors ? Object.entries(errors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  const AddLimit = () => {
    if ((state.limit < state.tickets.length)){
      let new_limit = state.limit + 15;
      setState({...state, limit: new_limit})
    }
  }

  const ReduceLimit = () => {
    
    if ((state.limit > 15)){
      let new_limit = state.limit - 15;
      setState({...state, limit: new_limit})
    }
  }

  const resetModals = () => {

    setModal(false);
    setModalState({});
  }

  /* New Ticket */
  const handleChange = (e) => {
    e.preventDefault();

    if (e.target.id === 'message') {
      var len = e.target.value.length;
      var elem = document.getElementById('charCount');

      if (len >= 600) {
        e.target.value = e.target.value.substring(0, 600);
      } 
      else {
        if (len > 450) {
          elem.classList.add("text-danger");
          elem.classList.remove("text-warning");
        } else if (len > 300) {
          elem.classList.add("text-warning");
        } else {
          elem.classList.remove("text-warning");
          elem.classList.remove("text-danger");
        }

      }

      elem.innerHTML = 600 - len;

    }

    setModalState({
      ...modalState,
      [e.target.id]: e.target.value
    })

  }

  const handleSubmit = (e) => {
    e.preventDefault();

    const submitTicket = async() => {
      await fetch('/api/user/tickets', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        body: JSON.stringify(modalState)
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok) {
            setState({
              ...state,
              tickets: data.tickets
            });
            resetModals();
          } else { 
            if (data.code === "INVALID_REQUEST") {
              setErrors({
                Request: {
                  name: "",
                  error: "     ,    .",
                  type: "warning"
                }
              })
            } 
            else {
              setErrors({
                Server: {
                  name: "",
                  error: "    ,     .",
                  type: "danger"
                }
              })
            }
          }
        })
      })
    }


    if (!["", "", ""].includes(modalState.type)) {
      setErrors({
        ...errors,
        type: {
          name: " ",
          error: "   ,    .",
          type: "danger"
        }
      })
    } else {
      submitTicket();
    }
  }
  /* New Ticket */

  if (redirect.redirect){
    return <Redirect to={{
      pathname: redirect.to,
      state: {flash: redirect.flash}
    }}/>
  }


  return(
    <div className="mb-5 pb-5 pt-5">
      
      <div className="container mt-5 pt-5">

        <div className="container text-center mt-5">
          <span className="display-7"></span>
          <hr/>
          <div className="container-fluid text-right">
            <button onClick={() => {setModal(true)}} className="btn btn-sm btn-outline-grey">
              <i className="fa fa-hand-paper fa-lg ml-2"/>  
            </button>
          </div>
          <div className="table-responsive">

            <table className="table table-bordered mt-4">
              <thead>
                <tr className="bg-light-grey">
                  <th className="table-header" scope="col"></th>
                  <th className="table-header" scope="col">ID</th>
                  <th className="table-header" scope="col"> </th>
                  <th className="table-header" scope="col"> </th>
                  <th className="table-header" scope="col"> </th>
                  <th className="table-header" scope="col"></th>
                </tr>
              </thead>
              {loadTickets()}
            </table>
          </div>
          <div className="justify-content-center">
            {(state.tickets && state.limit < state.tickets.length) ? (<button onClick={AddLimit} className="btn btn-sm btn-outline-primary"> </button>) : null}
            {state.limit > 15 ? (<button onClick={ReduceLimit} className="btn btn-sm btn-outline-secondary"> </button>) : null}
          </div>
        </div>

      </div>

      {/* Ticket Modal */}
      <Modal show={modal} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">  </Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">

          <div className="container">
            {loadErrors()}
          </div>

          <form id="ticketForm" onSubmit={handleSubmit}>

            <div className="row justify-content-center">

              <div className="col-sm-12 mt-2">
                <label htmlFor="title" className="font-weight-bold float-right">*  :</label>
                <div className="input-group">
                  <input onChange={handleChange} required id="title" type="text" placeholder=" (//)" className={"form-control col-sm-10 " + (errors.title ? "is-invalid" : "")}/>
                </div>
              </div>

              <div className="col-sm-12 mt-4">
                <label htmlFor="email" className="font-weight-bold float-right">  () :</label>
                <div className="input-group">
                  <input onChange={handleChange} id="email" type="email" placeholder="  ()" className={"form-control col-sm-10 " + (errors.email ? "is-invalid" : "")}/>
                </div>
              </div>

              <div className="col-sm-12 mt-4">
                <label htmlFor="type" className="font-weight-bold float-right">*   : </label>
                <div className="input-group">
                  <select onChange={handleChange} required id="type" className={"custom-select col-sm-10 " + (errors.type ? "is-invalid" : "")}>
                    <option value="" defaultValue> </option>
                    <option value="" className=""></option>
                    <option value="" className=""></option>
                    <option value="" className=""></option>
                  </select>
                </div>
              </div>

              <div className="col-sm-12 mt-4">
                <label htmlFor="message" className="font-weight-bold float-right">*   : </label>
                <div className="input-group">
                  <textarea onChange={handleChange} required id="message" rows="12" maxLength="600" placeholder=" (//)" className={"form-control col-sm-12 " + (errors.message ? "is-invalid" : "")}/>
                </div>
                <div className="float-left font-weight-bold" id="charCount">600</div>
              </div>

            </div>

          </form>
          
        </Modal.Body>
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={resetModals}>
              
            </Button>
            <Button form="ticketForm" type="submit" variant="primary">
               
            </Button>
          </div>
        </Modal.Footer>
      </Modal>
    
    </div>
  )
}