import React, { useState, useEffect } from 'react';

// Modals
import { Modal, Button } from 'react-bootstrap';
import { Redirect } from 'react-router-dom';


const BalanceRequests = () => {

  const [requests, setRequests] = useState({requests: null, limit: 50});

  const [state, setState] = useState({});
  const [redirect, setRedirect] = useState({redirect: false, flash: null})

  const [errors, setErrors] = useState({});
  const [isOk, setOk] = useState(false);
  const [isBad, setBad] = useState(false);

  useEffect(() => {
    fetch('/api/panel/balance/requests', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setRequests({
            requests: data.requests,
            limit: 50
          })
        } else {
          if ([
            'INVALID_AUTH_HEADER', 
            'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
            'ACCESS_DENIED', 'AUTH_ERROR'
          ].includes(data.code)) {
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
          if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
            localStorage.clear();
            sessionStorage.clear();
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
        }
      })
    })
  }, [])


  const LoadRequests = () => {

    const handleAR = (request, status) => {

      setState(request);

      if (status === 'A'){
        setOk(true);
      } else {
        setBad(true)
      }

    }

    const getMethod = (status) => {
      let types = {
        paypal: ['', 'primary'],
        bank: [' ', 'secondary'],
        mada: ['', 'primary']
      }

      return types[status]
    }

    const getStatus = (verified) => {

      if (verified) {
        return ['', 'primary']
      }
      return [' ', 'secondary']
    }

    const requests_patch = requests.requests && requests.requests.slice(0, requests.limit);

    return(
      <tbody>
        {
          requests_patch ? requests_patch.map((req, index) => {
            let method = getMethod(req.method);
            let status = getStatus(req.verified);
            return(
              <tr className="" key={index}>
                <th className="table-header">{index+1}</th>
                <td className="table-header">{req.username}</td>
                <td className="table-header">{req.user_id} #</td>
                <td className="table-header">{req.fullname || '.'}</td>
                <td className="table-header">{req.email}</td>
                <td dir='ltr' className="table-header">{req.phone}</td>
                <td className={`table-header text-${method[1]}`}>{method[0]}</td>
                <td className="table-header">{req.bank_account || '.'}</td>
                <td className="table-header">{req.amount}</td>
                <td className={`table-header text-${status[1]}`}>{status[0]}</td>
                {
                  req.verified ? (
                    <td className="table-header text-success"></td>
                  ) : (
                    <td className="table-header">
                      <button onClick={() => {handleAR(req, 'R')}} className="btn btn-sm btn-outline-danger"></button>
                      <button onClick={() => {handleAR(req, 'A')}} className="btn btn-sm btn-outline-success"></button>
                    </td>
                  )
                }
              </tr>
            )
          }) : null
        }
      </tbody>
    )
  }

  const AddLimit = () => {
    if (requests.limit < requests.requests.length){
      let new_limit = requests.limit + 50;
      setRequests({...requests, limit: new_limit})
    }
  }

  const ReduceLimit = () => {
    if (requests.limit > 50){
      let new_limit = requests.limit - 50;
      setRequests({...requests, limit: new_limit})
    }
  }

  const handleChange = (e) => {
    e.preventDefault();

    setState({
      ...state,
      [e.target.id]: e.target.value
    })

  }

  const handleApproved = async(e) => {
    e.preventDefault();

    await fetch(`/api/panel/balance/requests/${state.id}/accept`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      },
      body: JSON.stringify({amount: state.amount})
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          // Reset Modals
          resetModals();
          // Update Requests
          setRequests({
            ...requests,
            requests: data.requests
          })
        } else {
          if (data.code === 'SERVER'){
            setErrors({
              Server: {
                name: '',
                error: '    .',
                type: 'danger'
              }
            })
          }
          if (data.code === 'VERIFIED'){
            setErrors({
              Server: {
                name: '',
                error: '    ,   .',
                type: 'warning'
              }
            })
          }
          if (data.code === 'NO_MATCH_ID'){
            setErrors({
              Server: {
                name: ' ',
                error: '     ',
                type: 'danger'
              }
            })
          }
          if (data.code === 'INVALID_REQUEST'){
            setErrors({
              Server: {
                name: '',
                error: '     ,     .',
                type: 'danger'
              }
            })
          }
          if (data.code === 'INVALID_REQUEST'){
            setErrors({
              Server: {
                name: '',
                error: '       ',
                type: 'warning'
              }
            })
          }
          if (data.code === 'NO_USER'){
            setErrors({
              Server: {
                name: '',
                error: '     ,    .',
                type: 'danger'
              }
            })
          }
          if ([
            'INVALID_AUTH_HEADER', 
            'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
            'ACCESS_DENIED', 'AUTH_ERROR'
          ].includes(data.code)) {
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
          if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
            localStorage.clear();
            sessionStorage.clear();
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
        }
      })
    })
    
  }

  const handleReject = async(e) => {
    e.preventDefault();

    await fetch(`/api/panel/balance/requests/${state.id}/reject`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          // Reset Modals
          resetModals();
          // Update Requests
          setRequests({
            ...requests,
            requests: data.requests
          })
        } else {
          if (data.code === 'SERVER'){
            setErrors({
              Server: {
                name: '',
                error: '     ',
                type: 'danger'
              }
            })
          }
          if (data.code === 'NO_MATCH_ID'){
            setErrors({
              Server: {
                name: ' ',
                error: ' ,    ,    .',
                type: 'warning'
              }
            })
          }
          if (data.code === 'VERIFIED'){
            setErrors({
              Server: {
                name: '',
                error: '     ,        .',
                type: 'danger'
              }
            })
          }
          if ([
            'INVALID_AUTH_HEADER', 
            'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
            'ACCESS_DENIED', 'AUTH_ERROR'
          ].includes(data.code)) {
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
          if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
            localStorage.clear();
            sessionStorage.clear();
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
        }
      })
    })

  }

  const loadErrors = () => {
    if (Object.keys(errors).length !== 0 && errors.constructor === Object){
      return (
        <div>
          {errors ? Object.entries(errors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  const resetModals = () => {
    // Reset State & Errors
    setState({});
    setErrors({});
    // Reset Modals
    setOk(false);
    setBad(false);
  }

  if (redirect.redirect){
    return <Redirect to={{
      pathname: '/',
      state: {flash: redirect.flash}
    }}/>
  }

  return(
    <div className="table-responsive mt-4">
      
      <table className="table table-sm table-bordered">
        <thead>
          <tr className="bg-light-grey">
            <th className="font-weight-bold" scope="col">#</th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col">  (ID)</th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col">  ()</th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col">  ()</th>
            <th className="font-weight-bold" scope="col"> ($)</th>
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col">/</th>
          </tr>
        </thead>
        {LoadRequests()}
      </table>
      <div className="justify-content-center">
        {(requests.requests && requests.limit < requests.requests.length) ? (<button onClick={AddLimit} className="btn btn-sm btn-outline-primary"> </button>) : null}
        {requests.limit > 50 ? (<button onClick={ReduceLimit} className="btn btn-sm btn-outline-secondary"> </button>) : null}
      </div>

      {/* Bad Modal */}
      <Modal show={isBad} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">  </Modal.Title>
        </Modal.Header>
        
        <Modal.Body className="text-center mt-2">
          {loadErrors()}

          <p className="font-weight-bold text-danger">
                        .
          </p>

          <p className="font-info text-primary">
                ,    .
          </p>
          
        </Modal.Body>
        
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={resetModals}>
              
            </Button>
            <Button onClick={handleReject} variant="danger">
              
            </Button>
          </div>
        </Modal.Footer>

      </Modal>

      {/* Ok Modal */}
      <Modal show={isOk} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">  </Modal.Title>
        </Modal.Header>
        
        <Modal.Body className="text-center mt-2">
          {loadErrors()}

          <form id="mainForm" onSubmit={handleApproved}>

            <div className="container mt-2">

              <div className="row text-right">

                <div className="col-sm-10">
                  <label htmlFor="amount" className="font-weight-bold">*   :</label>
                  <div className="input-group">
                    <input required onChange={handleChange} id="amount" type="number" min="0" step="0.01" defaultValue={state.amount} className="form-control col-sm-10"/>
                  </div>
                  <p className="font-info text-info">
                         ,    .
                  </p>
                </div>

              </div>

            </div>

          </form>
          
        </Modal.Body>
        
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={resetModals}>
              
            </Button>
            <Button type="submit" form="mainForm" variant="success">
              
            </Button>
          </div>
        </Modal.Footer>

      </Modal>

   </div>
  )
}


export default BalanceRequests