import React, { useState, useEffect } from 'react';
import { Redirect } from 'react-router-dom';

// Modals
import { Modal, Button } from 'react-bootstrap';


const FilesTable = () => {

  const [uploading, setUploading] = useState(false);
  const [files, setFiles] = useState({files: null, limit: 50});

  const [errors, setErrors] = useState({});
  const [redirect, setRedirect] = useState({redirect: false, flash: null})

  // Modals
  const [state, setState] = useState({});
  const [isNew, setNewModal] = useState(false);
  const [isDelete, setDeleteModal] = useState({delete: false, id: null});

  useEffect(() => {
    fetch('/api/panel/files', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setFiles({
            files: data.files,
            limit: 50
          })
        } else {
          if ([
            'INVALID_AUTH_HEADER', 
            'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
            'ACCESS_DENIED', 'AUTH_ERROR'
          ].includes(data.code)) {
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
          if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
            localStorage.clear();
            sessionStorage.clear();
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
        }
      })
    })
  }, [])


  const handleChange = (e) => {
    e.preventDefault();
    // Reset Errors

    setErrors({});

    setState({
      ...state,
      [e.target.id]: e.target.value
    })
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    const SubmitFiles = async() => {
      await fetch('/api/panel/files', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
        },
        body: JSON.stringify(state)
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            setFiles({
              ...files,
              files: data.files
            });
            resetModals();
          } else {
            if ([
              'INVALID_AUTH_HEADER', 
              'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
              'ACCESS_DENIED', 'AUTH_ERROR'
            ].includes(data.code)) {
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      .'
                }
              })
            }
            if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
              localStorage.clear();
              sessionStorage.clear();
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      .'
                }
              })
            }
          }
        })
      })
    }

    // Check If Files Are Uploaded (Basic Method)
    let error = false;
    if (state.files){
      for (let i=0;i<state.files.length;i++){
        if (state.files[i].status === 'success'){
          break;
        } else {
          if (i === (state.files.length - 1)){
            setErrors({
              Files: {
                name: '',
                error: '  ',
                type: 'danger'
              }
            })
            error = true;
          }
        }
      }
    } else {
      setErrors({
        Files: {
          name: '',
          error: '  ',
          type: 'danger'
        }
      })
      error = true;
    }

    if (!error){
      SubmitFiles();
    }
  }

  const handleFileChange = (e) => {
    let target_file = e.target;
    let filesToUpload = target_file.files;
    // Reset File Names State
    setState({
      ...state,
      files: []
    });
  
    if (e.target.value) {
      // Reset File Errors
      let prev_errors = {...errors};
      delete prev_errors['Files']
      setErrors(prev_errors)

      var i;
      // Num Of Declined Files (MisType)
      var declined = 0;

      // Loop Through Files
      for (i=0; i < filesToUpload.length; i++){
        var filename = filesToUpload[i].name;
        var ex = filename.split('.').reverse()[0];
        let acceptedTypes = ['application/pdf', 'image/png', 'image/jpg'];
        let acceptedEx = ['zip', 'rar', 'pdf', 'png', 'jpg', 'docs'];
        if (acceptedTypes.includes(filesToUpload[i].type) || acceptedEx.includes(ex)){
          // Nothing Happens
        } else {
          declined += 1
        }
      }
      if (declined > 0){
        setErrors({
          Files: {
            name: '',
            error: '        ,      (zip/rar/pdf/png/jpg)',
            type: 'danger'
          }
        })
      } else {
        document.getElementById('FilesLabel').innerHTML = `(${filesToUpload.length}) Files Selected`;
        // Display Upload Status
        document.getElementById('uploadStatus').innerHTML = "  "
        setUploading(true);
        UploadFiles();
      }
    }
  }

  const UploadFiles = async() => {
    let formData = new FormData()
    const files = document.getElementById('files').files;

    // Loop Throw Files.
    for (let i = 0; i < files.length; i++){
      formData.append(`file${i}`, files[i])
    }
    
    await fetch('/api/panel/files/upload', {
      method: 'POST',
      headers: {
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      },
      body: formData
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setState({
            ...state,
            files: data.files
          })
          // Update Status Display
          document.getElementById('uploadStatus').innerHTML = "  !"
          document.getElementById('uploadStatus').classList.add('text-success')
          document.getElementById('uploadStatus').classList.remove('text-info')
        } else {
          // Update Status Display
          document.getElementById('uploadStatus').innerHTML = "    "
          document.getElementById('uploadStatus').classList.add('text-danger')
          document.getElementById('uploadStatus').classList.remove('text-info')
          if ([
            'INVALID_AUTH_HEADER', 
            'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
            'ACCESS_DENIED', 'AUTH_ERROR'
          ].includes(data.code)) {
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
          if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
            localStorage.clear();
            sessionStorage.clear();
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
        }
      })
    })

    // Activate Submittion Button
    setUploading(false);
  }

  const handleDelete = (e) => {
    e.preventDefault();

    const DeleteFile = async() => {
      await fetch(`/api/panel/files/${e.target.id}`, {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
        }
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            setFiles({
              ...files,
              files: data.files
            })
            resetModals();
          } else {
            if ([
              'INVALID_AUTH_HEADER', 
              'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
              'ACCESS_DENIED', 'AUTH_ERROR'
            ].includes(data.code)) {
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      .'
                }
              })
            }
            if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
              localStorage.clear();
              sessionStorage.clear();
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      .'
                }
              })
            }
          }
        })
      })
    }

    DeleteFile();
  }

  const LoadFiles = () => {

    const getType = (type) => {
      let types = {
        smallAds: '  ',
        carouselAds: "  ",
        providersAgreement: "  "
      }

      return types[type]
    }

    const handleDownload = async(e) => {
      e.preventDefault();
      await fetch(`/api/panel/files/${e.target.id}`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          // Authorization
        }
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            window.open(data.download_link)
          }
        })
      })
    }

    const files_patch = files.files && files.files.slice(0, files.limit);

    return(
      <tbody>
        {
          files_patch ? files_patch.map((file, index) => (
            <tr className="" key={index}>
              <th className="table-header">{index+1}</th>
              <td className="table-body">{file.file_name}</td>
              <td className="table-body">{file.original_name}</td>
              <td className="table-body">{getType(file.key)}</td>
              <td className="table-body">{file.description}</td>
              <td className="table-body">
                <button onClick={() => {setDeleteModal({delete: true, id: file.id})}} className="btn btn-sm btn-outline-danger font-weight-bold"></button>
                <button id={file.file_name} onClick={handleDownload} className="btn btn-sm btn-outline-success font-weight-bold"></button>
              </td>
            </tr>
          )) : null
        }
      </tbody>
    )
  }

  const AddLimit = () => {
    if (files.limit < files.files.length){
      let new_limit = files.limit + 50;
      setFiles({...files, limit: new_limit})
    }
  }

  const ReduceLimit = () => {
    if (files.limit > 50){
      let new_limit = files.limit - 50;
      setFiles({...files, limit: new_limit})
    }
  }

  const resetModals = () => {
    // Reset All Modals & Files State
    setState({names: []});
    setErrors({});

    setNewModal(false);
    setDeleteModal({delete: false, id: null});
  }

  const loadErrors = () => {
    if (Object.keys(errors).length !== 0 && errors.constructor === Object){
      return (
        <div>
          {errors ? Object.entries(errors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  if (redirect.redirect){
    return <Redirect to={{
      pathname: '/',
      state: {flash: redirect.flash}
    }}/>
  }

  return(
    <div className="table-responsive mt-4">
      
      <table className="table table-sm table-bordered">
        <thead>
          <tr className="bg-light-grey">
            <th className="font-weight-bold" scope="col">ID</th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col">  ()</th>
            <th className="font-weight-bold" scope="col"></th>
          </tr>
        </thead>
        {LoadFiles()}
      </table>
      <div className="justify-content-center">
        {(files.files && files.limit < files.files.length) ? (<button onClick={AddLimit} className="btn btn-sm btn-outline-primary"> </button>) : null}
        {files.limit > 50 ? (<button onClick={ReduceLimit} className="btn btn-sm btn-outline-secondary"> </button>) : null}
        <div className="float-right">
          <button onClick={() => {setNewModal(true)}} className="btn btn-sm btn-outline-success">
            <i className="fa fa-plus"/>  
          </button>
        </div>
      </div>
    

      {/* New Files Modal */}
      <Modal show={isNew} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8"> </Modal.Title>
        </Modal.Header>
        
        <Modal.Body className="text-center">
          {loadErrors()}

          <form id="mainForm" onSubmit={handleSubmit}>

            <div className="container">

              <div className="row text-right">

                <div className="col-sm-8">
                  <label htmlFor="key" className="font-weight-bold">*   : </label>
                  <select onChange={handleChange} required name="key" id="key" className="custom-select col-sm-12">
                    <option value="" defaultValue>  </option>
                    <option value="smallAds" defaultValue>  </option>
                    <option value="carouselAds" defaultValue>  </option>
                    <option value="providersAgreement" defaultValue>  </option>
                  </select>
                </div>

                <div className="col-sm-8 mt-4">
                  <label htmlFor="description" className="font-weight-bold">*   ( 64 ) : </label>
                  <div className="input-group">
                    <textarea onChange={handleChange} required id="description" placeholder=" " className="form-control"/>
                  </div>
                </div>

              </div>

            </div>

          </form>

          <form encType="multipart/form-data">

            <div className="container mt-4">

              <div className="text-right">
                <p className="font-info text-muted font-weight-bold">
                  <i className="fa fa-star-of-life ml-2"/>   : (png, jpg, zip, rar, pdf, docx)
                </p>
                <p className="font-info text-muted font-weight-bold">
                  <i className="fa fa-star-of-life ml-2"/>     : (560x235).
                </p>
                <p className="font-info text-muted font-weight-bold">
                  <i className="fa fa-star-of-life ml-2"/>     : (1870x465).
                </p>
                <p className="font-info text-muted font-weight-bold">
                  <i className="fa fa-star-of-life ml-2"/>     ,     rar  zip.
                </p>
              </div>

              <div className="row text-right">

                <div className="col-sm-12">
                  <label htmlFor="files" className="font-weight-bold float-right">*   : </label>
                    <div className="custom-file">
                      <input required multiple type="file" onChange={handleFileChange} id="files" className={"custom-file-input " + (errors['Files'] ? 'is-invalid' : '')}/>
                      <label id="FilesLabel" className="custom-file-label col-sm-12" htmlFor="files"/>
                    </div>
                </div>

              </div>

            </div>

            <p id="uploadStatus" className="font-info qsemh-turq"></p>
            
            {
              state.files && state.files.length > 0 ? (
                <div className="container text-right mt-4">
                  <span className="font-weight-bold"> : </span>

                  <div className="mt-2">
                    {
                      state.files.map((file, index) => (
                        <p key={index} className="font-weight-bold font-info text-muted">
                          {file.name}
                          {
                            file.status === 'success' ? (
                              <i title="" className="fa fa-check-circle text-success fa-s mr-2"/>
                            ) : (
                              <i title=",   " className="fa fa-times-circle text-danger fa-s mr-2"/>
                            )
                          }
                        </p>
                      ))
                    }
                  </div>
                </div>
                
              ) : null
            }

          </form>
          
        </Modal.Body>
        
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="outline-grey" onClick={resetModals}>
              
            </Button>
            <Button id="submitBtn" disabled={uploading} type="submit" variant="outline-success"  form="mainForm">
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>
      
      {/* Confirm Delete Modal */}
      <Modal show={isDelete.delete} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">   (#{isDelete.id})</Modal.Title>
        </Modal.Header>
        
        <Modal.Body className="text-center">
          {loadErrors()}

          <p className="font-weight-bold text-danger">
                  
          </p>
          
        </Modal.Body>
        
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={resetModals}>
              
            </Button>
            <Button id={isDelete.id} onClick={handleDelete} type="submit" variant="danger">
               
            </Button>
          </div>
        </Modal.Footer>
      </Modal>
      
    </div>
  )
}


export default FilesTable