import React, { useState, useEffect } from 'react';
import { Redirect, Link } from 'react-router-dom';
import copy from 'copy-to-clipboard'

// Modals
import { Modal, Button } from 'react-bootstrap';


export const RedeemCoupons = (props) => {

  const URLQuery = new URLSearchParams(props.location.search)

  const [QRCode, setQRCode] = useState(URLQuery.get('qrcode'));

  const [redirect, setRedirect] = useState({redirect: false, flash: null});
  const [errors, setErrors] = useState({});

  // Access Token Validation
  // const [token, setToken] = useState();

  // Current Page State (code_input, validate, progress, view)
  const [state, setState] = useState('code_input');

  // View Coupon Information
	const [info, setInfo] = useState({coupon: null, group: null})
	
	// Confirmation
	const [confirmModal, setConfirmModal] = useState(false);
	const [modalErrors, setModalErrors] = useState({});

  /*eslint-disable */
  //suppress all warnings between comments
  useEffect(() => {
    fetch('/api/authenticate/associate', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
        
        } else {
          if (data.code === 'SERVER'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      ,     '
              }
            })
          }
          else {
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
          
        }
      })
    })

    if(QRCode){
      fetch('/api/user/coupon/validate_provider', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
        },
        body: JSON.stringify({qr_code: QRCode})
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            setInfo({
              coupon: data.coupon,
              group: data.group
            });
            setState('view');
          } else {
            if (data.code === 'SERVER'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      ,     '
                }
              })
            }
            if ([
              'INVALID_AUTH_HEADER', 
              'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
              'ACCESS_DENIED', 'AUTH_ERROR'
            ].includes(data.code)) {
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      .'
                }
              })
            }
            if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
              localStorage.clear();
              sessionStorage.clear();
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      .'
                }
              })
            }
            if (data.code === 'INVALID_GROUP_COMPANY'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '       .'
                }
              })
            }
            if (data.code === 'NO_MATCH_COUPON'){
              setErrors({
                qr_code: {
                  name: ' ',
                  error:`       (${QRCode}),     .`,
                  type: 'warning'
                }
              })
            }
            if (data.code === 'NO_PROVIDER'){
              setErrors({
                noProvider: {
                  name: '',
                  error: "   ,     ,    .",
                  type: 'danger'
                }
              })
            }
          }
        })
      })
    }
  }, [])
  /*eslint-enable */

  const loadCodeForm = () => {

    const handleSubmit = (e) => {
      e.preventDefault();

      const ValidateQRCode = async() => {
        await fetch('/api/user/coupon/validate_provider', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json',
            'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
          },
          body: JSON.stringify({qr_code: QRCode})
        }).then(resp => {
          resp.json().then(data => {
            if (resp.ok){
              setInfo({
                coupon: data.coupon,
                group: data.group
              });
              setState('view');
            } else {
              if (data.code === 'SERVER'){
                setRedirect({
                  redirect: true,
                  flash: {
                    type: 'error',
                    title: '',
                    message: '      ,     '
                  }
                })
              }
              if ([
                'INVALID_AUTH_HEADER', 
                'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
                'ACCESS_DENIED', 'AUTH_ERROR'
              ].includes(data.code)) {
                setRedirect({
                  redirect: true,
                  flash: {
                    type: 'error',
                    title: '',
                    message: '      .'
                  }
                })
              }
              if (data.code === 'INVALID_GROUP_COMPANY'){
                setRedirect({
                  redirect: true,
                  flash: {
                    type: 'error',
                    title: '',
                    message: '       .'
                  }
                })
              }
              if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
                localStorage.clear();
                sessionStorage.clear();
                setRedirect({
                  redirect: true,
                  flash: {
                    type: 'error',
                    title: '',
                    message: '      .'
                  }
                })
              }
              if (data.code === 'NO_MATCH_COUPON'){
                setErrors({
                  qr_code: {
                    name: ' ',
                    error:`       (${QRCode}),     .`,
                    type: 'warning'
                  }
                })
              }
              if (data.code === 'NO_PROVIDER'){
                setErrors({
                  noProvider: {
                    name: '',
                    error: "   ,     ,    .",
                    type: 'danger'
                  }
                })
              }
            }
          })
        })
      }

      ValidateQRCode();
    }

    const handleChange = (e) => {
      e.preventDefault();

      setErrors({});

      setQRCode(e.target.value);
    }

    return (
      <form onSubmit={handleSubmit}>

        <div className="container text-center">

          <span className="display-8 qsemh-turq">
                   .
          </span>

        </div>

        <hr/>

        <div className="container mt-4">

          <div className="row">

            <div className="col-sm-4 text-right">

              <label htmlFor="qr_code" className="font-weight-bold">  : </label>
              <div className="input-group">
                <input onChange={handleChange} required type="text" id="qr_code" placeholder="    " className={"form-control col-sm-12 " + (errors['qr_code'] ? 'is-invalid' : '')}/>
                <div className="input-group-append">
                  <button type="submit" className="btn-sm btn-outline-dark font-weight-bold"></button>
                </div>
              </div>
              
            </div>

          </div>

        </div>

      </form>
    )
  }

  const ConfirmationProgress = () => (
    <div className="container text-center mt-5">
      <p className="text-info font-weight-bold">    </p>
      <div className="spinner-border text-info display-8" role="status">
        <span className="sr-only">Loading...</span>
      </div>
    </div>
	)
  
  
	const handleConfirm = (e) => {
		e.preventDefault();

		const RedeemCoupon = async() => {
			await fetch('/api/user/coupon/redeem', {
				method: 'POST',
				headers: {
					'Content-Type': 'application/json',
					'Accept': 'application/json',
					'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
				},
				body: JSON.stringify({coupon_id: info.coupon.id})
			}).then(resp => {
				resp.json().then(data => {
					if (resp.ok){
						setRedirect({
							redirect: true,
							flash: {
								type: 'success',
								title: ' ',
								message: '    !'
							}
						})
					} else {
						if (data.code === 'SERVER'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      ,     '
                }
              })
            }
            if ([
              'INVALID_AUTH_HEADER', 
              'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
              'ACCESS_DENIED', 'AUTH_ERROR'
            ].includes(data.code)) {
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      .'
                }
              })
            }
            if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
              localStorage.clear();
              sessionStorage.clear();
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      .'
                }
              })
            }
						if (data.code === 'NO_MATCH_COUPON'){
              setModalErrors({
                coupon_id: {
                  name: ' ',
                  error:`       (${QRCode}),     .`,
                  type: 'danger'
                }
              })
            }
						if (data.code === 'ONHOLD_COUPON'){
							setModalErrors({
								onholdStatus: {
									name: ' ',
									error: '  ,     .',
									type: 'warning'
								}
							})
						}
						if (data.code === 'USED_COUPON'){
							setModalErrors({
								onholdStatus: {
									name: ' ',
									error: '   ,     !',
									type: 'warning'
								}
							})
						}
						if (data.code === 'EXPIRED_COUPON'){
							setModalErrors({
								expired: {
									name: ' ',
									error: '  !',
									type: 'danger'
								}
							})
						}
						if (data.code === 'CANCELED_COUPON'){
							setModalErrors({
								canceled: {
									name: ' ',
									error: '   ,      .',
									type: 'warning'
								}
							})
						}
					}
				})
			})
		}

		RedeemCoupon();

	}

  const CouponInfo = () => {

    const OpenBranchMap = (e) => {
      e.preventDefault();
      window.open(e.target.id)
    }

    const getStatus = (status) => {

      let statuses = {
        available: [
          'text-success', ' '
        ],
        onhold: [
          'text-grey', ' '
        ],
        expired: [
          'text-danger', ' '
        ],
        used: [
          'text-warning', ''
        ],
        canceled: [
          'text-dark', ''
        ]
      }
  
      return statuses[status]
    }

    const getDate = (seconds) => {
      var date = new Date(1970, 0, 1);
      date.setSeconds(seconds);

      var sloppy_date = new Date(date);
      var tzOffset = sloppy_date.getTimezoneOffset();
      
      var target_date = new Date(sloppy_date.getTime() - tzOffset * 60 * 1000);
  
      var yy = target_date.getFullYear();
      var mm = target_date.getMonth() + 1;
      var dd = target_date.getDate();
  
      var h = target_date.getHours();
      var m = target_date.getMinutes();
      var s = target_date.getSeconds();
  
      return `${yy}-${(mm>9?'':'0')+mm}-${(dd>9?'':'0')+dd} ${(h>9?'':'0')+h}:${(m>9?'':'0')+m}:${(s>9?'':'0')+s}`
    }

    let status = getStatus(info.coupon.coupon_status)

    return(
      <div className="container text-center">

        <span className="display-8 qsemh-orange"> </span>

        <hr/>

				<p className="text-muted lead">
					<i className="fa fa-star-of-life fa-s ml-2"/>
					    <span className="text-success font-weight-bold">   </span>
					   ,  .
				</p>

        <div className="table-responsive">

          <table className="table table-sm table-bordered mt-4">
            
						<tbody>

              <tr>
                <th className="th-w table-header text-dark"></th>
                <td className="table-header text-muted">{info.group.company}</td>
              </tr>

              <tr>
                <th className="th-w table-header text-dark"></th>
                <td className="table-header text-muted">{info.group.type}</td>
              </tr>

              <tr>
                <th className="th-w table-header text-dark"></th>
                <td className="table-header text-muted">{info.group.country}</td>
              </tr>

              <tr>
                <th className="th-w table-header text-dark"></th>
                <td className="table-header">
                {info.group.branches.map((branch, index) => {
                  if (info.group.branches.length === (index+1)){
                    return(<span key={index}><Link className="text-info" to="#" id={branch.map} onClick={OpenBranchMap}>{branch.city}-{branch.name}</Link>.</span>)
                  } else {
                    return(<span key={index}><Link className="text-info" to="#" id={branch.map} onClick={OpenBranchMap}>{branch.city}-{branch.name}</Link>, </span>)
                  }
                })}
                </td>
              </tr>

              <tr>
                <th className="th-w table-header text-dark"></th>
                <td dir='ltr' className="table-header text-muted">{getDate(info.group.start_date)}</td>
              </tr>

              <tr>
                <th className="th-w table-header text-dark"></th>
                <td dir='ltr' className="table-header text-muted">{getDate(info.group.expire_date)}</td>
              </tr>

              <tr>
                <th className="th-w table-header text-dark">QRCode</th>
                <td className="table-header text-muted">{info.coupon.qr_code}</td>
              </tr>

              <tr>
                <th className="th-w table-header text-dark"></th>
                <td className={`table-header ${status[0]}`}>{status[1]}</td>
              </tr>

							<tr>
                <th className="th-w table-header text-dark"></th>
                <td className="table-header text-muted">{info.group.coupon_price}$ ({info.group.coupon_price*3.75}  )</td>
              </tr>

							<tr className="mt-4">
                <th className="th-w table-header-lg text-dark"></th>
                <td className="table-header-lg"><Link title="  " to="#" onClick={() => {copy(info.coupon.coupon_code)}} className="text-primary">{info.coupon.coupon_code}</Link></td>
              </tr>

            </tbody>
          
					</table>

        </div>
								
				<div className="container mt-4">

					{
            info.coupon && info.coupon.coupon_status === "available" ? (
              <div className="justify-content-center">
                <button onClick={() => {setConfirmModal(true)}} className="btn btn-lg btn-success"> </button>
              </div>
            ) : null
          }

					<div className="justify-content-center">
						<button onClick={() => {setRedirect({redirect: true, flash: null})}} className="btn btn-md btn-grey"></button>
					</div>

				</div>

			</div>
    )
  }

  const LoadContent = () => {
    switch(state){
      case 'code_input':
        return loadCodeForm();
      case 'progress':
        return ConfirmationProgress();
      case 'view':
        return CouponInfo();
      default:
        break;
    }
  }

  const loadErrors = () => {
    if (Object.keys(errors).length !== 0 && errors.constructor === Object){
      return (
        <div>
          {errors ? Object.entries(errors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
	}
	
	const loadModalErrors = () => {
    if (Object.keys(modalErrors).length !== 0 && modalErrors.constructor === Object){
      return (
        <div>
          {modalErrors ? Object.entries(modalErrors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  if (redirect.redirect){
    return <Redirect to={{
      pathname: '/',
      state: {flash: redirect.flash}
    }} />
  }

  return(
    <div className="mb-5 pb-5 pt-5">

      <div className="mt-5 pt-5">
        <div className="container">
          {loadErrors()}
        </div>
        {LoadContent()}
      </div>

			{/* Confirm Redeem */}
			<Modal show={confirmModal} onHide={() => {setConfirmModal(false)}}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">  </Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">
					<div className="container mt-2">
						{loadModalErrors()}
					</div>
          <p className="mt-2 lead text-primary">
               ,       .
          </p>
					<p className="mt-4 font-info text-muted">
						<i className="fa fa-star-of-life fa-xs ml-2"/>
						       ,       <Link to="#" onClick={() => window.open("/terms")} className="text-danger"> </Link>
					</p>
        </Modal.Body>
        <Modal.Footer>
					<div className="input-group justify-content-center">
            <Button variant="outline-grey" onClick={() => {setConfirmModal(false)}}>
              
            </Button>
						<Button variant="outline-success" onClick={handleConfirm}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>
   
    </div>
  )
}