import React, { useState, useEffect } from 'react';
import { Link, Redirect } from 'react-router-dom';

// Modals
import { Modal, Button } from 'react-bootstrap';


const Histories = () => {

  const [histories, setHistories] = useState({histories: null, limit: 50});
  const [types, setTypes] = useState([]);
  const [errors, setErrors] = useState({});
  const [redirect, setRedirect] = useState({redirect: false, flash: null});

  // Filters
  const [filters, setFilters] = useState({
    search: null,
    entity_type: null,
    record_type: null,
    record_state: null,
    start_date: null,
    end_date: null
  });
  
  // Extractor
  const [extractor, setExtractor] = useState({view: false, records: ""});

  /*eslint-disable */
  //suppress all warnings between comments
  useEffect(() => {
    // Fetch History Data
    fetch('/api/histories', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setErrors({}); // reset Errors
          setHistories({
            ...histories,
            histories: data.histories,
            limit: 50
          })
          if (data.histories.length === 0) {
            setErrors({
              NotFound: {
                name: "",
                error: "   ",
                type: 'warning'
              }
            })
          }
        } else {
          if (data.code === "SERVER") {
            setErrors({
              Server: {
                name: "",
                error: "    ",
                type: 'danger'
              }
            })
          }
          if ([
            'INVALID_AUTH_HEADER', 
            'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
            'ACCESS_DENIED', 'AUTH_ERROR'
          ].includes(data.code)) {
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
          if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
            localStorage.clear();
            sessionStorage.clear();
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
        }
      })
    });
  }, [])
  /*eslint-enable */
  
  const LoadFilters = () => {

    const handleChange = (e) => {
      e.preventDefault();
      setErrors({});
      if (e.target.id === "entity_type") {
        document.getElementById("record_type").value = '';
        // Types
        var Users = [
          {name: " ", value: " "},
          {name: " ", value: " "},
          {name: "   ", value: "   "},
          {name: "  ", value: "  "},
          {name: "  ", value: "  "},
          {name: "  ", value: "  "},
          {name: "  ", value: "  "},
          {name: "  ", value: "  "},
          {name: "   ", value: "  "},
          {name: "   ", value: "   "},
          {name: "    ( )", value: "    ( )"},
          {name: "    ( )", value: "    ( )"},
          {name: " ", value: " "},
          {name: "  ()", value: " "},
          {name: "  ", value: "  "},
          {name: " ", value: " "},
          {name: "  ", value: "  "},
          {name: " ", value: " "},
          {name: "  ", value: "  "},
          {name: "   ", value: "   "},
          {name: "    ", value: "    "},
          {name: "     ", value: "     "},
          {name: "     ", value: "     "},
          {name: "   ()", value: "  "},
          {name: "  ", value: "  "},
          {name: "  ", value: "  "},
          {name: "    ", value: "    "},
          {name: "     ", value: "     "},
        ];
        var Groups = [
          {name: "  ", value: "  "},
          {name: "  ", value: "  "},
          {name: "  ", value: "  "},
          {name: "   ", value: "   "},
          {name: "   ", value: "   "},
          {name: "   ", value: "   "}
        ];
        var Coupons = [
          {name: '  ( )', value: '  ( )'},
          {name: '  ( )', value: '  ( )'},
          {name: '  ', value: '  '},
          {name: '  ', value: '  '},
          {name: '  ()', value: '  ()'},
          {name: '  ', value: '  '},
        ];

        if (e.target.value === "Users") {
          setTypes(Users);
        }
        else if (e.target.value === "Groups") {
          setTypes(Groups);
        }
        else if (e.target.value === "Coupons") {
          setTypes(Coupons);
        }

        setFilters({
          ...filters,
          [e.target.id]: e.target.value,
          record_type: null,
        });

      } else {
        setFilters({
          ...filters,
          [e.target.id]: e.target.value
        });
      }
    }

    const handleFilters = (e) => {
      e.preventDefault();

      const SearchFilters = async() => {
        await fetch('/api/histories', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json',
            'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
          },
          body: JSON.stringify({...filters, offset: new Date().getTimezoneOffset()})
        }).then(resp => {
          resp.json().then(data => {
            if (resp.ok){
              setErrors({}); // reset Errors
              setHistories({
                ...histories,
                histories: data.histories
              })
              if (data.histories.length === 0) {
                setErrors({
                  NotFound: {
                    name: "",
                    error: "     .",
                    type: 'warning'
                  }
                })
              }
            } else {
              if (data.code === "SERVER"){
                setErrors({
                  Server: {
                    name: "",
                    error: "    ",
                    type: 'danger'
                  }
                })
              }
              if ([
                'INVALID_AUTH_HEADER', 
                'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
                'ACCESS_DENIED', 'AUTH_ERROR'
              ].includes(data.code)) {
                setRedirect({
                  redirect: true,
                  flash: {
                    type: 'error',
                    title: '',
                    message: '      .'
                  }
                })
              }
              if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
                localStorage.clear();
                sessionStorage.clear();
                setRedirect({
                  redirect: true,
                  flash: {
                    type: 'error',
                    title: '',
                    message: '      .'
                  }
                })
              }
            }
          })
        })
      }

      if (filters.start_date > filters.end_date){
        setErrors({
          ...errors,
          dates: {
            name: '',
            error: '     ,    .',
            type: 'warning'
          }
        })
      } else {
        console.log(filters)
        SearchFilters();
      }
    }

    const handleExtracting = async(e) => {
      e.preventDefault();
      await fetch('/api/histories/extract', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
        },
        body: JSON.stringify({...filters, offset: new Date().getTimezoneOffset()})
      }).then(resp => {
        resp.json().then(data_ => {
          if (resp.ok) {
            setExtractor({
              view: true,
              records: data_.records
            })
          } else {
            if (data_.code === "SERVER"){
              setErrors({
                Server: {
                  name: "",
                  error: "    ",
                  type: 'danger'
                }
              })
            }
            if ([
              'INVALID_AUTH_HEADER', 
              'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
              'ACCESS_DENIED', 'AUTH_ERROR'
            ].includes(data_.code)) {
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      .'
                }
              })
            }
            if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data_.code)){
              localStorage.clear();
              sessionStorage.clear();
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      .'
                }
              })
            }
          }
        })
      })
    }

    return(
      <div>

        <div className="row justify-content-right">

          <div className="col-sm-8">
            
            <label htmlFor="search" className="form-label font-weight-bold float-right"> : </label>
            <div className="input-group">
              <input onChange={handleChange} id="search" type="text" placeholder="   " title="   (ID)   " className="form-control col-sm-6 col-md-12"/>
            </div>

          </div>

          <div className="col-sm-4">
            
            <label htmlFor="search_id" className="form-label font-weight-bold float-right">  : </label>
            <div className="input-group">
              <input onChange={handleChange} id="search_id" type="text" placeholder="   (ID)" title="   (ID)   " className="form-control col-sm-6 col-md-12"/>
            </div>

          </div>

        </div>

        <div className="row mt-3 justify-content-right">

          <div className="col-sm-4">
            <label htmlFor="entity_type" className="form-label font-weight-bold float-right">  : </label>
            <div className="input-group">
              <select onChange={handleChange} id="entity_type" className="custom-select col-sm-6 col-md-12">
                <option className="font-weight-bold" value="" defaultValue> </option>
                <option value="Users"></option>
                <option value="Groups"></option>
                <option value="Coupons"></option>
              </select>
            </div>
          </div>

          <div className="col-sm-4">
            <label htmlFor="record_type" className="form-label font-weight-bold float-right">  : </label>
            <div className="input-group">
              <select onChange={handleChange} id="record_type" className="custom-select col-sm-6 col-md-12">
                <option className="font-weight-bold" value="" defaultValue> </option>
                {
                  types.length > 0 ? (
                    <optgroup label="  ">
                      {types.map((type, ind) => (
                        <option key={ind} value={type.value}>{type.name}</option>
                      ))}
                    </optgroup>
                  ) : (
                    <option disabled value="">---     ---</option>
                  )
                }
              </select>
            </div>
          </div>

          <div className="col-sm-4">
            <label htmlFor="record_state" className="form-label font-weight-bold float-right">  : </label>
            <div className="input-group">
              <select onChange={handleChange} id="record_state" className="custom-select col-sm-6 col-md-12">
                <option className="font-weight-bold" value="" defaultValue> </option>
                <option value="succeed"></option>
                <option value="failed"></option>
              </select>
            </div>
          </div>

        </div>

        <div className="row mt-3 justify-content-right">

          <div className="col-sm-6">
            <label htmlFor="start_date" className="form-label font-weight-bold float-right"> (): </label>
            <div className="input-group">
              <input onChange={handleChange} id="start_date" type="datetime-local" className={"form-control col-sm-6 col-md-12 " + (errors['dates'] ? 'is-invalid' : '')}/>
            </div>
          </div>

          <div className="col-sm-6">
            <label htmlFor="end_date" className="form-label font-weight-bold float-right"> () : </label>
            <div className="input-group">
              <input onChange={handleChange} id="end_date" type="datetime-local" className={"form-control col-sm-6 col-md-12 " + (errors['dates'] ? 'is-invalid' : '')}/>
            </div>
          </div>

        </div>

        <div className="mt-4 text-center">
          <button onClick={handleFilters} className="btn btn-md btn-primary">  </button>
          <button onClick={handleExtracting} className="btn btn-md btn-outline-dark"> </button>
          <p>
            <Link to="#" onClick={() => {window.location.reload()}} className="font-info text-primary">  </Link>
          </p>
        </div>
      </div>
    )
  }

  const LoadHistories = () => {

    const histories_patch = histories.histories && histories.histories.slice(0, histories.limit);

    const getType = (entity_type) => {
      let types = {
        Users: '',
        Groups: '',
        Coupons: '',
        Offers: '',
      }

      return types[entity_type]
    }

    const getStatus = (status, rec, err) => {
      let statuses = {
        succeed: [` ${rec} .`, "fa-check-circle", "text-success"],
        failed: [err, "fa-times-circle", "text-danger"]
      }

      return statuses[status]
    }

    const getDate = (seconds) => {
      var date = new Date(1970, 0, 1);
    date.setSeconds(seconds);
      var sloppy_date = new Date(date);
      var tzOffset = sloppy_date.getTimezoneOffset();
      
      var target_date = new Date(sloppy_date.getTime() - tzOffset * 60 * 1000);

      var yy = target_date.getFullYear();
      var mm = target_date.getMonth() + 1;
      var dd = target_date.getDate();

      var h = target_date.getHours();
      var m = target_date.getMinutes();

      return `${yy}-${(mm>9?'':'0')+mm}-${(dd>9?'':'0')+dd} ${(h>9?'':'0')+h}:${(m>9?'':'0')+m}`
    }

    return(
      <tbody>
        {
          histories_patch ? histories_patch.map((history, index) => {
            var entity_type = getType(history.entity_type);
            var status = getStatus(history.record_state, history.record_type, history.error_message);
            return(
              <tr className="" key={index}>
                <th className="table-header">#{history.id}</th>
                <td className="table-header text-info">{entity_type}</td>
                <td className="table-header">#{history.entity_id}</td>
                <td className="table-body text-primary">{history.record_type}</td>
                <td className="table-body wordWrap">{history.record}</td>
                <td title={status[0]} className="wordWrap tooltips table-body">
                  <i className={`fa ${status[1]} fa-lg ${status[2]}`}/>
                </td>
                <td dir='ltr' className="table-body">{getDate(new Date(history.date))}</td>
              </tr>
            )
          }) : null
        }
      </tbody>
    )
  }

  const AddLimit = () => {
    if (histories.limit < histories.histories.length){
      let new_limit = histories.limit + 50;
      setHistories({...histories, limit: new_limit})
    }
  }

  const ReduceLimit = () => {
    if (histories.limit > 50){
      let new_limit = histories.limit - 50;
      setHistories({...histories, limit: new_limit})
    }
  }

  const loadErrors = () => {
    if (Object.keys(errors).length !== 0 && errors.constructor === Object){
      return (
        <div>
          {errors ? Object.entries(errors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  const resetModals = () => {
    
    setExtractor({view: false, records: ""});
  }

  const downloadRecords = () => {
    var element = document.createElement('a');
    element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(extractor.records));
    element.setAttribute('download', 'records.txt');

    element.style.display = 'none';
    document.body.appendChild(element);

    element.click();

    document.body.removeChild(element);
  }

  if (redirect.redirect){
    return <Redirect to={{
      pathname: '/',
      state: {flash: redirect.flash}
    }}/>
  }

  return(
    <div className="mb-5 pt-5 pb-5">

      <div className="container text-center mt-5 pt-5">

        <div className="container mt-4">

          <span className="display-6"></span>
          <hr/>

          {LoadFilters()}

          <div className="container mt-4">
            {loadErrors()}
          </div>

          <div className="table-responsive mt-2">
      
            <table className="table mt-4 table-sm table-bordered">
              <thead>
                <tr className="bg-light-grey">
                  <th className="font-weight-bold" scope="col">ID</th>
                  <th className="font-weight-bold" scope="col"> </th>
                  <th className="font-weight-bold" scope="col"> (ID)</th>
                  <th className="font-weight-bold" scope="col"> </th>
                  <th className="font-weight-bold" scope="col"></th>
                  <th className="font-weight-bold" scope="col"> </th>
                  <th className="font-weight-bold" scope="col"></th>
                </tr>
              </thead>
              {LoadHistories()}
            </table>
            <div className="justify-content-center">
              {(histories.histories && histories.limit < histories.histories.length) ? (<button onClick={AddLimit} className="btn btn-sm btn-outline-primary"> </button>) : null}
              {histories.limit > 50 ? (<button onClick={ReduceLimit} className="btn btn-sm btn-outline-secondary"> </button>) : null}
            </div>
          </div>

        </div>

      </div>

      {/* Data Extractor Modal */}
      <Modal show={extractor.view} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8"> </Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">
          <textarea className="form-control" cols="30" rows="10">
            {extractor.records}
          </textarea>
          <div className="container mt-2">
            <Link to="#" onClick={downloadRecords}> </Link>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={resetModals}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>
   

    </div>
  )
}


export default Histories