import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

/*
  This Page Will Display The Best 9 Coupon Groups (Based on Full Price)
  First 3 Will Be Displayed at Large Cards, And
  The Rest Will Be Displayed as Normal Cards.
*/

export const ViewGroups = () => {
  
  const [user, setUser] = useState({});

  const [groups, setGroups] = useState();
  const [filters, setFilters] = useState({});
  const [advance, setAdvance] = useState(false);

  // Filters;

  const [types, setTypes] = useState([]);
  const [cities, setCities] = useState([]);

  const [errors, setErrors] = useState({});
  

  useEffect(() => {
    fetch('/api/groups/all', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      },
      body: JSON.stringify({user: localStorage.maidan_user ? JSON.parse(localStorage.maidan_user) : null})
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setGroups(data.groups);

          var user = localStorage.maidan_user && JSON.parse(localStorage.maidan_user);
          setUser(user);
          // Getting Cities Names (AR) & Types (AR).
          var elements = JSON.parse(localStorage.maidan_elements);

          // If user, we just add User's Cities (Based on his country).
          if (user){
            elements.forEach(element => {
              if (element.key === 'country'){
                if (element.values.country.ar === user.country){
                  setCities(element.values.cities.ar);
                }
              }
              if (element.key === 'coupon_types'){
                setTypes(element.values.ar)
              }
            })
          } else {
            var cities = []
            elements.forEach(element => {
              if (element.key === 'country'){
                  element.values.cities.ar.forEach(city => {
                    cities.push(city);
                  })
              }
              if (element.key === 'coupon_types'){
                setTypes(element.values.ar)
              }
            })
            setCities(cities);
          }

        } else {
          setErrors({
            QueryError: {
              name: '',
              error: '      ,   .',
              type: 'danger'
            }
          })
        }
      })
    })
  }, [])

  const handleFilter = (e) => {
    e.preventDefault()

    const SearchFilter = async() => {
      await fetch('/api/groups/all', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        body: JSON.stringify({...filters,
          [e.target.id]: e.target.value,
          user: localStorage.maidan_user ? JSON.parse(localStorage.maidan_user) : null})
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            setGroups(data.groups);
          } else {
            window.location.reload();
          }
        })
      })
    }

    SearchFilter();
    setFilters({
      ...filters,
      [e.target.id]: e.target.value
    });
  }

  const loadfilters = () => {
    return (
      <div className="mt-4">
        <form>

          <div className="row justify-content-center">

            <div className="form-group col-sm-12">
              <label htmlFor="search" className="form-label font-weight-bold float-right"> : </label>
              <div className="input-group justify-content-right">
                <input id="search" onChange={handleFilter} type="text" placeholder="   ( , , )" className="form-control col-sm-6 col-md-12"/>
              </div>
            </div>

            
          </div>
          
        </form>
        
        
        <div className="text-right">
          <button onClick={() => {setAdvance(!advance)}} className="btn btn-link font-weight-bold">
              
            {advance ? (
              <i className="mr-2 fa fa-arrow-up fa-xs"/>
            ) : (
              <i className="mr-2 fa fa-arrow-down fa-xs"/>
            )}
          </button>
        </div>

        {advance ? (
          <form>
          
            <div className="row justify-content-center">

              <div className="col-md">
                <select onChange={handleFilter} id="coupon_type" className="custom-select font-weight-bold">
                  <option className="font-weight-bold" value="" defaultValue> </option>
                  {
                    types.map((type, index) => (
                      <option key={index} value={type}>{type}</option>
                    ))
                  }
                </select>
              </div>

              <div className="col-md pt-mobile">
                <select onChange={handleFilter} id="city" className="custom-select font-weight-bold">
                  <option className="font-weight-bold" value="" defaultValue></option>
                  {
                    cities.map((city, index) => (
                      <option key={index} value={city}>{city}</option>
                    ))
                  }
                </select>
              </div>

              <div className="col-md pt-mobile">
                <select onChange={handleFilter} id="price" className="custom-select font-weight-bold">
                  <option className="font-weight-bold" value="" defaultValue> </option>
                  <option value="highest"> </option>
                  <option value="lowest"> </option>
                </select>
              </div>

              <div className="col-md pt-mobile">
                <select onChange={handleFilter} id="amount" className="custom-select font-weight-bold">
                  <option className="font-weight-bold" value="" defaultValue> </option>
                  <option value="available"></option>
                  <option value="highest"> </option>
                  <option value="lowest"> </option>
                </select>
              </div>

            </div>

            <div className="text-right">
              <button className="btn btn-link text-primary font-weight-bold">  </button>
            </div>

          </form>
        ) : null}

      </div>
    )
  }

  const loadCards = () => {
    return(
      <div className="row justify-content-center mt-4">
        {groups.map((group, index) => (
          <div key={index} className="col-sm-10 col-md-4">

            <div className="card hoverable mt-4 text-center bg-light-grey img-thumbnail">
              
              <div className="view overlay zoom">
                <img loading="lazy" src={`https://maidancom.s3.us-east-2.amazonaws.com/${group.images[0]}`} alt="Coupon" className="card-top-img card-normal-img img-fluid"/>
                <Link to={`/ads/${group.id}`}>
                  <div className="mask flex-center rgba-black-slight">
                    <div className="card-body">
                      <span className="display-7 card-title shadow-text qsemh-turq">{group.company}</span>
                      <p className="card-text font-weight-bold text-dark mt-4">
                           <i className="fa fa-external-link-alt "/>
                      </p>
                    </div>
                  </div>
                </Link>
              </div>
              
              <div className="card-body">
                
                <Link to={`/ads/${group.id}`} className="text-dark">
                  <p className="display-8 font-weight-bold">
                    {group.company}
                  </p>
                  {
                    user && (user.is_admin || user.is_super) ? (
                      <div>
                        <p className="font-info font-weight-bold">
                           ({group.coupons_num}) -  ({group.coupon_price}$)
                          <br/>
                           ({group.full_price}$) -  ({group.coupons_left})
                        </p>
                      </div>
                    ) : null
                  }
                </Link>

              </div>

            </div>

          </div>
        ))}
      </div>
    )
  }

  const loadErrors = () => {
    if (Object.keys(errors).length !== 0 && errors.constructor === Object){
      return (
        <div>
          {errors ? Object.entries(errors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  return(
    <div className="mb-5 pb-5">

      <div className="pt-4">
        
        <div id="carousel-example-1z" className="mt-5 pt-5 pr-md-2 pl-md-2 carousel slide carousel-fade" data-ride="carousel">

          <ol className="carousel-indicators">
            <li data-target="#carousel-example-1z" data-slide-to="0" className="active"></li>
            <li data-target="#carousel-example-1z" data-slide-to="1"></li>
            {/* <li data-target="#carousel-example-1z" data-slide-to="2"></li> */}
          </ol>

          <div className="carousel-inner" role="listbox">

            <div className="carousel-item carousel-sitem active">
              <img loading="lazy"  className="w-100 h-100 img-fluid" src="https://sites.google.com/site/moh897/banner1.png"
                alt="First slide"/>
            </div>

            <div className="carousel-item carousel-sitem">
              <img loading="lazy"  className="w-100 h-100 img-fluid" src="https://sites.google.com/site/moh897/banner2.png"
                alt="Second slide"/>
            </div>

            {/* <div className="carousel-item">
              <img loading="lazy"  className="card-xl-img" src="https://mdbootstrap.com/img/Photos/Slides/img%20(70).jpg"
                alt="Third slide"/>
            </div> */}

          </div>

          <a className="carousel-control-prev" href="#carousel-example-1z" role="button" data-slide="prev">
            {/* <span className="carousel-control-prev-icon" aria-hidden="true"></span> */}
            <i className="fa fa-arrow-alt-circle-left carousel-control-prev-icons" aria-hidden="true"></i>
            <span className="sr-only"></span>
          </a>

          <a className="carousel-control-next" href="#carousel-example-1z" role="button" data-slide="next">
            {/* <span className="carousel-control-next-icon" aria-hidden="true"></span> */}
            <i className="fa fa-arrow-alt-circle-right carousel-control-next-icons" aria-hidden="true"></i>
            <span className="sr-only"></span>
          </a>

        </div>     

      </div>

      <div className="mt-5 text-center container">
        {loadErrors()}
      </div>
      
      <div className="mt-4 text-center container">
        <span className="display-6 header-shadow qsemh-turq">
          
        </span>
        <hr/>
        {loadfilters()}
        <hr/>
        {groups ? loadCards() : null}
      </div>
    
    </div>
  )
}