import React, { useState, useEffect } from 'react';

// Modals
import { Modal, Button } from 'react-bootstrap';
import { Redirect } from 'react-router-dom';


const SellsTable = () => {

  const [offers, setOffers] = useState({offers: null, limit: 50});

  const [errors, setErrors] = useState({});
  const [redirect, setRedirect] = useState({redirect: false, flash: null})
  
  // Modals
  const [cancelModal, setCancelModal] = useState(false);
  const [modalState, setModalState] = useState({offer_id: null});

  useEffect(() => {
    fetch('/api/panel/offers', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setOffers({
            offers: data.offers,
            limit: 50
          })
        } else {
          if ([
            'INVALID_AUTH_HEADER', 
            'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
            'ACCESS_DENIED', 'AUTH_ERROR'
          ].includes(data.code)) {
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
          if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
            localStorage.clear();
            sessionStorage.clear();
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
        }
      })
    })
  }, [])

  const getDate = (seconds) => {
    var date = new Date(1970, 0, 1);
    date.setSeconds(seconds);
    var sloppy_date = new Date(date);
    var tzOffset = sloppy_date.getTimezoneOffset();
    
    var target_date = new Date(sloppy_date.getTime() - tzOffset * 60 * 1000);

    var yy = target_date.getFullYear();
    var mm = target_date.getMonth() + 1;
    var dd = target_date.getDate();

    var h = target_date.getHours();
    var m = target_date.getMinutes();

    return `${yy}-${(mm>9?'':'0')+mm}-${(dd>9?'':'0')+dd} ${(h>9?'':'0')+h}:${(m>9?'':'0')+m}`
  }

  /* Cancel Offers */

  const handleCancel = (e) => {
    e.preventDefault();

    setCancelModal(true);
    setModalState({offer_id: e.target.id});
  }

  const CancelOffer = async(e) => {
    e.preventDefault();

    await fetch(`/api/panel/offers/${modalState.offer_id}/cancel`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setOffers({
            ...offers,
            offers: data.offers
          });
          resetModals();
        } else {
          if (data.code === 'SERVER'){
            setErrors({
              Server: {
                name: '',
                error: '     ,   ',
                type: 'danger'
              }
            })
          }
          if (data.code === 'FINISHED_OFFER'){
            setErrors({
              Offer: {
                name: '',
                error: '    ,     .',
                type: 'warning'
              }
            })
          }
          if (data.code === 'NO_MATCH_ID'){
            setErrors({
              ID: {
                name: ' ',
                error: '    ,    .',
                type: 'danger'
              }
            })
          }
          if (data.code === 'NO_COUPON'){
            setErrors({
              Coupon: {
                name: '',
                error: '      !',
                type: 'danger'
              }
            })
          }
          if ([
            'INVALID_AUTH_HEADER', 
            'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
            'ACCESS_DENIED', 'AUTH_ERROR'
          ].includes(data.code)) {
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
          if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
            localStorage.clear();
            sessionStorage.clear();
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
        }
      })
    })
  }

  /* Cancel Offers */

  const LoadOffers = () => {

    const getStatus = (status) => {
      if (status){
        return ['success', ' ']
      }
      return ['secondary', ' ']
    }

    const offers_patch = offers.offers && offers.offers.slice(0, offers.limit);

    return(
      <tbody>
        {
          offers_patch ? offers_patch.map((offer, index) => {
            let status = getStatus(offer.completed);
            return(
              <tr className="" key={index}>
                <th className="table-header">{offer.id}</th>
                <th className="table-header text-primary">{offer.seller}</th>
                <td className="table-header">{offer.buyer || '   '}</td>
                <td className="table-header text-primary">{offer.price}$</td>
                <td className="table-header">{offer.coupon_price}$</td>
                <td dir='ltr' className="table-header text-danger">{getDate(offer.coupon_expire)}</td>
                <td className={`table-header text-${status[0]}`}>{status[1]}</td>
                <td dir='ltr' className="table-header">{getDate(offer.start_date)}</td>
                <td dir='ltr' className="table-header">{offer.end_date ? getDate(offer.end_date) : '   '}</td>
                <td className="table-header">
                  <button id={offer.id} onClick={handleCancel} className="btn btn-sm btn-warning font-weight-bold"> </button>
                </td>
              </tr>
            )
          }) : null
        }
      </tbody>
    )
  }

  const AddLimit = () => {
    if (offers.limit < offers.offers.length){
      let new_limit = offers.limit + 50;
      setOffers({...offers, limit: new_limit})
    }
  }

  const ReduceLimit = () => {
    if (offers.limit > 50){
      let new_limit = offers.limit - 50;
      setOffers({...offers, limit: new_limit})
    }
  }

  const resetModals = () => {

    // Reset States
    setModalState({offer_id: null});

    // Reset Modals
    setCancelModal(false);
  }

  const loadErrors = () => {
    if (Object.keys(errors).length !== 0 && errors.constructor === Object){
      return (
        <div>
          {errors ? Object.entries(errors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  if (redirect.redirect){
    return <Redirect to={{
      pathname: '/',
      state: {flash: redirect.flash}
    }}/>
  }

  return(
    <div className="table-responsive mt-4">
      
      <table className="table table-sm table-bordered">
        <thead>
          <tr className="bg-light-grey">
            <th className="font-weight-bold" scope="col">ID</th>
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"></th>
          </tr>
        </thead>
        {LoadOffers()}
      </table>
      <div className="justify-content-center">
        {(offers.offers && offers.limit < offers.offers.length) ? (<button onClick={AddLimit} className="btn btn-sm btn-outline-primary"> </button>) : null}
        {offers.limit > 50 ? (<button onClick={ReduceLimit} className="btn btn-sm btn-outline-secondary"> </button>) : null}
      </div>
   
      {/* Cancel Modal */}
      <Modal show={cancelModal} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">  ({modalState.offer_id}#)</Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">

          <div className="container mt-2">
            {loadErrors()}
          </div>

          <div className="container mt-4">
            
            <p className="text-warning font-weight-bold">
                    
            </p>

            <p className="text-info font-info">
                ,    .
            </p>
          </div>

        </Modal.Body>
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={resetModals}>
              
            </Button>
            <Button variant="warning" onClick={CancelOffer}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>
   
    </div>
  )
}


export default SellsTable