import React, { useState, useEffect } from 'react';

// Modals
import { Modal, Button } from 'react-bootstrap';
import { Link, Redirect } from 'react-router-dom';



const AddGroupsTable = () => {

  const [groups, setGroups] = useState({groups: null, limit: 50});

  const [errors, setErrors] = useState({});

  const [redirect, setRedirect] = useState({redirect: false, flash: null})

  // Modals
  const [openImage, setOpenImage] = useState({open: false, image: []})
  const [isBranches, setBranchesModal] = useState({view: false, branches: []});
  const [state, setState] = useState({
    display_time: 0,
    distribute_time: 120,
    coupon_type: null
  });
  const [isOk, setOk] = useState({ok: false, id: null, description: null});
  const [isBad, setBad] = useState({bad: false, id: null});

  useEffect(() => {
    fetch('/api/panel/new_groups', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setGroups({
            groups: data.groups,
            limit: 50
          });
        } else {
          if (data.code === 'AUTH_ERROR'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: ' ',
                message: '     '
              }
            })
          }
          if (data.code === 'ACCESS_DENIED'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: ' ',
                message: '       .'
              }
            })
          }
          if (data.code === 'INVALID_ACCESS_LEVEL'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '     ,    .'
              }
            })
          }
          if (data.code === 'SERVER'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      ,     '
              }
            })
          }
          if (data.code === 'NO_TOKEN'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '    ,       '
              }
            })
          }
          if (data.code === 'INVALID_AUTH_HEADER'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '     ,    .'
              }
            })
          }
          if (data.code === 'NOT_LOGGED_IN'){
            localStorage.clear();
            sessionStorage.clear();
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '        .'
              }
            })
          }
        }
      })
    })
  }, [])

  const LoadGroups = () => {

    const getDate = (seconds) => {
      var date = new Date(1970, 0, 1);
      date.setSeconds(seconds);
      var sloppy_date = new Date(date);
      var tzOffset = sloppy_date.getTimezoneOffset();
      
      var target_date = new Date(sloppy_date.getTime() - tzOffset * 60 * 1000);
  
      var yy = target_date.getFullYear();
      var mm = target_date.getMonth() + 1;
      var dd = target_date.getDate();
  
      var h = target_date.getHours();
      var m = target_date.getMinutes();
  
      return `${yy}-${(mm>9?'':'0')+mm}-${(dd>9?'':'0')+dd} ${(h>9?'':'0')+h}:${(m>9?'':'0')+m}`
    }

    const groups_patch = groups.groups && groups.groups.slice(0, groups.limit);

    return(
      <tbody>
        {
          groups_patch ? groups_patch.map((group, index) => {
            return(
              <tr className="" key={index}>
                <th className="table-header">{group.id}</th>
                <td className="table-header">{group.company}</td>
                <td className="table-header">{group.coupons_num}</td>
                <td className="table-header">{group.coupon_price}$</td>
                <td className="table-header">{group.full_price}$</td>
                <td className='table-header text-secondary'> </td>
                <td dir='ltr' className="table-header">{getDate(group.start_date)}</td>
                <td dir='ltr' className="table-header">{getDate(group.expire_date)}</td>
                <td className="table-header">
                  <button onClick={() => setBranchesModal({view: true, branches: group.branches})} className="btn btn-sm btn-outline-primary"></button>
                </td>
                <td className="table-header">
                  <button onClick={() => setOpenImage({open: true, image: group.images})} className="btn btn-sm btn-outline-primary"></button>
                </td>
                <td className="table-body">
                  <button onClick={() => setBad({bad: true, id: group.id})} className="btn btn-sm btn-outline-danger font-weight-bold"></button>
                  <button onClick={() => setOk({ok: true, id: group.id, description: group.description})} className="btn btn-sm btn-outline-success font-weight-bold"></button>
                </td>
            </tr>
            )
          }) : null
        }
      </tbody>
    )
  }

  const OpenBranchMap = (e) => {
    e.preventDefault();
    window.open(e.target.id)
  }

  const AddLimit = () => {
    if (groups.limit < groups.groups.length){
      let new_limit = groups.limit + 50;
      setGroups({...groups, limit: new_limit})
    }
  }

  const ReduceLimit = () => {
    if (groups.limit > 50){
      let new_limit = groups.limit - 50;
      setGroups({...groups, limit: new_limit})
    }
  }

  const handleChange = (e) => {
    e.preventDefault();

    setState({
      ...state,
      [e.target.id]: e.target.value
    })
  }

  const handleReject = async(e) => {
    e.preventDefault();
    await fetch(`/api/panel/groups/${isBad.id}/cancel`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setGroups({
            ...groups,
            groups: data.groups
          });
          resetModals();
        } else {
          if (data.code === 'AUTH_ERROR'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: ' ',
                message: '     '
              }
            })
          }
          if (data.code === 'ACCESS_DENIED'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: ' ',
                message: '       .'
              }
            })
          }
          if (data.code === 'INVALID_ACCESS_LEVEL'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '     ,    .'
              }
            })
          }
          if (data.code === 'SERVER'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      ,     '
              }
            })
          }
          if (data.code === 'NO_TOKEN'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '    ,       '
              }
            })
          }
          if (data.code === 'INVALID_AUTH_HEADER'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '     ,    .'
              }
            })
          }
          if (data.code === 'NOT_LOGGED_IN'){
            localStorage.clear();
            sessionStorage.clear();
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '        .'
              }
            })
          }
        }
      })
    })
  }

  const handleApproved = async(e) => {
    e.preventDefault();
    await fetch(`/api/panel/groups/${isOk.id}/approve`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      },
      body: JSON.stringify(state)
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setGroups({
            ...groups,
            groups: data.groups
          });
          resetModals();
        } else {
          if (data.code === 'AUTH_ERROR'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: ' ',
                message: '     '
              }
            })
          }
          if (data.code === 'ACCESS_DENIED'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: ' ',
                message: '       .'
              }
            })
          }
          if (data.code === 'INVALID_ACCESS_LEVEL'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '     ,    .'
              }
            })
          }
          if (data.code === 'SERVER'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      ,     '
              }
            })
          }
          if (data.code === 'NO_TOKEN'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '    ,       '
              }
            })
          }
          if (data.code === 'INVALID_AUTH_HEADER'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '     ,    .'
              }
            })
          }
          if (data.code === 'NOT_LOGGED_IN'){
            localStorage.clear();
            sessionStorage.clear();
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '        .'
              }
            })
          }
        }
      })
    })
  }

  const resetModals = () => {
    // Reset All Modals & Element State
    setState({
      display_time: 0,
      distribute_time: 120,
      coupon_type: null
    });
    setErrors({});

    setOpenImage({open: false, image: []});
    setBranchesModal({view: false, branches: []});
    setOk({ok: false, id: null, description: null});
    setBad({bad: false, id: null});
  }

  const loadErrors = () => {
    if (Object.keys(errors).length !== 0 && errors.constructor === Object){
      return (
        <div>
          {errors ? Object.entries(errors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  if (redirect.redirect){
    return <Redirect to={{
      pathname: '/',
      state: {flash: redirect.flash}
    }}/>
  }

  return(
    <div className="table-responsive mt-4">
      
      <table className="table table-sm table-bordered">
        <thead>
          <tr className="bg-light-grey">
            <th className="font-weight-bold" scope="col">ID</th>
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col"> / </th>
          </tr>
        </thead>
        {LoadGroups()}
      </table>
      <div className="justify-content-center">
        {(groups.groups && groups.limit < groups.groups.length) ? (<button onClick={AddLimit} className="btn btn-sm btn-outline-primary"> </button>) : null}
        {groups.limit > 50 ? (<button onClick={ReduceLimit} className="btn btn-sm btn-outline-secondary"> </button>) : null}
      </div>
    
      {/* Ok Modal */}
      <Modal show={isOk.ok} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">  (#{isOk.id})</Modal.Title>
        </Modal.Header>
        
        <Modal.Body className="text-center mt-2">
          {loadErrors()}

          <form id="MainForm" onSubmit={handleApproved}>

            <div className="container text-right">
              <span className="font-weight-bold">  : </span>
              <blockquote className="mt-2 info-weight">
                {isOk.description}
              </blockquote>
            </div>

            <div className="container mt-4">

              <div className="row text-right">

                {/* <div className="col-sm-10 mt-2">
                  <label htmlFor="display_time" className="font-weight-bold">*      () : </label>
                  <div className="input-group">
                    <input onChange={handleChange} required id="display_time" type="number" min="0" defaultValue="0" className="form-control col-sm-10"/>
                  </div>
                  <p className="font-info text-muted"> 0      </p>
                </div> */}

                <div className="col-sm-10 mt-2">
                  <label htmlFor="distribute_time" className="font-weight-bold">*    () : </label>
                  <div className="input-group">
                    <input onChange={handleChange} required id="distribute_time" type="number" min="1" defaultValue="120" className="form-control col-sm-10"/>
                  </div>
                  <p className="font-info text-muted"> </p>
                </div>

              </div>

            </div>

          </form>
          
        </Modal.Body>
        
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="outline-grey" onClick={resetModals}>
              
            </Button>
            <Button variant="outline-success" form="MainForm" type="submit">
              
            </Button>
          </div>
        </Modal.Footer>

      </Modal>

      {/* Open Image Modal */}
      <Modal show={openImage.open} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">  </Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">
          <div className="container">
            <div className="row justify-content-center">
              {openImage.image.map((img, index) => {
                if (img){
                  return(
                    <div key={index} className="col-sm-6">
                      <img src={`https://maidancom.s3.us-east-2.amazonaws.com/${img}`} alt={`img-${index}`} className="img-fluid img-thumbnail"/>
                    </div>
                  )
                }
              })}
            </div>
          </div>
          {/* <button className="btn btn-sm btn-outline-primary mt-2"> </button> */}
        </Modal.Body>
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="outline-grey" onClick={resetModals}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>
      
      {/* Branches Modal */}
      <Modal show={isBranches.view} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8"></Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">
          <p className="text-dark">
            {isBranches.branches.map((branch, index) => {
              if (isBranches.branches.length === (index+1)){
                return(<span key={index}><Link to="#" id={branch.map} onClick={OpenBranchMap}>{branch.city}-{branch.name}</Link>.</span>)
              } else {
                return(<span key={index}><Link to="#" id={branch.map} onClick={OpenBranchMap}>{branch.city}-{branch.name}</Link>, </span>)
              }
            })}
          </p>
        </Modal.Body>
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={resetModals}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>
      
      {/* Bad Modal */}
      <Modal show={isBad.bad} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">  (#{isBad.id})</Modal.Title>
        </Modal.Header>
        
        <Modal.Body className="text-center mt-2">
          {loadErrors()}

          <p className="font-weight-bold text-danger">
                  
          </p>
          
        </Modal.Body>
        
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={resetModals}>
              
            </Button>
            <Button onClick={handleReject} variant="danger">
              
            </Button>
          </div>
        </Modal.Footer>

      </Modal>

    </div>
  )
}


export default AddGroupsTable