import React, { useState, useEffect } from 'react';
import { Redirect, Link } from 'react-router-dom';
import LogoD from '../../static/images/logo-d.png'

import QRCode from 'qrcode.react';
import copy from 'copy-to-clipboard'

// Modals
import { Modal, Button } from 'react-bootstrap';



export const MyCoupons = () => {

  const [state, setState] = useState({coupons: null, limit: 15})
  const [errors, setErrors] = useState({});
  
  // Redirect
  const [redirect, setRedirect] = useState({
    redirect: false, to: null, flash: null
  });

  // Flash Modal
  const [isFlash, setFlashModal] = useState(false);
  const [flashState, setFlashState] = useState({});
  
  const [modalErrors, setModalErrors] = useState({});

  // QRCode Modal
  const [isQRCode, setQRCodeModal] = useState(false);
  const [QRCodeState, setQRCode] = useState({});

  // Gifting Modal
  const [isGift, setGiftModal] = useState(false);
  const [giftState, setGiftState] = useState({coupon_id: null, to_user_id: null});
  const [searchUsers, setSearchUsers] = useState({});

  // Selling Modal
  const [isSell, setSellModal] = useState(false);
  const [sellState, setSellState] = useState({coupon_id: null, coupon_price: null});

  // Cancel Modal
  const [offerState, setOfferState] = useState({coupon_id: null});
  const [cancelModal, setCancelModal] = useState(false);


  useEffect(() => {
    // Check If User is Logged-in
    if (!localStorage.maidan_jwt_$m3){
      setRedirect({
        redirect: true,
        to: '/'
      });
    }

    // Fetch Current User Coupons
    fetch('/api/user/coupons', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setState({
            coupons: data.coupons,
            limit: 15
          });
        } else {
          if (data.code === 'NO_COUPONS'){
            setErrors({
              NotFound: {
                name: '',
                error: '   ,    ',
                link: {
                  id: 'GROUPS_PAGE',
                  text: ' '
                },
                type: 'warning'
              }
            })
          }
        }
      })
    })

  }, [])


  const OpenBranchMap = (e) => {
    e.preventDefault();
    window.open(e.target.id)
  }

  const getQRCode = (coupon) => {
    setQRCode(coupon);
    setQRCodeModal(true);
  }

  const getStatus = (status) => {

    let statuses = {
      available: [
        'text-success', ' '
      ],
      onhold: [
        'text-grey', ''
      ],
      expired: [
        'text-danger', ' '
      ],
      used: [
        'text-warning', ''
      ],
      canceled: [
        'text-dark', ''
      ]
    }

    return statuses[status]
  }

  /* Gifting */

  const handleGift = (e) => {
    e.preventDefault();

    setGiftState({
      ...giftState,
      coupon_id: e.target.id,
    });
    setGiftModal(true);
  }

  const fetchUsersResualts = async(e) => {
    var value = e.target && e.target.value;
    setGiftState({
      ...giftState,
      to_user_id: null
    });
    await fetch('/api/user/search', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      },
      body: JSON.stringify({searchQuery: value})
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setModalErrors({});
          if (data.length === 0 && value){
            setModalErrors({
              users: {
                name: '',
                error: '     ',
                type: 'warning'
              }
            })
          }
          setSearchUsers({
            users: data.users,
            length: data.length
          });
        } else {
          setModalErrors({
            users: {
              name: '',
              error: '      ,     .',
              type: 'danger'
            }
          })
        }
      })
    })
  }

  const handleUserSelection = (id) => {

    // Remove Color from old Selected, and remove Selected Sign.
    var oldElemnt = document.getElementById(`user-card-${giftState.to_user_id}`);
    var oldSelectedElement = document.getElementById(`user-select-${giftState.to_user_id}`);
    if (oldElemnt){
      oldElemnt.classList.remove('bg-light-grey');
      oldElemnt.classList.add('bg-white');
    }
    if (oldSelectedElement){
      oldSelectedElement.classList.remove('fa-check-circle')
    }

    // Changing Color of new Selected, and add Selected Sign.
    var newElemnt = document.getElementById(`user-card-${id}`);
    var newSelectedElemnt = document.getElementById(`user-select-${id}`);
    newElemnt.classList.remove('bg-white');
    newElemnt.classList.add('bg-light-grey');
    newSelectedElemnt.classList.add('fa-check-circle')

    // Adding Selected User as `to_user_id` in `giftState`
    setGiftState({
      ...giftState,
      to_user_id: id
    });
  }

  const handleSendGift = (e) => {
    e.preventDefault();

    const SendGift = async() => {
      await fetch('/api/user/coupons/gift', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
        },
        body: JSON.stringify(giftState)
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            resetModals();
            setFlashState({
              type: 'success',
              title: ' ',
              message: '    !'
            });
            setFlashModal(true);
          } else {
            if (data.code === 'SERVER'){
              setRedirect({
                redirect: true,
                to: '/',
                flash: {
                  type: 'error',
                  title: '',
                  message: '      ,     '
                }
              })
            }
            if ([
              'INVALID_AUTH_HEADER', 
              'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
              'ACCESS_DENIED', 'AUTH_ERROR'
            ].includes(data.code)) {
              setRedirect({
                redirect: true,
                to: '/',
                flash: {
                  type: 'error',
                  title: '',
                  message: '    .'
                }
              })
            }
            if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
              localStorage.clear();
              sessionStorage.clear();
              setRedirect({
                redirect: true,
                to: '/',
                flash: {
                  type: 'error',
                  title: '',
                  message: '    .'
                }
              })
            }
            if (data.code === 'INVALID_REQUEST'){
              setModalErrors({
                request: {
                  name: '',
                  error: ' ,     ,     .',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'MISSING_VALUES'){
              setModalErrors({
                request: {
                  name: '',
                  error: '       .',
                  type: 'warning'
                }
              })
            }
            if (data.code === 'NO_MATCH_COUPON'){
              resetModals();
              setFlashState({
                type: 'error',
                title: ' ',
                message: '      ,    .'
              });
              setFlashModal(true);
            }
            if (data.code === 'NOT_YOURS'){
              setModalErrors({
                not_yours: {
                  name: '',
                  error: '   ,      .',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'INVALID_STATUS'){
              setModalErrors({
                coupon_status: {
                  name: '',
                  error: '       ,       .',
                  type: 'warning'
                }
              })
            }
            if (data.code === 'NO_MATCH_USER'){
              resetModals();
              setFlashState({
                type: 'error',
                title: ' ',
                message: '  ,    ,    .'
              });
              setFlashModal(true);
            }
          }
        })
      })
    }

    SendGift();
  }

  /* Gifting */

  /* Selling */

  const handleSell = (coupon) => {
    
    setSellState({
      coupon_id: coupon.id,
      coupon_price: coupon.coupon_price
    });

    setSellModal(true);
  }

  const handleSellChange = (e) => {
    e.preventDefault();

    setSellState({
      ...sellState,
      [e.target.id]: e.target.value
    });
  }

  const handleSellSubmit = (e) => {
    e.preventDefault();

    const SellCoupon = async() => {
      await fetch(`/api/user/coupons/${sellState.coupon_id}/sell`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
        },
        body: JSON.stringify(sellState)
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            resetModals();
            setFlashState({
              type: 'success',
              title: ' ',
              message: '     !'
            });
            setFlashModal(true);
          } else {
            if (data.code === 'SERVER'){
              setRedirect({
                redirect: true,
                to: '/',
                flash: {
                  type: 'error',
                  title: '',
                  message: '      ,     '
                }
              })
            }
            if ([
              'INVALID_AUTH_HEADER', 
              'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
              'ACCESS_DENIED', 'AUTH_ERROR'
            ].includes(data.code)) {
              setRedirect({
                redirect: true,
                to: '/',
                flash: {
                  type: 'error',
                  title: '',
                  message: '    .'
                }
              })
            }
            if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
              localStorage.clear();
              sessionStorage.clear();
              setRedirect({
                redirect: true,
                to: '/',
                flash: {
                  type: 'error',
                  title: '',
                  message: '    .'
                }
              })
            }
            if (data.code === 'INVALID_REQUEST'){
              setModalErrors({
                request: {
                  name: ' ',
                  error: ' ,     ,     .',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'MISSING_VALUES'){
              setModalErrors({
                price: {
                  name: ' ',
                  error: '  .',
                  type: 'warning'
                }
              })
            }
            if (data.code === 'NO_MATCH_COUPON'){
              resetModals();
              setFlashState({
                type: 'error',
                title: ' ',
                message: '      ,    .'
              });
              setFlashModal(true);
            }
            if (data.code === 'LOWER_PRICE'){
              setModalErrors({
                price: {
                  name: ' ',
                  error: '    ,  .',
                  type: 'warning'
                }
              })
            }
            if (data.code === 'NOT_YOURS'){
              setModalErrors({
                not_yours: {
                  name: '',
                  error: '   ,      .',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'INVALID_STATUS'){
              setModalErrors({
                coupon_status: {
                  name: '',
                  error: '       ,       .',
                  type: 'warning'
                }
              })
            }
          }
        })
      })
    }

    if (parseInt(sellState.price) > parseInt(sellState.coupon_price)){
      setModalErrors({
        price: {
          name: '',
          error: '     .',
          type: 'warning'
        }
      })
    } else {
      SellCoupon();
    }
  }

  /* Selling */

  /* Cancel Offers */

  const handleCancel = (coupon_id) => {
    setCancelModal(true);
    setOfferState({coupon_id: coupon_id});
  }

  const CancelOffer = async(e) => {
    e.preventDefault();

    await fetch(`/api/user/offers/${offerState.coupon_id}`, {
      method: 'DELETE',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setState({
            ...state,
            coupons: data.coupons
          })
          resetModals();
        } else {
          if (data.code === 'SERVER'){
            setErrors({
              Server: {
                name: '',
                error: '     ,   ',
                type: 'danger'
              }
            })
          }
          if (data.code === 'FINISHED_OFFER'){
            setErrors({
              Offer: {
                name: '',
                error: '    ,     .',
                type: 'warning'
              }
            })
          }
          if (data.code === 'NO_MATCH_ID'){
            setErrors({
              ID: {
                name: ' ',
                error: '    ,    .',
                type: 'danger'
              }
            })
          }
          if (data.code === 'NO_COUPON'){
            setErrors({
              Coupon: {
                name: '',
                error: '      !',
                type: 'danger'
              }
            })
          }
        }
      })
    })
  }

  /* Cancel Offers */

  const getDate = (seconds) => {
    var date = new Date(1970, 0, 1);
    date.setSeconds(seconds);
    var sloppy_date = new Date(date);
    var tzOffset = sloppy_date.getTimezoneOffset();
    
    var target_date = new Date(sloppy_date.getTime() - tzOffset * 60 * 1000);

    var yy = target_date.getFullYear();
    var mm = target_date.getMonth() + 1;
    var dd = target_date.getDate();

    var h = target_date.getHours();
    var m = target_date.getMinutes();

    return `${yy}-${(mm>9?'':'0')+mm}-${(dd>9?'':'0')+dd} ${(h>9?'':'0')+h}:${(m>9?'':'0')+m}`
  }

  const loadCoupons = () => {
    const coupons_patch = state.coupons && state.coupons.slice(0, state.limit)

    return (
      <tbody>
        {
          coupons_patch ? coupons_patch.map((coupon, index) => {
            let status = getStatus(coupon.coupon_status)
            return(
              <tr key={index}>
                
                <th className="table-header">#{index+1}</th>
                <th className="table-header">
                  <Link className="text-primary" to={`/ads/${coupon.group_id}`}>{coupon.group_id}</Link>
                </th>
                <td className="table-header">{coupon.group_company}</td>
                <td className="table-header">
                  {coupon.group_branches.map((branch, index) => {
                    if (coupon.group_branches.length === (index+1)){
                      return(<span key={index}><Link className="text-primary" to="#" id={branch.map} onClick={OpenBranchMap}>{branch.city}-{branch.name}</Link>.</span>)
                    } else {
                      return(<span key={index}><Link className="text-primary" to="#" id={branch.map} onClick={OpenBranchMap}>{branch.city}-{branch.name}</Link>, </span>)
                    }
                  })}
                </td>
                <td dir='ltr' className="table-header">{getDate(coupon.expire_date)}</td>
                <td className="table-header">{coupon.coupon_price}$</td>
                <td className={`table-header ${status[0]}`}>{status[1]}</td>
                {
                  coupon.coupon_status === 'available' ? (
                      <td>
                        <button onClick={() => {getQRCode(coupon)}} className="btn font-btn btn-sm btn-outline-success"></button>
                      </td>
                  ) : (
                      <td className="table-header text-danger">
                        .
                      </td>
                  )
                }
                {
                  coupon.coupon_status === 'available' ? (
                    <td>
                      <button id={coupon.id} onClick={handleGift} className="btn font-btn btn-sm btn-outline-orange"></button>
                    </td>
                  ) : (
                    <td className="table-header text-danger">
                      .
                    </td>
                  )
                }
                {
                  coupon.coupon_status === 'available' ? (
                    <td>
                      <button onClick={() => {handleSell(coupon)}} className="btn font-btn btn-sm btn-outline-turq"></button>
                    </td>
                  ) : coupon.coupon_status === 'onhold' ? (
                    <td>
                      <button onClick={() => {handleCancel(coupon.id)}} className="btn font-btn btn-sm btn-warning"> </button>
                    </td>
                  ) : (
                    <td className="table-header text-danger">
                      .
                    </td>
                  )
                }
              </tr>
            )
          }) : null
        }
      </tbody>
    )
  }

  const handleErrorEvent = (e) => {
    e.preventDefault();

    if (e.target.id === 'GROUPS_PAGE'){
      setRedirect({
        redirect: true,
        to: '/ads'
      })
    }
  }
  
  const loadErrors = () => {
    if (Object.keys(errors).length !== 0 && errors.constructor === Object){
      return (
        <div>
          {errors ? Object.entries(errors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error} {e[1].link ? (<Link to="#" id={e[1].link.id} onClick={handleErrorEvent}>{e[1].link.text}</Link>) : null}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  const loadModalErrors = () => {
    if (Object.keys(modalErrors).length !== 0 && modalErrors.constructor === Object){
      return (
        <div>
          {modalErrors ? Object.entries(modalErrors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error} {e[1].link ? (<Link to="#" id={e[1].link.id} onClick={handleErrorEvent}>{e[1].link.text}</Link>) : null}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  const AddLimit = () => {
    if ((state.limit < state.coupons.length)){
      let new_limit = state.limit + 15;
      setState({...state, limit: new_limit})
    }
  }

  const ReduceLimit = () => {
    
    if ((state.limit > 15)){
      let new_limit = state.limit - 15;
      setState({...state, limit: new_limit})
    }
  }

  const reloadPage = () => {
    window.location.reload();
  }

  if (redirect.redirect){
    return <Redirect to={{
      pathname: redirect.to,
      state: {flash: redirect.flash}
    }}/>
  }

  const resetModals = () => {

    // Reset Modal States
    setSearchUsers({});
    setGiftState({
      coupon_id: null,
      to_user_id: null
    });
    setQRCode({});
    setSellState({
      coupon_id: null,
      coupon_price: null
    })
    setOfferState({coupon_id: null})
    // Reset Modals
    setQRCodeModal(false);
    setGiftModal(false);
    setSellModal(false);
    setCancelModal(false);
    
    // Reset Errors
    setModalErrors({});
  }


  return(
    <div className="mb-5 pb-5 pt-5">
      
      <div className="container mt-5 pt-5">

        <div className="container mt-2">
          {loadErrors()}
        </div>

        <div className="container text-center mt-5">
          <span className="display-7"></span>
          <hr/>
          <div className="table-responsive">

            <table className="table table-bordered mt-4">
              <thead>
                <tr className="bg-light-grey">
                  <th  className="table-header" scope="col">ID</th>
                  <th className="table-header" scope="col"># </th>
                  <th className="table-header" scope="col"></th>
                  <th className="table-header" scope="col"></th>
                  <th className="table-header" scope="col">  </th>
                  <th className="table-header" scope="col"> </th>
                  <th className="table-header" scope="col"> </th>
                  <th className="table-header" scope="col">QRCode</th>
                  <th className="table-header" scope="col"></th>
                  <th className="table-header" scope="col"></th>
                </tr>
              </thead>
              {loadCoupons()}
            </table>
          </div>
          <div className="justify-content-center">
            {(state.coupons && state.limit < state.coupons.length) ? (<button onClick={AddLimit} className="btn btn-sm btn-outline-primary"> </button>) : null}
            {state.limit > 15 ? (<button onClick={ReduceLimit} className="btn btn-sm btn-outline-secondary"> </button>) : null}
          </div>
        </div>

      </div>
    
      {/* Flash Modal */}
      <Modal show={isFlash} onHide={reloadPage}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">{flashState.title}</Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">
          {flashState.type === 'success' ? (
            <i className="fa fa-check-circle text-success fa-2x"/>
          ) : (
            <i className="fa fa-times-circle text-danger fa-2x"/>
          )}
          <p className="font-weight-bold text-muted mt-2">
            {flashState.message}
          </p>
        </Modal.Body>
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={reloadPage}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>
    
      {/* QRCode Modal */}
      <Modal show={isQRCode} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">  : </Modal.Title>
        </Modal.Header>
        
        <Modal.Body className="text-center">
          
          
          <div className="container text-center border mt-2">
            <img className="coupon-img pt-3" src={LogoD} alt="brand"/>
            
            <div className="font-weight-bold row mt-4 ml-2">

              <div className="col-6 text-left">
                :
              </div>
              <div className="col-6 text-right">
                <Link to="#" onClick={() => {copy(QRCodeState.qr_code)}} title="  ">{QRCodeState.qr_code}</Link>
              </div>

              <div className="col-6 text-left mt-2">
                :
              </div>
              <div className="col-6 text-right mt-2">
                {QRCodeState.group_company}
              </div>

              <div className="col-6 text-left mt-2">
                :
              </div>
              <div className="col-6 text-right mt-2">
                {QRCodeState.coupon_price}$
              </div>

            </div>
            
            <div className="container mt-4">
              <QRCode size={152} level="Q" renderAs="svg" className="img-fluid" value={`${window.location.origin}/admin/redeem?qrcode=${QRCodeState.qr_code}`}/>
            </div>
            <div className="container mt-4 pb-4">
              <b className="text-danger"> </b>
              <br/>
              <b dir='ltr' className="text-danger">{getDate(QRCodeState.expire_date)}</b>
            </div>
          </div>

          <p className="text-muted font-info mt-2">
            <i className="fa fa-star-of-life fa-xs ml-2"/>
                 ,       QRCode   .
          </p>
        

        </Modal.Body>
        
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={resetModals}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>

      {/* Gifting Modal */}
      <Modal show={isGift} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8"> </Modal.Title>
        </Modal.Header>
        
        <Modal.Body className="text-center">
          <span className="font-weight-bold text-muted">        : </span>

          <div className="container mt-4">

            <div className="row justify-content-center">

              <div className="col-sm-8">

                <div className="input-group">
                  <input id="users" onChange={fetchUsersResualts} type="text" placeholder=" /" className={"form-control col-sm-12 " + (modalErrors['users'] ? 'is-invalid' : '')}/>
                </div>

              </div>

            </div>

          </div>
          
          <div className="container mt-2">
            {loadModalErrors()}
          </div>
          
          {
            searchUsers.users ? (
              
              <div className="container mt-4 text-right">
                <span className="font-weight-bold"> ({searchUsers.length}) : </span>
                {searchUsers.users.map(user => (
                  <Link key={user.id} to="#" onClick={() => {handleUserSelection(user.id)}}>
                    <div id={`user-card-${user.id}`} className="card shadow bg-white mt-2">
                      <div className="card-body">
                        <i id={`user-select-${user.id}`} className="fa text-info float-left"/>
                        <p className="card-text font-weight-bold">
                            : {user.username}
                        </p>
                        <p className="card-text font-weight-bold">
                            : {user.email}
                        </p>
                      </div>
                    </div>
                  </Link>
                ))}
              </div>
              
            ) : null
          }
        </Modal.Body>
        
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={resetModals}>
              
            </Button>
            <Button variant="orange" onClick={handleSendGift}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>
      
      {/* Selling Modal */}
      <Modal show={isSell} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8"> </Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">
          <form onSubmit={handleSellSubmit} id="mainForm">
            
            <div className="container">
              <p className="text-muted font-info font-weight-bold">
                <i className="fa fa-star-of-life fa-xs"/>         ({sellState.coupon_price}$)
              </p>
            </div>

            <div className="container mt-2">
              {loadModalErrors()}
            </div>

            <div className="row justify-content-center">

              <div className="col-sm-8 text-right">
                
                <label htmlFor="price" className="font-weight-bold">*  :</label>
                <div className="input-group">
                  <input required id="price" onChange={handleSellChange} type="number" min={0} max={sellState.coupon_price} step="0.01" placeholder="" className={"form-control col-sm-12 " + (modalErrors['price'] ? 'is-invalid' : '')}/>
                </div>

              </div>

            </div>
          </form>

        </Modal.Body>
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={resetModals}>
              
            </Button>
            <Button type="submit" form="mainForm" variant="turq">
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>
          
      {/* Cancel Modal */}
      <Modal show={cancelModal} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">   ({offerState.coupon_id}#)</Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">

          <div className="container mt-2">
            {loadErrors()}
          </div>

          <div className="container mt-4">
            
            <p className="text-warning font-weight-bold">
                    
            </p>

            <p className="text-info font-info">
                 .
            </p>
          </div>

        </Modal.Body>
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={resetModals}>
              
            </Button>
            <Button variant="warning" onClick={CancelOffer}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>
   
    </div>
  )
}