import React, { useState, useEffect } from 'react';
import { Link, Redirect } from 'react-router-dom';


const TicketsTable = () => {

  const [tickets, setTickets] = useState({tickets: null, limit: 50});

  const [redirect, setRedirect] = useState({redirect: false, flash: null})

  useEffect(() => {
    fetch('/api/panel/tickets', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setTickets({
            tickets: data.tickets,
            limit: 50
          })
        } else {
          if ([
            'INVALID_AUTH_HEADER', 
            'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
            'ACCESS_DENIED', 'AUTH_ERROR'
          ].includes(data.code)) {
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
          if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
            localStorage.clear();
            sessionStorage.clear();
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
        }
      })
    })
  }, [])

  const LoadTickets = () => {

    const getDate = (seconds) => {
      var date = new Date(1970, 0, 1);
      date.setSeconds(seconds);
      var sloppy_date = new Date(date);
      var tzOffset = sloppy_date.getTimezoneOffset();
      
      var target_date = new Date(sloppy_date.getTime() - tzOffset * 60 * 1000);
  
      var yy = target_date.getFullYear();
      var mm = target_date.getMonth() + 1;
      var dd = target_date.getDate();
  
      var h = target_date.getHours();
      var m = target_date.getMinutes();
  
      return `${yy}-${(mm>9?'':'0')+mm}-${(dd>9?'':'0')+dd} ${(h>9?'':'0')+h}:${(m>9?'':'0')+m}`
    }
  
    const getStatus = (status) => {
      let statuses = {
        open: ["  ", "info"],
        waiting: [" ", "success"],
        closed: ["", "dark"]
      }
  
      return statuses[status]
    }

    const tickets_patch = tickets.tickets && tickets.tickets.slice(0, tickets.limit);

    return(
      <tbody>
        {
          tickets_patch ? tickets_patch.map((ticket, index) => {
            let status = getStatus(ticket.status);
            return(
              <tr className="" key={index}>
                <th className="table-header">
                  <Link className="text-primary" to={`/user/tickets/${ticket.id}`}>{ticket.title}</Link>
                </th>
                <th className="table-header">#{ticket.id}</th>
                <th className="table-header">{ticket.user.username}</th>
                <th className="table-header">#{ticket.user.id}</th>
                <th className="table-header text-secondary">{ticket.type}</th>
                <th dir='ltr' className="table-header">{getDate(ticket.created)}</th>
                <th dir='ltr' className="table-header">{ticket.last_activity}</th>
                <th className={`table-header text-${status[1]}`}>{status[0]}</th>
              </tr>
            )
          }) : null
        }
      </tbody>
    )
  }

  const AddLimit = () => {
    if (tickets.limit < tickets.tickets.length){
      let new_limit = tickets.limit + 50;
      setTickets({...tickets, limit: new_limit})
    }
  }

  const ReduceLimit = () => {
    if (tickets.limit > 50){
      let new_limit = tickets.limit - 50;
      setTickets({...tickets, limit: new_limit})
    }
  }

  if (redirect.redirect){
    return <Redirect to={{
      pathname: '/',
      state: {flash: redirect.flash}
    }}/>
  }

  return(
    <div className="table-responsive mt-4">
      
      <table className="table table-sm table-bordered">
        <thead>
          <tr className="bg-light-grey">
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col">ID</th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"> (ID)</th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"></th>
          </tr>
        </thead>
        {LoadTickets()}
      </table>
      <div className="justify-content-center">
        {(tickets.tickets && tickets.limit < tickets.tickets.length) ? (<button onClick={AddLimit} className="btn btn-sm btn-outline-primary"> </button>) : null}
        {tickets.limit > 50 ? (<button onClick={ReduceLimit} className="btn btn-sm btn-outline-secondary"> </button>) : null}
      </div>
   </div>
  )
}


export default TicketsTable