import React, { useState, useEffect } from 'react';
import { Redirect, useParams } from 'react-router-dom';
import PH from '../../static/images/icon.png'



export const Ticket = () => {

  const { id } = useParams();

  const [state, setState] = useState({})
  const [errors, setErrors] = useState({});

  const [messageState, setMessageState] = useState({});
  
  // Redirect
  const [redirect, setRedirect] = useState({
    redirect: false, to: null, flash: null
  });

  useEffect(() => {
    // Fetch Current User Tickets
    const fetchTicket = () => {
      fetch(`/api/user/tickets/${id}`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        }
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            setState({...data.ticket, is_admin: data.is_admin});
          } else {
            if (data.code === "NO_TICKET") {
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '  ,     .'
                },
                to: '/'
              });
            } else {
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '    .'
                },
                to: '/'
              });
            }
          }
        })
      })
    }

    // Initial Fetching
    fetchTicket();
    // Repetitive Fetching with setInterval
    const interval = setInterval(() => {
      fetchTicket();
    }, 1000 * 35);

    // Clear interval when unmount
    return () => clearInterval(interval);

  }, []);

  useEffect(() => {
    let message = document.getElementById('lastMessage');
    message && message.classList.add('card-fade');
  }, [state]);

  const getDate = (seconds) => {
    var date = new Date(1970, 0, 1);
    date.setSeconds(seconds);
    var sloppy_date = new Date(date);
    var tzOffset = sloppy_date.getTimezoneOffset();
    
    var target_date = new Date(sloppy_date.getTime() - tzOffset * 60 * 1000);

    var yy = target_date.getFullYear();
    var mm = target_date.getMonth() + 1;
    var dd = target_date.getDate();

    var h = target_date.getHours();
    var m = target_date.getMinutes();

    return `${yy}-${(mm>9?'':'0')+mm}-${(dd>9?'':'0')+dd} ${(h>9?'':'0')+h}:${(m>9?'':'0')+m}`
  }

  const getStatus = (status) => {
    let statuses = {
      open: ["  ", "info"],
      waiting: [" ", "success"],
      closed: ["", "dark"]
    }

    return statuses[status]
  }
  
  const loadErrors = () => {
    if (Object.keys(errors).length !== 0 && errors.constructor === Object){
      return (
        <div>
          {errors ? Object.entries(errors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  const handleChange = (e) => {
    e.preventDefault();

    if (e.target.id === 'message') {
      var len = e.target.value.length;
      var elem = document.getElementById('charCount');

      if (len >= 600) {
        e.target.value = e.target.value.substring(0, 600);
      } 
      else {
        if (len > 450) {
          elem.classList.add("text-danger");
          elem.classList.remove("text-warning");
        } else if (len > 300) {
          elem.classList.add("text-warning");
        } else {
          elem.classList.remove("text-warning");
          elem.classList.remove("text-danger");
        }

      }

      elem.innerHTML = 600 - len;

    }

    setMessageState({
      ...messageState,
      [e.target.id]: e.target.value
    })

  }

  const handleSubmit = (e) => {
    e.preventDefault();

    var bid = e.target.id;

    const SubmitMessage = async(status) => {
      await fetch(`/api/user/tickets/${id}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        body: JSON.stringify({...messageState, status: status})
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok) {
            document.getElementById('message').value = '';
            document.getElementById('charCount').innerHTML = 600;
            setMessageState({});
            setErrors({});
            setState({...data.ticket, is_admin: data.is_admin});
          } else {
            if (data.code === 'NO_TICKET') {
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '  ,     .'
                },
                to: '/'
              });
            }
            else if (data.code === 'NOT_YOURS') {
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '    .'
                },
                to: '/'
              });
            }
            else {
              setErrors({
                message: {
                  name: "",
                  error: "    ,     .",
                  type: 'danger'
                }
              })
            }
          }
        })
      })
    }

    const closeTicket = async() => {
      await fetch(`/api/user/tickets/${id}/close`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok) {
            document.getElementById('message').value = '';
            document.getElementById('charCount').innerHTML = 600;
            setMessageState({});
            setErrors({});
            setState({...data.ticket, is_admin: data.is_admin})
          } else {
            setErrors({
              close: {
                name: "",
                error: "     ,    .",
                type: 'danger'
              }
            })
          }
        })
      })
    }

    if (state.is_admin) {
      if (state.status === 'closed') {
        setErrors({
          unAble: {
            name: "",
            error: "      .",
            type: "warning"
          }
        });
      } else {
        if (bid === 'close'){
          closeTicket();
        }
        else if (bid === 'send') {
          if (!messageState.message) {
            setErrors({
              message: {
                name: "",
                error: "   ",
                type: 'warning'
              }
            })
          } else {
            SubmitMessage();
          }
        }
        else if (bid === 'closeNsend') {
          if (!messageState.message) {
            setErrors({
              message: {
                name: "",
                error: "   ",
                type: 'warning'
              }
            })
          } else {
            SubmitMessage('closed');
          }
        }
        else if (bid === 'reOpen') {
          if (!messageState.message) {
            setErrors({
              message: {
                name: "",
                error: "   ",
                type: 'warning'
              }
            })
          } else {
            SubmitMessage('open');
          }
        }
      }
    } 
    else {
      if (bid !== 'close' && (!state.user_resp && state.status !== 'closed')) {
        setErrors({
          unAble: {
            name: "",
            error: "           .",
            type: "danger"
          }
        });
      }
      else {
        if (bid === 'close'){
          closeTicket();
        }
        else if (bid === 'send') {
          if (!messageState.message) {
            setErrors({
              message: {
                name: "",
                error: "   ",
                type: 'warning'
              }
            })
          } else {
            SubmitMessage();
          }
        }
        else if (bid === 'closeNsend') {
          if (!messageState.message) {
            setErrors({
              message: {
                name: "",
                error: "   ",
                type: 'warning'
              }
            })
          } else {
            SubmitMessage('closed');
          }
        }
        else if (bid === 'reOpen') {
          if (!messageState.message) {
            setErrors({
              message: {
                name: "",
                error: "   ",
                type: 'warning'
              }
            })
          } else {
            SubmitMessage('open');
          }
        }
      }
    }
    
  }

  if (redirect.redirect){
    return <Redirect to={{
      pathname: redirect.to,
      state: {flash: redirect.flash}
    }}/>
  }

  return(
    <div className="mb-5 pb-5 pt-5">
      
      <div className="container-fluid mt-5 pt-5">

        <div className="row p-md-5 justify-content-right">

          <div className="col-sm-8">
            <div className="card shadow-sm p-4">
              <span className="display-8 card-title text-right pr-2"> : {state.title}</span>

              <div className="card-body">
                
                <div className="row">

                  {
                    state.messages && state.messages.map((message, index) => {
                      if (index === state.messages.length - 1) {
                        return(
                          <div className="col-sm-12" key={index}>

                            <div id="lastMessage" className="card p-2 bg-lighter-grey shadow-none">

                              <div className="card-body">
                                <div className="row">

                                  <div className="col-sm-12 text-right">
                                    <img className="rounded-circle ticket-img" src={message.profile_img ? (`https://s3.us-east-2.amazonaws.com/maidancom/${message.profile_img}`) : PH} alt="PI"/>
                                    <b className="font-weight-bold ticket-username mr-2">{message.user}</b>
                                  </div>
                                  
                                  <div className="col-sm-12 mt-2 text-right">
                                    <p dir='ltr' className="text-muted ticket-date">{getDate(message.date)}</p>
                                  </div>

                                  <div className="col-sm-12 text-right ticket-text wordBreak">
                                    {message.body}
                                  </div>

                                </div>
                              </div>

                            </div>

                            <hr/>

                          </div>
                        )
                      } else {
                        return(
                          <div className="col-sm-12" key={index}>

                            <div className="card p-2 bg-lighter-grey shadow-none">

                              <div className="card-body">
                                <div className="row">

                                  <div className="col-sm-12 text-right">
                                    <img className="rounded-circle ticket-img" src={message.profile_img ? (`https://s3.us-east-2.amazonaws.com/maidancom/${message.profile_img}`) : PH} alt="PI"/>
                                    <b className="font-weight-bold ticket-username mr-2">{message.user}</b>
                                  </div>
                                  
                                  <div className="col-sm-12 mt-2 text-right">
                                    <p dir='ltr' className="text-muted ticket-date">{getDate(message.date)}</p>
                                  </div>

                                  <div className="col-sm-12 text-right ticket-text wordBreak">
                                    {message.body}
                                  </div>

                                </div>
                              </div>

                            </div>

                            <hr/>

                          </div>
                        )
                      }
                    })
                  }

                  {
                    state.is_admin ? (
                      <div className="col-sm-12">

                        <div className="mt-2">
                          {loadErrors()}
                        </div>

                        {
                          state.status === 'closed' ? null : (
                            <div className="row">
                        
                              <div className="col-sm-12 mt-4 text-right">

                                <div className="input-group">
                                  <textarea onChange={handleChange} required id="message" rows="6" maxLength="600" placeholder=" " className={"form-control fixed-width-sm col-sm-12 " + (errors.message ? "is-invalid" : "")}/>
                                </div>
                                <div className="float-left font-weight-bold" id="charCount">600</div>

                              </div>
                              
                              <div className="col-sm-12 mt-2 text-left">
                                <button id="close" onClick={handleSubmit} className="btn btn-sm fixed-width-sm btn-grey"> </button>
                                <button id="send" onClick={handleSubmit} className="btn btn-sm fixed-width-sm btn-orange"></button>
                              </div>
                            
                            </div>
                          )
                        }
                      
                      </div>
                    ) : (
                      <div className="col-sm-12">

                        <div className="mt-2">
                          {loadErrors()}
                        </div>

                        <div className="row">
                        
                          <div className="col-sm-12 mt-4 text-right">

                            <div className="input-group">
                              <textarea disabled={!state.user_resp && state.status !== 'closed'} onChange={handleChange} required id="message" rows="6" maxLength="600" placeholder=" " className={"form-control fixed-width-sm col-sm-12 " + (errors.message ? "is-invalid" : "")}/>
                            </div>
                            <div className="float-left font-weight-bold" id="charCount">600</div>

                          </div>

                          {
                          state.status === 'closed' ? (
                            <div className="col-sm-12 mt-2 text-left">
                              <button id="reOpen" onClick={handleSubmit} className="btn btn-sm fixed-width-sm btn-outline-turq">  </button>
                            </div>
                          ) : null
                          }

                          {
                          state.status === 'open' ? (
                            <div className="col-sm-12 mt-2 text-left">
                              <button id="close" onClick={handleSubmit} className="btn btn-sm fixed-width-sm btn-grey"> </button>
                              <button disabled id="send" onClick={handleSubmit} className="btn btn-sm fixed-width-sm btn-orange"></button>
                              <button disabled id="closeNsend" onClick={handleSubmit} className="btn btn-sm fixed-width-sm btn-turq"> </button>
                            </div>
                          ) : null
                          }

                          {
                          state.status === 'waiting' ? (
                            <div className="col-sm-12 mt-2 text-left">
                              <button id="close" onClick={handleSubmit} className="btn btn-sm fixed-width-sm btn-grey"> </button>
                              <button id="send" onClick={handleSubmit} className="btn btn-sm fixed-width-sm btn-orange"></button>
                              <button id="closeNsend" onClick={handleSubmit} className="btn btn-sm fixed-width-sm btn-turq"> </button>
                            </div>
                          ) : null
                          }

                        </div>

                      </div>
                    )
                  }
                
                </div>

              </div>

            </div>
          </div>

          <div className="col-sm-4">
            <div className="card bg-light-grey shadow-sm p-4">
              
              <div className="card-body text-right">
                
                <div className="row">

                  <div className="col-sm-6 text-right">
                    <p className="font-info font-weight-bold">  :</p>
                  </div>
                  <div className="col-sm-6 text-left">
                    <p className="font-info font-weight-bold">{state.user && state.user.username}</p>
                  </div>

                  <div className="col-sm-6 text-right">
                    <p className="font-info font-weight-bold">  :</p>
                  </div>
                  <div className="col-sm-6 text-left">
                    <p className="font-info font-weight-bold text-secondary">{state.type}</p>
                  </div>

                  <div className="col-sm-4 col-md-6 text-right">
                    <p className="font-info font-weight-bold">  :</p>
                  </div>
                  <div className="col-sm-6 text-left">
                    <p className={`font-info text-${state.status && getStatus(state.status)[1]} font-weight-bold`}>{state.status && getStatus(state.status)[0]}</p>
                  </div>

                  <div className="col-sm-12"><hr/></div>

                  <div className="col-sm-6 text-right">
                    <p className="font-info font-weight-bold">  :</p>
                  </div>
                  <div className="col-sm-6 text-left">
                    <p dir='ltr' className="font-info font-weight-bold">{getDate(state.created)}</p>
                  </div>

                  <div className="col-sm-6 text-right">
                    <p className="font-info font-weight-bold">  :</p>
                  </div>
                  <div className="col-sm-6 text-left">
                    <p dir='ltr' className="font-info font-weight-bold">{getDate(state.last)}</p>
                  </div>

                  <div className="col-sm-12"><hr/></div>

                  <div className="col-sm-5 text-right">
                    <p className="font-info font-weight-bold"> :</p>
                  </div>
                  <div className="col-sm-7 text-left">
                    <p dir='ltr' className="font-info font-weight-bold">{state.email || '.'}</p>
                  </div>

                </div>
                
              </div>

            </div>
          </div>

        </div>

      </div>

    </div>
  )
}