import React, { useState, useEffect } from 'react';
import { Link, Redirect } from 'react-router-dom';

// Panels{Users}
import AdminsTable from '../Panels/AdminsTable';
import ProvidersTable from '../Panels/ProvidersTable';
import AssociatesTable from '../Panels/AssociatesTable';
import UsersTable from '../Panels/UsersTable';
import ToProviderTable from '../Panels/ToProviderTable';
import BalanceRequests from '../Panels/BalanceRequests';
import DisabledUsers from '../Panels/DisabledUsers';
import InActiveUsers from '../Panels/InActiveUsers';


// Panels{Labels}
import ElementsTable from '../Panels/ElementsTable';
import FilesTable from '../Panels/FilesTable';

// Panels{Groups/Offers}
import GroupsTable from '../Panels/Groups';
import SellsTable from '../Panels/Sells';
import AddGroupsTable from '../Panels/AddGroupTable';
import AddSellsTable from '../Panels/AddSellsTable';
import TicketsTable from '../Panels/Tickets';


export const SuperAdminPanel = () => {

  const [subActive, setSubActive] = useState({});
  const [redirect, setRedirect] = useState({redirect: false, flash: null})


  useEffect(() => {
    // Authenticate Current User.
    fetch("/api/authenticate/superadmin", {
      method: 'Get',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          
        } else {
          if (data.code === 'AUTH_ERROR'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: ' ',
                message: '     '
              }
            })
          }
          if (data.code === 'ACCESS_DENIED'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: ' ',
                message: '       .'
              }
            })
          }
          if (data.code === 'INVALID_ACCESS_LEVEL'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '     ,    .'
              }
            })
          }
          if (data.code === 'SERVER'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      ,     '
              }
            })
          }
          if (data.code === 'NO_TOKEN'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '    ,       '
              }
            })
          }
          if (data.code === 'INVALID_AUTH_HEADER'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '     ,    .'
              }
            })
          }
          if (data.code === 'NOT_LOGGED_IN'){
            localStorage.clear();
            sessionStorage.clear();
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '        .'
              }
            })
          }
        }
      })
    })

    // Tree Menu
    var toggler = document.getElementsByClassName("caret");
    var i;

    for (i = 0; i < toggler.length; i++) {
      toggler[i].addEventListener("click", function() {
        this.parentElement.querySelector(".nested").classList.toggle("active");
        this.classList.toggle("caret-down");
      });
    }
  }, [])


  const handleSubTableSwitch = (e) => {
    e.preventDefault();

    // Activate Element's Table
    setSubActive({
      [e.target.id]: true
    })

    // Reset All Elements to Normal Color
    var toggler = document.getElementsByClassName('tabs-links');
    var i;
    for (i=0;i < toggler.length; i++){
      toggler[i].classList.remove('text-dark');
      toggler[i].classList.add('text-primary');
    }

    // Change Color of Selected Element.
    document.getElementById(e.target.id).classList.add('text-dark')
  }

  if (redirect.redirect){
    return <Redirect to={{
      pathname: '/',
      state: {flash: redirect.flash}
    }}/>
  }

  return(
    <div className="mb-5 pb-5 pt-5">
      
      <div className="container-fluid mt-5 pt-5">

        <div className="card bg-light-grey shadow-sm p-5">

          <div className="text-center">
            <span className="display-7"> </span>
          </div>

          <hr/>

          <div className="card-body mt-5">

            <div className="row justify-content-center">

              <div className="col-sm-2 mt-5">
                
                <ul id="myUL" className="text-right font-weight-bold">
                  
                  <li className="mt-5">
                    <span className="caret caret-down tabs-special"> </span>
                    <ul className="nested active">
                      <li>
                        <Link className="tabs-links" id="Users" to="#" onClick={handleSubTableSwitch}></Link>
                      </li>
                      <li>
                        <Link className="tabs-links" id="Providers" to="#" onClick={handleSubTableSwitch}></Link>
                      </li>
                      <li>
                        <Link className="tabs-links" id="Associates" to="#" onClick={handleSubTableSwitch}></Link>
                      </li>
                      <li>
                        <Link className="tabs-links" id="Admins" to="#" onClick={handleSubTableSwitch}></Link>
                      </li>
                      <li>
                        <Link className="tabs-links" id="Tickets" to="#" onClick={handleSubTableSwitch}></Link>
                      </li>
                      <li>
                        <Link className="tabs-links" id="Balance" to="#" onClick={handleSubTableSwitch}>  </Link>
                      </li>
                      <li>
                        <Link className="tabs-links" id="Applications" to="#" onClick={handleSubTableSwitch}>   </Link>
                      </li>
                      <li>
                        <Link className="tabs-links" id="Disabled" to="#" onClick={handleSubTableSwitch}> </Link>
                      </li>
                      <li>
                        <Link className="tabs-links" id="Newbie" to="#" onClick={handleSubTableSwitch}> </Link>
                      </li>
                    </ul>
                  </li>

                  <li className="mt-3">
                    <span className="caret caret-down tabs-special">  </span>
                    <ul className="nested active">
                      <li>
                        <Link className="tabs-links" id="Groups" to="#" onClick={handleSubTableSwitch}></Link>
                      </li>
                      <li>
                        <Link className="tabs-links" id="AddGroup" to="#" onClick={handleSubTableSwitch}>  </Link>
                      </li>
                      <li>
                        <Link className="tabs-links" id="Sells" to="#" onClick={handleSubTableSwitch}></Link>
                      </li>
                      <li>
                        <Link className="tabs-links" id="AddSells" to="#" onClick={handleSubTableSwitch}>  </Link>
                      </li>
                    </ul>
                  </li>

                  <li className="mt-3">
                    <span className="caret caret-down tabs-special">    </span>
                    <ul className="nested active">
                      <li>
                        <Link className="tabs-links" id="Files" to="#" onClick={handleSubTableSwitch}></Link>
                      </li>
                      <li>
                        <Link className="tabs-links" id="Elements" to="#" onClick={handleSubTableSwitch}> </Link>
                      </li>
                    </ul>
                  </li>

                </ul>

              </div>

              <div className="col-sm-10 mt-5">

                <div className="text-center">
                  {
                    subActive.Users ? <UsersTable/> : null
                  }

                  {
                    subActive.Providers ? <ProvidersTable/> : null
                  }

                  {
                    subActive.Associates ? <AssociatesTable/> : null
                  }

                  {
                    subActive.Admins ? <AdminsTable/> : null
                  }

                  {
                    subActive.Tickets ? <TicketsTable/> : null
                  }

                  {
                    subActive.Applications ? <ToProviderTable/> : null
                  }

                  {
                    subActive.Disabled ? <DisabledUsers/> : null
                  }

                  {
                    subActive.Newbie ? <InActiveUsers/> : null
                  }

                  {
                    subActive.Balance ? <BalanceRequests/> : null
                  }

                  {
                    subActive.Groups ? <GroupsTable/> : null
                  }

                  {
                    subActive.Sells ? <SellsTable/> : null
                  }

                  {
                    subActive.AddGroup ? <AddGroupsTable/> : null
                  }

                  {
                    subActive.AddSells ? <AddSellsTable/> : null
                  }

                  {
                    subActive.Elements ? <ElementsTable/> : null
                  }

                  {
                    subActive.Files ? <FilesTable/> : null
                  }
                </div>

              </div>

            </div>

          </div>

        </div>

      </div>
      
    </div>
  )
}