import React, { useState, useEffect } from 'react';
import PH from '../../static/images/icon.png'
import { Redirect } from 'react-router-dom';



export const Profile = () => {
  
  // User Information
  const [user, setUser] = useState({});
  const [userErrors, setUserErrors] = useState({});
  
  // Redirect
  const [redirect, setRedirect] = useState(false);
  // Edit
  const [edit, setEdit] = useState(false);

  useEffect(() => {
    // Check If User is Logged-in
    if (!localStorage.maidan_jwt_$m3){
      setRedirect(true);
    }
    

    // Fetch User Data
    fetch('/api/user', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setUser(data.user)
        } else {
          if (data.code === 'SERVER'){
            setUserErrors({
              FetchError: {
                name: '',
                error: '      ,    .',
                type: 'danger'
              }
            })
          }
        }
      })
    })
  }, [])


  const loadUserInfo = () => {
    return(

      <div className="container">

          <div className="row justify-content-center">

            <div className="col-sm-5">
              <p className="font-weight-bold text-muted">
                <i className="fa fa-font ml-2"/>
                  : 
              </p>
              <p className="font-weight-bold">{user.fullname || ' '}</p>
            </div>

            <div className="col-sm-5">
              <p className="font-weight-bold text-muted">
                <i className="fa fa-signature ml-2"/>
                  : 
              </p>
              <p className="font-weight-bold">{user.username}</p>
            </div>

          </div>

          <hr/>

          <div className="row justify-content-center">

            <div className="col-sm-5">
              <p className="font-weight-bold text-muted">
                <i className="fa fa-at ml-2"/> 
                  : 
              </p>
              <p className="font-weight-bold">{user.email}</p>
            </div>

            <div className="col-sm-5">
              <p className="font-weight-bold text-muted">
                <i className="fa fa-mobile-alt ml-2"/> 
                  : 
              </p>
              <p dir='ltr' className="font-weight-bold">{user.phone}</p>
            </div>

          </div>

          {
            user.is_provider ? (
              <div>
                
                <hr/>

                <div className="row justify-content-center">

                  <div className="col-sm-12">
                    <p className="font-weight-bold text-muted">
                      <i className="far fa-building ml-2"/> 
                        : 
                    </p>
                    <p className="font-weight-bold">{user.company}</p>
                  </div>

                </div>


              </div>
            ) : null
          }

          <hr/>

          <div className="row justify-content-center">

            <div className="col-sm-4">
              <p className="font-weight-bold text-muted">
                <i className="fa fa-globe-africa ml-2"/> 
                 : 
              </p>
              <p className="font-weight-bold">{user.country || ' '}</p>
            </div>

            <div className="col-sm-4">
              <p className="font-weight-bold text-muted">
                <i className="fa fa-city ml-2"/> 
                 : 
              </p>
              <p className="font-weight-bold">{user.city || ' '}</p>
            </div>

            <div className="col-sm-4">
              <p className="font-weight-bold text-muted">
                <i className="fa fa-dollar-sign ml-2"/> 
                 : 
              </p>
              <p className="font-weight-bold">{parseFloat(user.balance).toFixed(2)}$</p>
            </div>

          </div>


          <div className="input-group justify-content-center mt-5">
            <button onClick={() => {setEdit(true)}} className="btn btn-md btn-outline-grey">
               
            </button>
          </div>

      </div>

    )
  }

  const loadErrors = () => {
    if (Object.keys(userErrors).length !== 0 && userErrors.constructor === Object){
      return (
        <div>
          {userErrors ? Object.entries(userErrors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  if (redirect){
    return <Redirect to="/"/>
  }

  if (edit){
    return <Redirect to="/settings"/>
  }

  return(
    <div className="mb-5 pb-5 pt-5">
      
      <div className="container mt-5 pt-5">
        
        <div className="row justify-content-center">
          <div className="col-sm-10">
            
            <div className="card bg-light-grey shadow-sm p-4">
              <span className="display-7 card-title text-right pr-5">  : </span>
            
              <div className="card-body text-center">
                
                <div className="mt-2">
                  {loadErrors()}
                </div>
                
                <div className="row mt-4">

                  <div className="col-sm-12">
                    <img src={localStorage.profile_img ? (`https://s3.us-east-2.amazonaws.com/maidancom/${localStorage.profile_img}`) : PH} alt="PI" width="125" height="125" className="rounded-circle"/>
                  </div>

                  <div className="col-sm-12">
                    <span className="display-8 card-text-head text-info">{user.username}</span>
                  </div>

                </div>

                <div className="mt-5">
                  {loadUserInfo()}
                </div>
              
              </div>

            </div>
            
          </div>
        </div>

      </div>

    </div>
  )
}