import React, { useState, useEffect } from 'react';

// Modals
import { Modal, Button } from 'react-bootstrap';
import { Link, Redirect } from 'react-router-dom';

const GroupsTable = () => {

  // User
  // const userData = localStorage.maidan_user
  // const User = userData ? {...JSON.parse(userData)} : {};

  const [groups, setGroups] = useState({groups: null, limit: 50});
  const [errors, setErrors] = useState({});

  const [redirect, setRedirect] = useState({redirect: false, flash: null})

  // Search Groups
  const [search, setSearch] = useState();

  // Modals
  const [deleteModal, setDeleteModal] = useState({view: false, group_id: null})

  useEffect(() => {
    fetch('/api/panel/groups', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setGroups({
            groups: data.groups,
            limit: 50
          })
        } else {
          if ([
            'INVALID_AUTH_HEADER', 
            'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
            'ACCESS_DENIED', 'AUTH_ERROR'
          ].includes(data.code)) {
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
          if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
            localStorage.clear();
            sessionStorage.clear();
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
        }
      })
    })
  }, [])

  const hideGroup = (e) => {
    e.preventDefault();

    const Hide = async() => {
      await fetch(`/api/panel/groups/${deleteModal.group_id}`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
        }
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            setGroups({
              ...groups,
              groups: data.groups,
            });
            resetModals();
          } else {
            if (data.code === 'AUTH_ERROR'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: ' ',
                  message: '     '
                }
              })
            }
            if (data.code === 'ACCESS_DENIED'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: ' ',
                  message: '       .'
                }
              })
            }
            if (data.code === 'INVALID_ACCESS_LEVEL'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '     ,    .'
                }
              })
            }
            if (data.code === 'NO_TOKEN'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '    ,       '
                }
              })
            }
            if (data.code === 'INVALID_AUTH_HEADER'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '     ,    .'
                }
              })
            }
            if (data.code === 'NOT_LOGGED_IN'){
              localStorage.clear();
              sessionStorage.clear();
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '        .'
                }
              })
            }
            if (data.code === 'NO_MATCH_ID'){
              setErrors({
                ID: {
                  name: ' ',
                  error: '     ,     .',
                  type: 'warning'
                }
              })
            }
            if (data.code === 'NOT_YOURS'){
              setErrors({
                Company: {
                  name: ' ',
                  error: '       .',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'SERVER'){
              setErrors({
                Server: {
                  name: '',
                  error: '     ,   .',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'NOT_EXPIRED'){
              setErrors({
                Expired: {
                  name: '',
                  error: '     .',
                  type: 'danger'
                }
              })
            }
          }
        })
      })
    }

    Hide();
  }

  const SearchGroups = async(e) => {
    setSearch({
      ...search,
      [e.target.id]: e.target.value
    })
    await fetch('/api/panel/groups', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      },
      body: JSON.stringify({...search, [e.target.id]: e.target.value})
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setGroups({
            ...groups,
            groups: data.groups
          })
        } else {
          if ([
            'INVALID_AUTH_HEADER', 
            'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
            'ACCESS_DENIED', 'AUTH_ERROR'
          ].includes(data.code)) {
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
          if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
            localStorage.clear();
            sessionStorage.clear();
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
        }
      })
    })
  }

  const LoadGroups = () => {

    const getDate = (seconds) => {
      var date = new Date(1970, 0, 1);
      date.setSeconds(seconds);
      var sloppy_date = new Date(date);
      var tzOffset = sloppy_date.getTimezoneOffset();
      
      var target_date = new Date(sloppy_date.getTime() - tzOffset * 60 * 1000);
  
      var yy = target_date.getFullYear();
      var mm = target_date.getMonth() + 1;
      var dd = target_date.getDate();
  
      var h = target_date.getHours();
      var m = target_date.getMinutes();
  
      return `${yy}-${(mm>9?'':'0')+mm}-${(dd>9?'':'0')+dd} ${(h>9?'':'0')+h}:${(m>9?'':'0')+m}`
    }

    const getStatus = (status) => {
      let types = {
        canceled: ['', 'warning'],
        available: ['', 'success'],
        expired: ['', 'danger'],
        finished: [' ', 'secondary']
      }

      return types[status]
    }

    const getDistributeType = (type) => {
      let types = {
        display: [' ', 'dark'],
        request: [' ', 'primary']
      }

      return types[type]
    }

    const groups_patch = groups.groups && groups.groups.slice(0, groups.limit);

    return(
      <tbody>
        <tr>
          <th className="table-header font-weight-bold">
            #
          </th>

          <td className="table-body">
            <input onChange={SearchGroups} id="name" type="text" placeholder=" : " className="form-contro"/>
          </td>

          <td className="table-body"/>
          <td className="table-body"/>
          <td className="table-body"/>
          <td className="table-body"/>
          <td className="table-body"/>
          <td className="table-body"/>
          <td className="table-body"/>
          <td className="table-body"/>
          <td className="table-body"/>
          <td className="table-body"/>
          <td className="table-body"/>
        </tr>
        {
          groups_patch ? groups_patch.map((group, index) => {
            let status = getStatus(group.status)
            let disType = getDistributeType(group.distribute_type)
            return(
              <tr className="" key={index}>
                <th className="table-header">
                  <Link className="text-primary" to={`/admin/ads/${group.id}`}>{group.id}</Link>
                </th>
                <td className="table-header">{group.company}</td>
                <td className="table-header">{group.coupons_num}</td>
                <td className="table-header">{group.coupon_price}$</td>
                <td className="table-header">{group.full_price}$</td>
                <td className="table-header">{group.coupons_left}</td>
                <td className={`table-header text-${disType[1]}`}>{disType[0]}</td>
                <td className={`table-header text-${status[1]}`}>{status[0]}</td>
                <td className="table-header">{group.views}</td>
                <td className="table-header">{group.unique_views}</td>
                {/* <td dir='ltr' className="table-header">{getDate(group.start_date)}</td> */}
                <td dir='ltr' className="table-header">{getDate(group.expire_date)}</td>
                <td className="table-body">
                  <button onClick={() => {setDeleteModal({view: true, group_id: group.id})}} className="btn btn-sm btn-outline-danger font-weight-bold"></button>
                </td>
            </tr>
            )
          }) : null
        }
      </tbody>
    )
  }

  const AddLimit = () => {
    if (groups.limit < groups.groups.length){
      let new_limit = groups.limit + 50;
      setGroups({...groups, limit: new_limit})
    }
  }

  const ReduceLimit = () => {
    if (groups.limit > 50){
      let new_limit = groups.limit - 50;
      setGroups({...groups, limit: new_limit})
    }
  }

  const loadErrors = () => {
    if (Object.keys(errors).length !== 0 && errors.constructor === Object){
      return (
        <div>
          {errors ? Object.entries(errors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  const resetModals = () => {

    // Reset States
    setErrors({});
    setDeleteModal({view: false, group_id: null});
  }

  if (redirect.redirect){
    return <Redirect to={{
      pathname: '/',
      state: {flash: redirect.flash}
    }}/>
  }

  return(
    <div className="table-responsive mt-4">

      <table className="table mt-2 table-sm table-bordered">
        <thead>
          <tr className="bg-light-grey">
            <th className="font-weight-bold" scope="col">ID</th>
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col"></th>
            {/* <th className="font-weight-bold" scope="col"> </th> */}
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col"></th>
          </tr>
        </thead>
        {LoadGroups()}
      </table>
      <div className="justify-content-center">
        {(groups.groups && groups.limit < groups.groups.length) ? (<button onClick={AddLimit} className="btn btn-sm btn-outline-primary"> </button>) : null}
        {groups.limit > 50 ? (<button onClick={ReduceLimit} className="btn btn-sm btn-outline-secondary"> </button>) : null}
      </div>
    
      {/* Delete Modal */}
      <Modal show={deleteModal.view} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">  (#{deleteModal.group_id})</Modal.Title>
        </Modal.Header>
        
        <Modal.Body className="text-center">

          <div className="container mt-2">
            {loadErrors()}
          </div>

          <div className="container mt-4">
            
            <p className="font-weight-bold text-danger">
                    
            </p>

            <p className="font-info">
                   ,      .
            </p>

          </div>

        </Modal.Body>
        
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={resetModals}>
              
            </Button>
            <Button variant="danger" onClick={hideGroup}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal> 

    
    </div>
  )
}

export default GroupsTable