import React, { useState, useEffect } from 'react';
// Modals
import { Modal, Button } from 'react-bootstrap';
import { Redirect } from 'react-router-dom';


const DisabledUsers = () => {

  const [users, setUsers] = useState({users: null, limit: 50});

  const [redirect, setRedirect] = useState({redirect: false, flash: null});
  const [errors, setErrors] = useState({});

  // Modals
  const [modalState, setModalState] = useState({});

  const [isActivate, setActivatingModal] = useState(false);

  useEffect(() => {
    fetch('/api/panel/users/disabled', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setUsers({
            users: data.users,
            limit: 50
          })
        } else {
          if ([
            'INVALID_AUTH_HEADER', 
            'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
            'ACCESS_DENIED', 'AUTH_ERROR'
          ].includes(data.code)) {
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
          if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
            localStorage.clear();
            sessionStorage.clear();
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
        }
      })
    })
  }, [])

  const handleActivate = (user) => {
    setActivatingModal(true);
    setModalState(user);
  }

  const LoadUsers = () => {

    const users_patch = users.users && users.users.slice(0, users.limit);

    return(
      <tbody>
        {
          users_patch ? users_patch.map((user, index) => (
            <tr className="" key={index}>
              <th className="table-header">{user.id}</th>
              <td className="table-body">{user.username}</td>
              <td className="table-body">{user.fullname}</td>
              <td className="table-body">{user.email}</td>
              <td dir='ltr' className="table-body">{user.phone}</td>
              <td className="table-body">{user.claimed_coupons}</td>
              <td className="table-body">{user.balance} $</td>
              <td className="table-body">
                <button onClick={() => {handleActivate(user)}} className="btn btn-sm btn-success font-weight-bold"></button>
              </td>
            </tr>
          )) : null
        }
      </tbody>
    )
  }

  const AddLimit = () => {
    if (users.limit < users.users.length){
      let new_limit = users.limit + 50;
      setUsers({...users, limit: new_limit})
    }
  }

  const ReduceLimit = () => {
    if (users.limit > 50){
      let new_limit = users.limit - 50;
      setUsers({...users, limit: new_limit})
    }
  }

  // Settings

  const handleReActivate = (e) => {
    e.preventDefault();

    const ActivateUser = async() => {
      await fetch(`/api/panel/users/${modalState.id}/reactivate`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
        }
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            setUsers({
              ...users,
              users: data.users
            });
            resetModals();
          } else {
            if ([
              'INVALID_AUTH_HEADER', 
              'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
              'ACCESS_DENIED', 'AUTH_ERROR'
            ].includes(data.code)) {
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      .'
                }
              })
            }
            if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
              localStorage.clear();
              sessionStorage.clear();
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      .'
                }
              })
            }
            if (data.code === 'NO_MATCH_ID'){
              setErrors({
                ID: {
                  name: '',
                  error: '  ,     ,    .',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'SERVER'){
              setErrors({
                Server: {
                  name: '',
                  error: '     ,    .',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'NOT_DISABLED'){
              setErrors({
                Server: {
                  name: '',
                  error: '  ,    .',
                  type: 'warning'
                }
              })
            }
          }
        })
      })
    }

    ActivateUser();
  }

  const loadErrors = () => {
    if (Object.keys(errors).length !== 0 && errors.constructor === Object){
      return (
        <div>
          {errors ? Object.entries(errors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  const resetModals = () => {

    // Reset State, Errors
    setErrors({});
    setModalState({});

    // Reset Modals
    setActivatingModal(false);
  }

  if (redirect.redirect){
    return <Redirect to={{
      pathname: '/',
      state: {flash: redirect.flash}
    }} />
  }

  return(
    <div className="table-responsive mt-4">
    
      <table className="table table-sm table-bordered">
        <thead>
          <tr className="bg-light-grey">
            <th className="font-weight-bold" scope="col">ID</th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col"></th>
          </tr>
        </thead>
        {LoadUsers()}
      </table>
      <div className="justify-content-center">
        {(users.users && users.limit < users.users.length) ? (<button onClick={AddLimit} className="btn btn-sm btn-outline-primary"> </button>) : null}
        {users.limit > 50 ? (<button onClick={ReduceLimit} className="btn btn-sm btn-outline-secondary"> </button>) : null}
      </div>

      {/* Activate Modal */}
      <Modal show={isActivate} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">  ({modalState.username}) : </Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">

          <div className="container mt-2">
            {loadErrors()}
          </div>

          <div className="container">
            <p className="text-info font-weight-bold">
                    
            </p>
          </div>

        </Modal.Body>
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="outline-grey" onClick={resetModals}>
              
            </Button>
            <Button onClick={handleReActivate} variant="success">
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>
      
    </div>
  )
}


export default DisabledUsers