import React, { useState } from 'react';
import { Link, Redirect, useParams } from 'react-router-dom';
import { useEffect } from 'react';

// Modals
import { Modal, Button } from 'react-bootstrap';



export const ViewGroup = () => {

  const currentUser = localStorage.maidan_user ? JSON.parse(localStorage.maidan_user) : {};
  const { gid } = useParams()
  const [group, setGroup] = useState();

  const [flash, setFlash] = useState({});
  const [isFlash, setFlashModal] = useState(false);

  const [notify, setNotify] = useState({});
  const [isNotify, setNotifyModal] = useState(false);
  const [redirect, setRedirect] = useState({redirect: false, flash: null})
  

  useEffect(() => {
    fetch(`/api/groups/${gid}`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setGroup(data.group);
        } else {
          if (data.code === 'NO_MATCH_ID') {
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      '
              }
            })
          }
        }
      })
    })
  }, [gid])


  const OpenBranchMap = (e) => {
    e.preventDefault();
    window.open(e.target.id)
  }
  
  const handleRequest = (e) => {
    e.preventDefault();
    const SendRequest = async() => {
      await fetch(`/api/groups/request/${group.id}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
        },
        body: JSON.stringify({request_type: group.distribute_type})
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            setFlash({
              title: '   ',
              message: '   !',
              type: 'success'
            })
            setFlashModal(true);
          } else {
            if (data.code === 'NO_MATCH_ID'){
              setFlash({
                title: '   ',
                message: '  ,    ',
                type: 'error'
              })     
            }
            if (data.code === 'SERVER'){
              setFlash({
                title: '',
                message: '     ,     .',
                type: 'error'
              })
            }
            if (data.code === 'INVALID_TYPE'){
              setFlash({
                title: '',
                message: '     ,     .',
                type: 'error'
              })
            }
            if (data.code === 'NO_COUPON'){
              setFlash({
                title: '',
                message: ',     ,    .',
                type: 'error'
              })
            }
            if (data.code === 'HAS_CLAIMED'){
              setFlash({
                title: '',
                message: '     ,      24 .',
                type: 'error'
              })
            }
            if (data.code === 'HAS_A_COUPON'){
              setFlash({
                title: '',
                message: '      ,     .',
                type: 'error'
              })
            }
            if (data.code === "ITS_YOUR_COUPON"){
              setFlash({
                title: '   ',
                message: '     .',
                type: 'error'
              })
            }
            if (data.code === "COUNTRY_ERROR"){
              setFlash({
                title: '   ',
                message: '       .',
                type: 'error'
              })
            }
            if (data.code === "SING_UPS_EXPIRED"){
              setFlash({
                title: '   ',
                message: '       ,     .',
                type: 'error'
              })
            }
            if (data.code === "ALREADY_SIGNED_UP"){
              setFlash({
                title: '   ',
                message: '       ,     .',
                type: 'error'
              })
            }
            if (data.code === "DISABLED"){
              setFlash({
                title: '',
                message: '   ,    ,    .',
                type: 'error',
                logout: true
              });
            }
            if (data.code === "UNCLAIMABLE"){
              setFlash({
                title: '',
                message: '     ,   ,     .',
                type: 'error',
              });
            }
            if ([
                'INVALID_AUTH_HEADER', 
                'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
                'ACCESS_DENIED', 'AUTH_ERROR'
              ].includes(data.code)){
                setFlash({
                  title: '',
                  message: '       ',
                  type: 'error'
                })
            }
            if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
              localStorage.clear();
              sessionStorage.clear();
              window.location.reload();
            }
            setFlashModal(true)
          }
        })
      })
    }

    SendRequest();
    setNotifyModal(false);
  }

  const handleNotify = (e) => {
    e.preventDefault();
    const user = localStorage.logged;

    if (user){
      setNotify({
        title: ' ',
        message: '        ,   ',
        can_sign_up: true
      })
    } else {
      setNotify({
        title: ' ',
        message: '       .',
        can_sign_up: false
      })
    }
    setNotifyModal(true)
  }

  const getDate = (seconds) => {
    var date = new Date(1970, 0, 1);
    date.setSeconds(seconds);

    var sloppy_date = new Date(date);
    var tzOffset = sloppy_date.getTimezoneOffset();
    
    var target_date = new Date(sloppy_date.getTime() - tzOffset * 60 * 1000);

    var yy = target_date.getFullYear();
    var mm = target_date.getMonth() + 1;
    var dd = target_date.getDate();

    var h = target_date.getHours();
    var m = target_date.getMinutes();

    return `${yy}-${(mm>9?'':'0')+mm}-${(dd>9?'':'0')+dd} ${(h>9?'':'0')+h}:${(m>9?'':'0')+m}`
  }

  const setTimer = (seconds) => {
    var date = new Date(1970, 0, 1);
    date.setSeconds(seconds);

    var deadline = new Date(date).getTime();
    var x = setInterval(function() {
      var sloppy_time = new Date();
      // get the timezone offset from local time in minutes
      var tzDifference = sloppy_time.getTimezoneOffset();
      // convert the offset to milliseconds, add to sloppy_time, and make new date
      var now = new Date(sloppy_time.getTime() + tzDifference * 60 * 1000);

      var t = deadline - now;
      
      var hh = Math.floor((t%(1000 * 60 * 60 * 24))/(1000 * 60 * 60)); 
      var mm = Math.floor((t % (1000 * 60 * 60)) / (1000 * 60)); 
      var ss = Math.floor((t % (1000 * 60)) / 1000);

      let timer = document.getElementById('Timer')
      if (timer){
        timer.innerHTML = `${(hh>9?'':'0')+hh}:${(mm>9?'':'0')+mm}:${(ss>9?'':'0')+ss}`
      }

      if (t < 0){
        clearInterval(x); 
        document.getElementById('TimerEl').innerHTML = '   ';
        document.getElementById('TimerEl').classList.add('text-danger')
      }
    })
  }

  const loadCard = () => {
    // Branch Configuration
    const groupBranches = group && group.branches;
    const lenGroupBranches = groupBranches.length;
    // Images Configuration
    const groupImages = group && group.images;
    
    return(
      <div className="card hoverable bg-light-grey">

        <div className="card-body ">

          <div className="row justify-content-center">
            
            <div className="col-sm-10">
              <div className="carousel slide carousel-fade" id="GroupCard" data-ride="carousel">

                <div className="carousel-inner">

                  {/* <div className="carousel-item active">
                    <img loading="lazy"  src={`Rests`} alt="Coupon 1" width="100%" height="30%"/>
                  </div>

                  <div className="carousel-item">
                    <img loading="lazy"  src={`Games`} alt="Coupon 2" width="100%" height="30%"/>
                  </div> */}

                  {groupImages.map((key, index) => {
                    if (index === 0){
                      return(
                        <div key={index} className="carousel-item active">
                          <img className="img-fluid carousel-img" loading="lazy" src={`https://s3.us-east-2.amazonaws.com/maidancom/${key}`} alt={key} width="100%"/>
                        </div>
                      )
                    } else {
                      return(
                        <div key={index} className="carousel-item">
                          <img className="img-fluid carousel-img" loading="lazy" src={`https://s3.us-east-2.amazonaws.com/maidancom/${key}`} alt={key} width="100%"/>
                        </div>
                      )
                    }
                  })}

                </div>

                <a className="carousel-control-prev" href="#GroupCard" role="button" data-slide="prev">
                  {/* <span className="carousel-control-prev-icon" aria-hidden="true"></span> */}
                  <i className="fa fa-arrow-alt-circle-left carousel-control-prev-icons" aria-hidden="true"></i>
                  <span className="sr-only"></span>
                </a>
                
                <a className="carousel-control-next" href="#GroupCard" role="button" data-slide="next">
                  {/* <span className="carousel-control-next-icon" aria-hidden="true"></span> */}
                  <i className="fa fa-arrow-alt-circle-right carousel-control-next-icons" aria-hidden="true"></i>
                  <span className="sr-only"></span>
                </a>

              </div>
            </div>

          </div>

          <div className="row mt-4 justify-content-center">

            <div className="col-sm-6 text-right mt-4">

              <p className="text-dark font-weight-bold"><i className="fa fa-utensils fa-s"/>  : {group.company}</p>

              <p className="text-dark"><i className="fa fa-receipt fa-s"/>   : {group.type}.</p>

              <p className="text-dark"><i className="fa fa-dollar-sign fa-s"/>   : {group.coupon_price}$ ({parseFloat(group.coupon_price*3.75).toFixed(2)}  ).</p>
              
              <p className="text-dark"><i className="fa fa-database fa-s"/>   : {group.coupons_left} (/).</p>

            </div>

            <div className="col-sm-4 text-right mt-md-4 col-gap">
              <span className="text-dark font-weight-bold"><i className="fa fa-globe fa-s"/>  : </span>
              <p className="text-dark">{group.country}</p>

              <span className="text-dark font-weight-bold"><i className="fa fa-code-branch fa-s"/>  : </span>
              <p className="text-dark">
                {groupBranches.map((branch, index) => {
                  if (lenGroupBranches === (index+1)){
                    return(<span key={index}><Link to="#" id={branch.map} onClick={OpenBranchMap}>{branch.city}-{branch.name}</Link>.</span>)
                  } else {
                    return(<span key={index}><Link to="#" id={branch.map} onClick={OpenBranchMap}>{branch.city}-{branch.name}</Link>, </span>)
                  }
                })}
              </p>
            
            </div>

            <div className="col-sm-10 text-right mt-2">
              <span className="text-dark font-weight-bold"><i className="fa fa-comment-dots fa-s"/>  : </span>
                <blockquote className="text-muted mt-2 blockquote text-right text-quote">
                  {group.description}
                </blockquote>
            </div>
            
            <div className="col-sm-10 text-right mt-2">
              <span className="text-dark font-weight-bold"><i className="fa fa-calendar-times fa-s"/>   : </span>
              <p className="mt-2 mr-3" dir="ltr">{getDate(group.expire_date)}</p>
            </div>

          </div>

          {
            group.status === 'expired' ? (
              <p className="text-danger font-weight-bold"> .</p>
            ) : (
              <div>
                {
                  parseInt(group.coupons_left) > 0 ? (
                    <div>
                    {
                      group.claimants.includes(currentUser.id) ? (
                        <div>
                          <div className="input-group justify-content-center mt-3">
                            <button disabled className="btn btn-lg btn-grey">  <i className="fa fa-cart-arrow-down fa-s"></i></button>
                          </div>
                          <small className="text-muted">     </small>
                        </div>
                      ) : (
                        <div className="input-group justify-content-center mt-3">
                          <button onClick={handleNotify} className="btn btn-lg btn-orange">  <i className="fa fa-cart-arrow-down fa-s"></i></button>
                        </div>
                      )
                    }

                    {
                      group.distribute_type === 'display' ? (
                        <p id={'TimerEl'} className="font-weight-bold">    : <span id={'Timer'}>{setTimer(group.distribute_end_date, group.id)}</span></p>

                      ): (
                        <p className="font-weight-bold">    </p>
                      )
                    }
                    </div>
                  ) : (
                    <p className="text-danger font-weight-bold">  !</p>
                  )
                }
              </div>
            )
          }

        </div>

      </div>
    )
  }

  const resetFlash = () => {
    if (flash.logout){
      localStorage.removeItem('maidan_jwt_$m3');
      localStorage.removeItem('maidan_user');
      localStorage.removeItem('logged');
      localStorage.removeItem('maidan_user_access');
      localStorage.removeItem('profile_img');
      sessionStorage.clear();
      window.location.href = window.location.origin
    }
    setFlashModal(false);
    setFlash({});
    window.location.reload();
  }

  if (redirect.redirect){
    return <Redirect to={{
      pathname: '/',
      state: {flash: redirect.flash}
    }}/>
  }

  return(
    <div className="mb-5 pb-5">

      <div className="pt-4">
        
        <div id="carousel-example-1z" className="mt-5 pt-5 pr-md-2 pl-md-2 carousel slide carousel-fade" data-ride="carousel">

          <ol className="carousel-indicators">
            <li data-target="#carousel-example-1z" data-slide-to="0" className="active"></li>
            <li data-target="#carousel-example-1z" data-slide-to="1"></li>
            {/* <li data-target="#carousel-example-1z" data-slide-to="2"></li> */}
          </ol>

          <div className="carousel-inner" role="listbox">

            <div className="carousel-item carousel-sitem active">
              <img loading="lazy"  className="w-100 h-100 img-fluid" src="https://sites.google.com/site/moh897/banner1.png"
                alt="First slide"/>
            </div>

            <div className="carousel-item carousel-sitem">
              <img loading="lazy"  className="w-100 h-100 img-fluid" src="https://sites.google.com/site/moh897/banner2.png"
                alt="Second slide"/>
            </div>

          </div>

          <a className="carousel-control-prev" href="#carousel-example-1z" role="button" data-slide="prev">
            {/* <span className="carousel-control-prev-icon" aria-hidden="true"></span> */}
            <i className="fa fa-arrow-alt-circle-left carousel-control-prev-icons" aria-hidden="true"></i>
            <span className="sr-only"></span>
          </a>

          <a className="carousel-control-next" href="#carousel-example-1z" role="button" data-slide="next">
            {/* <span className="carousel-control-next-icon" aria-hidden="true"></span> */}
            <i className="fa fa-arrow-alt-circle-right carousel-control-next-icons" aria-hidden="true"></i>
            <span className="sr-only"></span>
          </a>

        </div> 

      </div>

      <div className="mt-5 text-center container">
        <span className="display-6 shadow-text-sm qsemh-turq">
           ({gid}#)
        </span>
        <hr/>
        <div className="mt-4">
          <div className="row justify-content-center">
            <div className="col-sm-11">
              {group ? loadCard() : null}
            </div>
          </div>
          
        </div>
      </div>

      {/* Flash Message Modal */}
      <Modal show={isFlash} onHide={resetFlash}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">{flash.title}</Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">
          {flash.type === 'success' ? (
            <i className="fa fa-check-circle text-success fa-3x"/>
          ) : flash.type === 'error' ? (
            <i className="fa fa-times-circle text-danger fa-3x"/>
          ) : null}
          <p className="mt-2 lead text-muted">
            {flash.message}
          </p>  
        </Modal.Body>
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="outline-success" onClick={resetFlash}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>
      
      {/* Notification Modal */}
      <Modal show={isNotify} onHide={() => {setNotifyModal(false)}}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">{notify.title}</Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">
          {!notify.can_sign_up ? (
            <i className="fa fa-times-circle text-danger fa-3x"/>
          ) : null}
          <p className="mt-2 lead text-muted">
            {notify.message}
          </p>
        </Modal.Body>
        <Modal.Footer>
          <div className="input-group justify-content-center">
            {notify.can_sign_up ? (
              <div>
                <Button variant="outline-grey" onClick={() => {setNotifyModal(false)}}>
                  
                </Button>
                <Button variant="outline-success" onClick={handleRequest}>
                  
                </Button>
              </div>
            ) : (
              <Button variant="outline-grey" onClick={() => {setNotifyModal(false)}}>
                
              </Button>
            )}
          </div>
        </Modal.Footer>
      </Modal>   
    </div>
  )
}