import React from 'react';
import copy from 'copy-to-clipboard';
import { Link } from 'react-router-dom';




export const Footer = () => {
  const currentYear = new Date().getFullYear()


  return (
    <footer className="page-footer bg-footer font-small">

      <div className="bg-sub-footer">
        <div className="container">

          <div className="row py-3 d-flex align-items-center">

            <div className="col-md-6 col-lg-5 text-center text-md-right mb-4 mb-md-0">
              <h6 className="mb-0 text-dark font-weight-bold">      </h6>
            </div>

            <div className="col-md-6 col-lg-7 text-center text-md-left">

              <Link dir="ltr" title="@qsemh1" to="#" onClick={() => {window.open("https://t.me/qsemh1")}} className="fb-ic text-primary">
                <span className="fab fa-telegram-plane fa-2x font-weight-bold mr-4"/>
              </Link>

              <Link dir="ltr" title="@qsemh" to="#" onClick={() => {window.open("https://twitter.com/qsemh")}} className="li-ic text-info">
                <span className="fab fa-twitter fa-2x font-weight-bold mr-4"/>
              </Link>

              <Link dir="ltr" title="@" to="#" className="wa-ic text-success">
                <span className="fab fa-whatsapp fa-2x font-weight-bold mr-4"/>
              </Link>

              <Link dir="ltr" title="@qsemh" to="#" onClick={() => {window.open("https://www.instagram.com/qsemh/")}} className="ins-ic text-warning">
                <span className="fab fa-instagram fa-2x font-weight-bold mr-4"/>
              </Link>

              <Link dir="ltr" title="QsemhApp@gmail.com" to="#" onClick={() => {copy("QsemhApp@gmail.com")}} className="ins-ic text-danger">
                <span className="fa fa-at fa-2x font-weight-bold mr-4"/>
              </Link>

            </div>

          </div>

        </div>
      </div>
      <hr/>

      <div className="container text-center text-md-left">

        <div className="row mt-3">
          
          <div className="col-md-3 text-right col-lg-2 col-xl-2 mx-auto mb-4">

            <h6 className="text-uppercase font-weight-bold text-dark"> </h6>
            
            <hr className="deep-purple accent-2 mb-4 mt-0 d-inline-block mx-auto" style={{width: "85px"}}/>
            
            <p>
              <Link className="font-weight-bold qsemh-turq" to="/"> </Link>
            </p>

            <p>
              <Link className="font-weight-bold qsemh-turq" to="/ads"></Link>
            </p>

            <p>
              <Link className="font-weight-bold qsemh-turq" to="/coupons/offers"> </Link>
            </p>
            
          </div>
        
          <div className="col-md-2 text-right col-lg-2 col-xl-2 mx-auto mb-4">

            <h6 className="text-uppercase font-weight-bold text-dark"> </h6>
            <hr className="deep-purple accent-2 mb-4 mt-0 d-inline-block mx-auto" style={{width: "78px"}}/>  

            <p>
              <Link className="font-weight-bold qsemh-turq" to="/terms"> </Link>
            </p>
            
          </div>

          <div className="col-md-3 text-right col-lg-4 col-xl-3 mx-auto mb-4">

            <h6 className="text-uppercase font-weight-bold text-dark">  ""</h6>
            <hr className="deep-purple accent-2 mb-4 mt-0 d-inline-block mx-auto" style={{width: "152px"}}/>

          </div>

        </div>

      </div>

      <div className="footer-copyright font-weight-bold text-dark text-center py-3">
            "" {currentYear} 
      </div>
    
    </footer>
  )
}