import React, { useEffect, useState } from 'react';
import { Link, withRouter } from 'react-router-dom';
// Modals
import { Modal, Button } from 'react-bootstrap';

// Phone Input
import { getCountryCallingCode } from 'react-phone-number-input';
// Place Holder Image
import PH from '../../static/images/icon.png'
import LogoD from '../../static/images/logo-d.png'



const Navbar = () => {

  // User
  const userData = localStorage.maidan_user
  const User = userData ? {...JSON.parse(userData)} : {};

  // Basic Data
  const [basic, setBasicData] = useState([]);

  const [regState, setRegState] = useState({
    email: '',
    username: '',
    password: '',
    pre_phone: null,
    country: ''
  });
  const [registerModal, setRegister] = useState(false);
  const [regErrors, setRegErrors] = useState({});
  const [successReg, setSuccessReg] = useState(false);
  
  const [logState, setLogState] = useState({
    email: '',
    password: ''
  });
  const [loginModal, setLogin] = useState(false);
  const [logErrors, setLogErrors] = useState({});
  
  const [logoutModal, setLogout] = useState(false);
  const [logoutErrors, setLogoutErrors] = useState({});

  const [resetPasswModal, setResetPassw] = useState(false);
  const [resetPasswState, setResetPasswState] = useState({
    email: null,
    submitted: false,
    token: null,
    password: null,
    confirm_password: null
  })
  const [resetPasswErrors, setResetPasswErrors] = useState({});

  useEffect(() => {
    let basicData = JSON.parse(localStorage.maidan_elements);
    setBasicData(basicData);
  }, [])

  /* Login */
  const resetLogin = () => {

    setLogErrors({});
    setLogState({
      email: '',
      password: ''
    });
    setLogin(false);
  }

  const handleLogChange = (e) => {
    e.preventDefault()
    setLogErrors({});
    setLogState({
      ...logState,
      [e.target.id]: e.target.value
    })
  }

  const handleLogSubmit = (e) => {
    e.preventDefault()

    const Login = async() => {
      await fetch('/api/login', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        body: JSON.stringify({...logState, logged: localStorage.logged})
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            localStorage.setItem('logged', true);
            localStorage.setItem('maidan_jwt_$m3', data.jwt);
            localStorage.setItem('maidan_user_access', data.access_level);
            localStorage.setItem('maidan_user', JSON.stringify(data.user));
            if (data.profile_img){
              localStorage.setItem('profile_img', data.profile_img);
            }
            setLogErrors({});
            setLogState({});
            setLogin(false);
            // Reload Page
            window.location.reload();
          } else {
            if (data.code === 'SERVER'){
              setLogErrors({
                serverError: {
                  name: '',
                  error: '     ,   .',
                  type: 'danger'
                }
              })  
            }
            if (data.code === 'LOGGED_IN'){
              setLogErrors({
                serverError: {
                  name: '',
                  error: '     ,       .',
                  type: 'danger'
                }
              })  
            }
            if (data.code === 'INVALID_REQUEST'){
              setLogErrors({
                requestError: {
                  name: '  ',
                  error: '     ,    .',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'NOT_ACTIVE'){
              setLogErrors({
                notActiveAccount: {
                  name: '',
                  error: '   ,    ',
                  link: {
                    text: ' ',
                    id: 'ACTIVE_LINK'
                  },
                  type: 'warning'
                }
              })
            }
            if (data.code === 'DISABLED'){
              setLogErrors({
                notActiveAccount: {
                  name: '',
                  error: '  ,      .',
                  type: 'warning'
                }
              })
            }
            if (data.code === 'NO_EMAIL'){
              setLogErrors({
                email: {
                  name: ' ',
                  error: '   ,   ',
                  link: {
                    text: ' ',
                    id: 'REGISTER'
                  },
                  type: 'warning'
                }
              })
            }
            if (data.code === 'WRONG_PASSW'){
              setLogErrors({
                password: {
                  name: ' ',
                  error: '   ,      .',
                  type: 'warning'
                }
              })
            }
          }
        })
      })
    }

    Login();
  }
  /* END Login */ 

  /* Register */
  const resetRegister = () => {

    setRegErrors({});
    setRegState({
      email: '',
      username: '',
      password: '',
      country: '',
      pre_phone: null
    });
    setRegister(false);
  }

  const handleRegChange = (e) => {
    e.preventDefault()

    // Reset Register Errors
    setRegErrors({});

    if (e.target.id === 'pre_phone'){
      setRegState({
        ...regState,
        [e.target.id]: e.target.name,
        country: e.target.title
      });

      document.getElementById('pre_phone_selector').innerText = e.target.name;

    } else {
      setRegState({
        ...regState,
        [e.target.id]: e.target.value
      })
    }
    
  }

  const handleRegSubmit = (e) => {
    e.preventDefault()

    const Register = async() => {
      await fetch('/api/register', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        body: JSON.stringify({...regState, hostname: window.location.origin, logged: localStorage.logged})
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            setRegErrors({});
            setRegister(false);
            setRegState({
              email: '',
              username: '',
              password: '',
              country: '',
              pre_phone: null
            });
            setSuccessReg(true);
          } else {
            if (data.code === 'SERVER'){
              setRegErrors({
                serverError: {
                  name: '',
                  error: '     ,   .',
                  type: 'danger'
                }
              })  
            }
            if (data.code === 'INVALID_REQUEST'){
              setRegErrors({
                requestError: {
                  name: '  ',
                  error: '     ,    .',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'EMAIL_EXIST'){
              setRegErrors({
                email: {
                  name: ' ',
                  error: '   ,     ',
                  type: 'warning'
                }
              })
            }
            if (data.code === 'USERNAME_EXIST'){
              setRegErrors({
                username: {
                  name: ' ',
                  error: '   ,     .',
                  type: 'warning'
                }
              })
            }
            if (data.code === 'INVALID_USERNAME'){
              setRegErrors({
                username: {
                  name: ' ',
                  error: '    : ,    (_).',
                  type: 'warning'
                }
              })
            }
            if (data.code === 'PHONE_EXIST'){
              setRegErrors({
                phone: {
                  name: ' ',
                  error: '   ,     .',
                  type: 'warning'
                }
              })
            }
          }
        })
      })
    }

    // Register();

    // Validate Password
    if (regState.password.length < 8){
      setRegErrors({
        ...regErrors,
        password: {
          name: ' ',
          error: '      8   .',
          type: 'warning'
        }
      })
    } 
    else if (regState.password !== regState.confirm_password) {
      setRegErrors({
        ...regErrors,
        password: {
          name: ' ',
          error: " ' '     '  '.",
          type: 'warning'
        }
      })
    } 
    else if (!regState.pre_phone) {
      setRegErrors({
        ...regErrors,
        pre_phone: {
          name: ' ',
          error: '   ,   .',
          type: 'warning'
        }
      })
    }
    else if (regState.phone[0] !== "5" || regState.phone.length !== 9){
      setRegErrors({
        ...regErrors,
        phone: {
          name: ' ',
          error: '      5     9 .',
          type: 'warning'
        }
      })
    }
    else {
      Register();
    }
  }
  /* END Register */

  /* Logout */
  const handleLogout = (e) => {
    e.preventDefault();

    const Logout = async() => {
      await fetch('/api/logout', {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        }
      }).then(resp => {
        resp.json().then(_ => {
          setLogout(false);
          setLogoutErrors({});
          localStorage.removeItem('maidan_jwt_$m3');
          localStorage.removeItem('maidan_user');
          localStorage.removeItem('logged');
          localStorage.removeItem('maidan_user_access');
          localStorage.removeItem('profile_img');
          sessionStorage.clear();
          window.location.href = window.location.origin
        })
      })
    }

    Logout();
  }

  /* END Logout */

  /* Reset Password */

  const resetPasswReset = () => {

    // Reset Password Reset State
    setResetPassw(false);
    setResetPasswState({
      email: null,
      submitted: false,
      password: null,
      token: null,
      confirm_password: null
    });
    setResetPasswErrors({});
  }

  const handleResetPasswCall = (e) => {
    e.preventDefault();
    // Set Modal State
    setResetPassw(true);

    // Reset Login Modal & State
    resetLogin();
  }

  const sendResetEmail = (e) => {
    e.preventDefault()

    const sendResetPasswEmail = async() => {
      await fetch(`/api/user/${resetPasswState.email}/sendResetToken`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        }
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            setResetPasswState({
              ...resetPasswState,
              submitted: true
            })
            setResetPasswErrors({});
          } else {
            if (data.code === 'NO_EMAIL'){
              setResetPasswErrors({
                email: {
                  name: " ",
                  error: "   .",
                  type: "warning"
                }
              })

            }
            if (data.code === 'INVALID_EMAIL'){
              setResetPasswErrors({
                email: {
                  name: " ",
                  error: "   ,    .",
                  type: "warning"
                }
              })
              
            }
            if (data.code === 'SERVER'){
              setResetPasswErrors({
                email: {
                  name: "",
                  error: "       .",
                  type: "danger"
                }
              })
              
            }
          }
        })
      })
    }

    sendResetPasswEmail();
  }

  const handleResetPasswChange = (e) => {
    e.preventDefault();
    // Set Reset State
    setResetPasswState({
      ...resetPasswState,
      [e.target.id]: e.target.value
    });
    // Reset Errors State
    setResetPasswErrors({});
  }

  const handleResetPasswSubmit = (e) => {
    e.preventDefault();

    const ResetPassword = async() => {
      await fetch('/api/user/resetPassword', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        body: JSON.stringify(resetPasswState)
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            resetPasswReset();
            setLogErrors({
              successReset: {
                name: ' ',
                error: '    !',
                type: 'success'
              }
            });
            setLogin(true);

          } else {
            if (data.code === 'INVALID_REQUEST'){
              setResetPasswErrors({
                Request: {
                  name: "",
                  error: "   ",
                  type: "danger"
                }
              })

            }
            if (data.code === 'MISSING_VALUES'){
              setResetPasswErrors({
                Values: {
                  name: "",
                  error: "   ,    .",
                  type: "warning"
                }
              })
              
            }
            if (data.code === 'NO_USER'){
              setResetPasswErrors({
                email: {
                  name: " ",
                  error: "       ,    .",
                  type: "danger"
                }
              })
              
            }
            if (data.code === 'INVALID_TOKEN'){
              setResetPasswErrors({
                token: {
                  name: "  ",
                  error: "    ,     .",
                  type: "warning"
                }
              })
              
            }
            if (data.code === 'EXPIRED_TOKEN'){
              setResetPasswErrors({
                token: {
                  name: "  ",
                  error: "     ,    .",
                  type: "danger"
                }
              })
            }
            if (data.code === 'USED_TOKEN'){
              setResetPasswErrors({
                token: {
                  name: "  ",
                  error: "     ,   .",
                  type: "danger"
                }
              })
            }
            if (data.code === 'SERVER'){
              setResetPasswErrors({
                error: {
                  name: "",
                  error: "  ,     .",
                  type: "danger"
                }
              })
              
            }
          }
        })
      })

    }

    if (resetPasswState.password.length < 8){
      setResetPasswErrors({
        ...resetPasswErrors,
        password: {
          name: ' ',
          error: '      8   .',
          type: 'warning'
        }
      })
    } 
    else if (resetPasswState.password !== resetPasswState.confirm_password) {
      setResetPasswErrors({
        ...resetPasswErrors,
        password: {
          name: ' ',
          error: " ' '     '  '.",
          type: 'warning'
        }
      })
    } else {
      ResetPassword();
    }
  }

  /* END Reset Password */

  const handleErrorEvent = (e) => {
    e.preventDefault();

    const SendActivationLink = async() => {
      await fetch('/api/activation', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        body: JSON.stringify({email: logState.email, hostname: window.location.origin})
      }).then(resp => {
        if (resp.ok){
          setLogErrors({
            ActivationLink: {
              name: ' ',
              error: '    ',
              type: 'success'
            }
          });
        }
      })
    }

    if (e.target.id === 'ACTIVE_LINK'){
      SendActivationLink();
    }
    if (e.target.id === 'REGISTER'){
      setLogErrors({});
      setLogin(false);
      setRegister(true);
    }
  }

  const loadRegErrors = () => {
    if (Object.keys(regErrors).length !== 0 && regErrors.constructor === Object){
      return (
        <div>
          {regErrors ? Object.entries(regErrors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  const loadLogErrors = () => {
    if (Object.keys(logErrors).length !== 0 && logErrors.constructor === Object){
      return (
        <div>
          {logErrors ? Object.entries(logErrors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error} {e[1].link ? (<Link className="text-primary" to="#" id={e[1].link.id} onClick={handleErrorEvent}>{e[1].link.text}</Link>) : null}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  const loadResetPasswErrors = () => {
    if (Object.keys(resetPasswErrors).length !== 0 && resetPasswErrors.constructor === Object){
      return (
        <div>
          {resetPasswErrors ? Object.entries(resetPasswErrors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error} {e[1].link ? (<Link to="#" id={e[1].link.id} onClick={handleErrorEvent}>{e[1].link.text}</Link>) : null}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  const loadLogoutErrors = () => {
    if (Object.keys(logoutErrors).length !== 0 && logoutErrors.constructor === Object){
      return (
        <div>
          {logoutErrors ? Object.entries(logoutErrors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  // Current Date
  const DateDisplay = () => {
    var date = new Date();
    var days = ["", "", "", "", "", "", ""];
    var delDateString = days[date.getDay()] + ', ' + date.getDate() + '-' + (date.getMonth() + 1) + '-' + date.getFullYear();

    return delDateString;
  }

  const caretDown = () => {
    // Pre-Phone Selector
    var toggler = document.getElementById("pre_phone_selector");
    toggler && toggler.addEventListener("click", function() {
      this.classList.toggle("selector-down");
    });
  }

  const LoggedOutLinksDesktop = (
    <ul className="navbar-nav mobile-lg-h ml-5">

      <li className="nav-item" data-toggle="collapse" data-target=".navbar-collapse.show">
        <Link to="#" onClick={() => setRegister(true)} className="nav-link qsemh-turq" >
          <i title="" className="fa fa-user-plus fa-lg"/>
        </Link>
      </li>

      <li className="nav-item" data-toggle="collapse" data-target=".navbar-collapse.show">
        <Link to="#" onClick={() => setLogin(true)} className="nav-link qsemh-turq" >
          <i title="" className="fa fa-sign-in-alt fa-lg"/>
        </Link>
      </li>

    </ul>
  )

  const LoggedInLinksDesktop = (
    <ul className="mobile-lg-h navbar-nav ml-2">

      <li className="nav-item dropdown" data-toggle="collapse" data-target=".navbar-collapse.show">
        <Link to="#" className="nav-link text-info" id="dropdown1" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          <img title={User.username} src={localStorage.profile_img ? (`https://s3.us-east-2.amazonaws.com/maidancom/${localStorage.profile_img}`) : PH} alt="PI" width="36" height="36" className="rounded-circle"/>
        </Link>
        <div className="dropdown-menu text-right no-shadow" aria-labelledby="dropdown1">
          <Link className="dropdown-item" to="/user/profile">
            <i className="fa fa-user fa-s"/>  
          </Link>
          {
            User.is_provider ? (
              <Link className="dropdown-item" to="/providers/panel">
                <i className="fa fa-tools fa-s"/>  
              </Link>
            ) : null
          }
          {
            User.is_associate ? (
              <Link className="dropdown-item" to="/admin/redeem">
                <i className="fa fa-tools fa-s"/>  
              </Link>
            ) : null
          }
          {
            User.is_admin ? (
              <Link className="dropdown-item" to="/admins/panel">
                <i className="fa fa-tools fa-s"/>  
              </Link>
            ) : null
          }
          {
            User.is_super ? (
              <Link className="dropdown-item" to="/admin/panel">
                <i className="fa fa-tools fa-s"/>  
              </Link>
            ) : null
          }
          {
            User.is_super ? (
              <Link className="dropdown-item" to="/admin/histories">
                <i className="fa fa-history fa-s"/> 
              </Link>
            ) : null
          }
          <div className="dropdown-divider"/>
          <Link className="dropdown-item" to="/user/coupons">
            <i className="fa fa-wallet fa-s"/> 
          </Link>
          <Link className="dropdown-item" to="/user/tickets">
            <i className="fa fa-ticket-alt fa-s"/> 
          </Link>
          <Link className="dropdown-item" to="/balance">
            <i className="fa fa-dollar-sign fa-s"/> 
          </Link>
          <div className="dropdown-divider"/>
          <Link className="dropdown-item" to="#" onClick={() => {setLogout(true)}}>
            <i className="fa fa-sign-out-alt fa-s"/>  
          </Link>
        </div>
      </li>

    </ul>
  )

  const LoggedInLinksSideNav = (
    <li>
      <a className="nav-a qsemh-turq font-weight-bold" href="#accountMenu" data-toggle="collapse" aria-expanded="false">
        {User.username}
        <i className="left fa fa-arrow-down fa-xs"></i>
      </a>
      <ul className="collapse list-unstyled" id="accountMenu">
        <li className="qsemh-turq font-weight-light">
          <Link name="SideLink" className="nav-a dropdown-item" to="/user/profile">
            <i className="fa fa-user fa-s"/>  
          </Link>
        </li>
        <li className="qsemh-turq font-weight-light">
          <Link name="SideLink" className="nav-a dropdown-item" to="/user/notifications">
            <i className="fa fa-bell fa-s"/>  
            {parseInt(localStorage.notifs) > 0 ? (
              <i title={parseInt(localStorage.notifs)} className="fa fa-circle text-danger float-left"/>
            ) : null}
          </Link>
        </li>
        {
          User.is_provider ? (
            <li className="qsemh-turq font-weight-light">
              <Link name="SideLink" className="nav-a dropdown-item" to="/providers/panel">
                <i className="fa fa-tools fa-s"/>  
              </Link>
            </li>
          ) : null
        }
        {
          User.is_associate ? (
            <li className="qsemh-turq font-weight-light">
              <Link name="SideLink" className="nav-a dropdown-item" to="/admin/redeem">
                <i className="fa fa-tools fa-s"/>  
              </Link>
            </li>
          ) : null
        }
        {
          User.is_admin ? (
            <li className="qsemh-turq font-weight-light">
              <Link name="SideLink" className="nav-a dropdown-item" to="/admins/panel">
                <i className="fa fa-tools fa-s"/>  
              </Link>
            </li>
          ) : null
        }
        {
          User.is_super ? (
            <li className="qsemh-turq font-weight-light">
              <Link name="SideLink" className="nav-a dropdown-item" to="/admin/panel">
                <i className="fa fa-tools fa-s"/>  
              </Link>
            </li>
          ) : null
        }
        {
          User.is_super ? (
            <li className="qsemh-turq font-weight-light">
              <Link name="SideLink" className="nav-a dropdown-item" to="/admin/histories">
                <i className="fa fa-history fa-s"/> 
              </Link>
            </li>
          ) : null
        }
        <div className="dropdown-divider"/>
        <li className="qsemh-turq font-weight-light">
          <Link name="SideLink" className="nav-a dropdown-item" to="/user/coupons">
            <i className="fa fa-wallet fa-s"/> 
          </Link>
        </li>
        <li className="qsemh-turq font-weight-light">
          <Link name="SideLink" className="nav-a dropdown-item" to="/user/tickets">
            <i className="fa fa-ticket-alt fa-s"/> 
          </Link>
        </li>
        <li className="qsemh-turq font-weight-light">
          <Link name="SideLink" className="nav-a dropdown-item" to="/balance">
            <i className="fa fa-dollar-sign fa-s"/> 
          </Link>
        </li>
        <div className="dropdown-divider"/>
        <li className="qsemh-turq font-weight-light">
          <Link name="SideLink" className="nav-a dropdown-item" to="#" onClick={() => {setLogout(true)}}>
            <i className="fa fa-sign-out-alt fa-s"/>  
          </Link>
        </li>
        
      </ul>
    </li>
  )

  const LoggedOutLinksSideNav = (
    <ul className="list-unstyled CTAs">
      <li>
        <Link name="SideLink" className="btn btn-sm btn-outline-orange" to="#" onClick={() => setLogin(true)}>
          <i className="fa fa-sign-in-alt fa-lg"/> 
        </Link>
      </li>
      <li>
        <Link name="SideLink" className="btn btn-sm btn-outline-turq" to="#" onClick={() => setRegister(true)}>
          <i className="fa fa-user-plus fa-lg"/> 
        </Link>
      </li>
    </ul>
  )
  
  return (
    <div className="wrapper fixed-top">
      
      {/* Sidebar */}
      <nav id="sidebar">

        <div className="sidebar-header text-center">
          <Link name="SideLink" to="/">
            <img className="navbar-brand-side" src={LogoD} alt="brand"/>
          </Link>
        </div>

        <hr/>

        <div className="text-center">
          <b className="font-weight-bold">{DateDisplay()}</b>
        </div>
        
        <hr/>

        <ul className="text-right list-unstyled components">
          <li className="qsemh-orange">
            <Link className="nav-a" name="SideLink" to="/"></Link>
          </li>
          <li className="qsemh-turq">
            <Link className="nav-a" name="SideLink" to="/ads"> </Link>
          </li>
          <li className="qsemh-turq">
            <Link className="nav-a" name="SideLink" to="/coupons/offers"> </Link>
          </li>
          <li className="qsemh-turq">
            <Link className="nav-a" name="SideLink" to="/terms"> </Link>
          </li>
          {localStorage.maidan_jwt_$m3 ? LoggedInLinksSideNav : null}
        </ul>

        {localStorage.maidan_jwt_$m3 ? null : LoggedOutLinksSideNav}

      </nav>
      {/* Sidebar */}

      {/* Navbar Content */}
      <div id="content">

        <nav className="navbar shadow navbar-expand-lg rounded navbar-light">
          
          <div className="container-fluid">

            <div className="float-right">
              <button type="button" id="sidebarCollapse" className="btn btn-link">
                <i className="fas fa-align-justify fa-2x"></i>
              </button>
              <Link className="mobile-h" to="/">
                <img className="navbar-brand ml-auto" src={LogoD} alt="brand"/>
              </Link>
              <Link className="desktop-h" to="/">
                <img className="navbar-brand" src={LogoD} alt="brand"/>
              </Link>
            </div>
            {/* {localStorage.maidan_jwt_$m3 ? LoggedInLinksMobile : LoggedOutLinksMobile} */}

            <div className="collapse navbar-collapse text-right" id="navbarSupportedContent">

              <ul className="mobile-lg-h navbar-nav ml-md-auto mr-md-4">
                <li className="nav-item" data-toggle="collapse" data-target=".navbar-collapse.show">
                  <Link to="/ads" className="nav-link qsemh-turq nav-text"> </Link>
                </li>

                <li className="nav-item" data-toggle="collapse" data-target=".navbar-collapse.show">
                  <Link to="/coupons/offers" className="nav-link qsemh-turq nav-text"> </Link>
                </li>

                <li className="nav-item" data-toggle="collapse" data-target=".navbar-collapse.show">
                  <Link to="/terms" className="nav-link nav-text qsemh-turq"> </Link>
                </li>
              </ul>

              <ul className="mobile-lg-h navbar-nav mr-md-2">
                <li className="nav-item" data-toggle="collapse" data-target=".navbar-collapse.show">
                  <b className="nav-text">{DateDisplay()}</b>
                </li>
              </ul>

              {
                Object.keys(User).length !== 0 && User.constructor === Object ? (
                  <ul className="mobile-lg-h navbar-nav">
                    <li className="nav-item" data-toggle="collapse" data-target=".navbar-collapse.show">
                      <Link to="/user/notifications" className="nav-link nav-text qsemh-turq">
                        <i title={` (${parseInt(localStorage.notifs)})`} className="far fa-bell fa-lg"/>
                        {parseInt(localStorage.notifs) > 0 ? (
                          <span title={parseInt(localStorage.notifs)} className="fa fa-circle text-danger"/>
                        ) : null}
                      </Link>
                    </li>
                  </ul>
                ) : null
              }

              {localStorage.maidan_jwt_$m3 ? LoggedInLinksDesktop : LoggedOutLinksDesktop}
            
            </div>

          </div>

        </nav>
      
      </div>
      {/* Navbar Content */}

      {/* Overlay */}
      <div className="overlay-nav"></div>
      {/* Overlay */}

            
      {/* Register Modal */}
      <Modal show={registerModal} onHide={resetRegister}>
        
        <Modal.Header>
          <Modal.Title>  </Modal.Title>
          <Link to="#" onClick={resetRegister}>
            <i className="fa fa-times text-warning"/>
          </Link>
        </Modal.Header>
        
        <Modal.Body>
          <form onSubmit={handleRegSubmit}>
            
            <p className="text-muted font-info text-right">
              <i className="fa fa-star-of-life fa-xs"/>     .
            </p>
            
            <div className="mt-2">
              {loadRegErrors()}
            </div>
            
            <div className="row">

              <div className="col-sm-12 text-right">
                <label htmlFor="email" className="ml-sm-4 font-weight-bold">  : </label>
                <div className="input-group">
                  <input onChange={handleRegChange} dir='ltr' required id="email" type="email" placeholder="example@gmail.com" className={"form-control col-sm-8 " + (regErrors.email ? "is-invalid" : '')}/>
                </div>
              </div>

              <div className="col-sm-12 mt-2 text-right">
                <label htmlFor="username" className="ml-sm-4 font-weight-bold">  : </label>
                <div className="input-group">
                  <input onChange={handleRegChange} required id="username" type="text" placeholder=" " className={"form-control col-sm-8 " + (regErrors.username ? "is-invalid" : '')}/>
                </div>
              </div>

              <div className="col-sm-12 mt-2 text-right">
                <label htmlFor="phone" className="ml-sm-4 font-weight-bold">  : </label>
                <div className="input-group">

                  <input onChange={handleRegChange} dir='ltr' required id="phone" type="text" placeholder="5xxxxxxxx" className={"form-control col-sm-6 " + (regErrors.phone ? "is-invalid" : '')}/>
                  
                  <div className="btn-group">
                    <Link onClick={caretDown} dir='ltr' id="pre_phone_selector" className={"form-control btn-white selector" + (regErrors.pre_phone ? 'is-invalid' : '')} data-toggle="dropdown" to="#"></Link>
                    <ul className="dropdown-menu font-weight-bold">
                      {
                        basic.map((data, index) => {
                          if (data.key === 'country'){
                            return(
                              <li key={index}>
                                <Link to="#" className="text-dark ml-2" id="pre_phone" name={`+${data.values.telecode}`} title={data.values.country.ar} onClick={handleRegChange}>
                                  {getCountryCallingCode(data.label)}+ <img src={`http://catamphetamine.gitlab.io/country-flag-icons/3x2/${data.label}.svg`} width="24" alt={data.label}/>
                                </Link>
                              </li>
                            )
                          }
                        })
                      }
                    </ul>
                  </div>

                </div>
              </div>

              <div className="col-sm-12 mt-2 text-right">
                <label htmlFor="password" className="ml-sm-4 font-weight-bold">  : </label>
                <div className="input-group">
                  <input onChange={handleRegChange} required id="password" type="password" placeholder=" " className={"form-control col-sm-8 " + (regErrors.password ? "is-invalid" : '')}/>
                </div>
              </div>

              <div className="col-sm-12 mt-2 text-right">
                <label htmlFor="confirm_password" className="ml-sm-4 font-weight-bold">   : </label>
                <div className="input-group">
                  <input onChange={handleRegChange} required id="confirm_password" type="password" placeholder="  " className={"form-control col-sm-8 " + (regErrors.password ? "is-invalid" : '')}/>
                </div>
              </div>

              <div className="col-sm-12 form-check mt-4 text-right">
                <input required type="checkbox" className="form-check-input"/>
                <label htmlFor="confirm" className="form-check-label font-weight-bold mr-4">
                    <Link to="#" className="text-primary" onClick={() => window.open("/terms")}> </Link> 
                </label>
              </div>
            
            </div>

            <Modal.Footer className="mt-4">
              <Button variant="grey" onClick={resetRegister}>
                
              </Button>
              <Button variant="turq" type="submit">
                
              </Button>
            </Modal.Footer>
             
          </form>
        </Modal.Body>   
        
      </Modal>
    
      {/* Login Modal */}
      <Modal show={loginModal} onHide={resetLogin}>
        
        <Modal.Header>
          <Modal.Title> </Modal.Title>
          <Link to="#" onClick={resetLogin}>
            <i className="fa fa-times text-warning"/>
          </Link>
        </Modal.Header>

        <Modal.Body>
          <form onSubmit={handleLogSubmit}>
            
            <div className="mt-2">
              {loadLogErrors()}
            </div>
            
            <div className="row">
              
              <div className="col-sm-12 text-right">
                <label htmlFor="email" className="ml-sm-4 font-weight-bold">  : </label>
                <div className="input-group">
                  <input onChange={handleLogChange} required id="email" type="email" placeholder=" " className={"form-control col-sm-8 " + (logErrors.email ? 'is-invalid' : '')}/>
                </div>
              </div>

              <div className="col-sm-12 mt-2 text-right">
                <label htmlFor="password" className="ml-sm-4 font-weight-bold">  : </label>
                <div className="input-group">
                  <input onChange={handleLogChange} required id="password" type="password" placeholder=" " className={"form-control col-sm-8 " + (logErrors.password ? 'is-invalid' : '')}/>
                </div>
              </div>

              <p className="mt-1 mr-4 font-weight-light">
                <Link to="#" onClick={handleResetPasswCall} className="text-primary">  </Link>
              </p>

            </div>

            <Modal.Footer className="mt-4">

              <Button variant="grey" onClick={resetLogin}>
                
              </Button>
              <Button variant="orange" type="submit">
                 
              </Button>

            </Modal.Footer>
          
          </form>
        </Modal.Body>

      </Modal>
      
      {/* Reset Password Modal */}
      <Modal show={resetPasswModal} onHide={resetPasswReset}>
        
        <Modal.Header>
          <Modal.Title>   </Modal.Title>
          <Link to="#" onClick={resetPasswReset}>
            <i className="fa fa-times text-warning"/>
          </Link>
        </Modal.Header>

        <Modal.Body>
          <form onSubmit={handleResetPasswSubmit}>
            
            <div className="mt-2">
              {loadResetPasswErrors()}
            </div>
            
            <div className="row">

              <div className="col-sm-12 text-right">
                <label htmlFor="email" className="ml-sm-4 font-weight-bold">  : </label>
                <div className="input-group">
                  <input disabled={resetPasswState.submitted} onChange={handleResetPasswChange} required id="email" type="email" placeholder=" " className={"form-control col-sm-8 " + (resetPasswErrors.email ? 'is-invalid' : '')}/>
                </div>
                <p className="text-muted mr-sm-4 font-info">
                         .
                </p>
                <div className="text-right">
                    <button disabled={resetPasswState.submitted} onClick={sendResetEmail} className="btn btn-sm btn-outline-dark"></button>
                  </div>
              </div>

              {
                resetPasswState.submitted ? (
                    <div className="col-sm-12 mt-3 text-right">
                      <label htmlFor="token" className="ml-sm-4 font-weight-bold">*    : </label>
                      <div className="input-group">
                        <input onChange={handleResetPasswChange} required id="token" type="text" placeholder="  " className={"form-control col-sm-8 " + (resetPasswErrors.token ? 'is-invalid' : '')}/>
                      </div>
                    </div>
                ) : null
              }
              {
                resetPasswState.submitted ? (
                  <div className="col-sm-12 mt-2 text-right">
                    <label htmlFor="password" className="ml-sm-4 font-weight-bold">   : </label>
                    <div className="input-group">
                      <input onChange={handleResetPasswChange} required id="password" type="password" placeholder=" " className={"form-control col-sm-8 " + (resetPasswErrors.password ? 'is-invalid' : '')}/>
                    </div>
                  </div>
                ) : null
              }
              {
                resetPasswState.submitted ? (
                  <div className="col-sm-12 mt-2 text-right">
                    <label htmlFor="confirm_password" className="ml-sm-4 font-weight-bold">    : </label>
                    <div className="input-group">
                      <input onChange={handleResetPasswChange} required id="confirm_password" type="password" placeholder="  " className={"form-control col-sm-8 " + (resetPasswErrors.password ? 'is-invalid' : '')}/>
                    </div>
                  </div>
                ) : null
              }

            </div>

            <Modal.Footer className="mt-4">

              <Button variant="grey" onClick={resetPasswReset}>
                
              </Button>
              <Button disabled={resetPasswState.token ? false : true} variant="primary" type="submit">
                 
              </Button>

            </Modal.Footer>
          
          </form>
        </Modal.Body>

      </Modal>
      
      {/* Logout Modal */}
      <Modal show={logoutModal} onHide={() => {setLogout(false)}}>
        <Modal.Header>
          <Modal.Title> </Modal.Title>
          <Link to="#" onClick={() => setLogout(false)}>
            <i className="fa fa-times text-warning"/>
          </Link>
        </Modal.Header>
        <Modal.Body className="text-right">
          <div className="mt-2">
            {loadLogoutErrors()}
          </div>
          <span className="display-8 text-dark mt-4">    </span>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="grey" onClick={() => {setLogout(false); setLogoutErrors({})}}>
            
          </Button>
          <Button variant="danger" onClick={handleLogout}>
             
          </Button>
        </Modal.Footer>
      </Modal>

      {/* Successful Registeration */}
      <Modal show={successReg} onHide={() => {setSuccessReg(false)}}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">  !</Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">
          <i className="fa fa-check-circle text-success fa-3x"/>
          <p className="mt-2 lead text-muted">
                  , 
                  .
          </p>
        </Modal.Body>
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="outline-success" onClick={() => {setSuccessReg(false)}}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>


    </div>
  )
}


export default withRouter(Navbar)