import React, {useRef, useEffect, useState} from 'react';
 


const PayPalBtn = ({user_id, amount, fullname}) => {

  const paypalRef = useRef(null);
  const [progress, setProgress] = useState(false);
  const [errors, setErrors] = useState({});

  /*eslint-disable */
  //suppress all warnings between comments
  useEffect(() => {
    // Load Paypal Buttons

    const HandleSuccessPayment = (order_id) => {

      const reloadPage = () => {
        window.location.reload()
      }
      
      const AddBalance = async() => {
        await fetch(`/api/user/balance/${user_id}`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json',
          },
          body: JSON.stringify({
            fullname: fullname,
            amount: amount,
            method: 'paypal',
            verified: true,
            order_id: order_id
          })
        }).then(resp => {
          resp.json().then(data => {
            if (resp.ok){
              reloadPage();
            } else {
              if (data.code === 'NO_MATCH_ID'){
                setErrors({
                  ID: {
                    name: '',
                    error: '  ,      .',
                    type: 'danger'
                  }
                })
              }
              if (data.code === 'INVALID_REQUEST'){
                setErrors({
                  Request: {
                    name: '',
                    error: '  ,           .',
                    type: 'danger'
                  }
                })
              }
              if (data.code === 'MISSING_VALUES'){
                setErrors({
                  Values: {
                    name: '',
                    error: '   ,           .',
                    type: 'danger'
                  }
                })
              }
              if (data.code === 'INVALID_METHOD'){
                setErrors({
                  Method: {
                    name: ' ',
                    error: '      ,           .',
                    type: 'danger'
                  }
                })
              }
              if (data.code === 'SERVER'){
                setErrors({
                  Server: {
                    name: '',
                    error: '      ,    .',
                    type: 'danger'
                  }
                })
              }
              setTimeout(reloadPage, 7500);
            }
          })
        })
      }

      AddBalance()
    }

    const renderBtns = () => {
      window.paypal
        .Buttons({
          createOrder: (data, actions) => {
            return actions.order.create({
              purchase_units: [
                {
                  description: 'Qsemh Service Fees',
                  amount: {
                    currency_code: 'USD',
                    value: parseFloat(amount),
                  },
                }
              ],
            });
          },
          onApprove: async (data, actions) => {
            setProgress(true);
            document.getElementById('PaypalBtns').hidden = true;
            const order = await actions.order.capture();

            if (order.status === 'COMPLETED'){
              HandleSuccessPayment(order.id);
            } else {
              console.log(order.status);
              setErrors({
                Payment: {
                  name: '',
                  error: '    ,    ,      ,    .',
                  type: 'danger'
                }
              })
            }
            
          },
          onError: e => {
            console.log(e);
            setErrors({
              PaymentBtns: {
                name: ' ',
                error: '     ,   .    ,    .',
                type: 'warning'
              }
            })
          },
          onCancel: e => {
            console.log('Cancel Payment ', e)
          },
          onClose: _ => {
            console.log('Close Paypal.')
          }
        }).render(paypalRef.current);
    }
    
    if (window.paypal) {
      renderBtns();
    }
  }, [])
  /*eslint-enable */
  
  const loadErrors = () => {
    if (Object.keys(errors).length !== 0 && errors.constructor === Object){
      return (
        <div>
          {errors ? Object.entries(errors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  return(
    <div className="mt-4">

      <div className="containet mt-2">
        {loadErrors()}
      </div>

      <div id="PaypalBtns" className="mt-2" ref={paypalRef}/>

      <p hidden={!progress} className="font-weight-bold">    ,  .</p>
      <p hidden={!progress} className="font-weight-boldtext-info font-info">    ,    </p>
    </div>
  )
}


export default PayPalBtn