import React, { useState, useEffect } from 'react';

// Modals
import { Modal, Button } from 'react-bootstrap';
import { Link, Redirect } from 'react-router-dom';



const ElementsTable = () => {

  const [elements, setElements] = useState({elements: null, limit: 50});
  

  const [errors, setErrors] = useState({});
  const [redirect, setRedirect] = useState({redirect: false, flash: null})

  // Search Elements
  const [search, setSearch] = useState();

  // Edit/Add Modals
  const [isCountry, setCountryModal] = useState(false);  
  const [isNew, setNewModal] = useState(false);
  const [countryState, setCountryState] = useState({});


  const [isTypes, setTypesModal] = useState(false);
  const [typeState, setTypeState] = useState({});


  useEffect(() => {
    fetch('/api/panel/labels', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setElements({
            elements: data.labels,
            limit: 50
          })
        } else {
          if ([
            'INVALID_AUTH_HEADER', 
            'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
            'ACCESS_DENIED', 'AUTH_ERROR'
          ].includes(data.code)) {
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
          if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
            localStorage.clear();
            sessionStorage.clear();
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
        }
      })
    })
  }, [])


  const handleSettings = (elem) => {
    
    if (elem.key === 'country'){
      setCountryModal(true);
      setCountryState({
        id: elem.id,
        label: elem.label,
        name: elem.name,
        country_ar: elem.values.country.ar,
        country_en: elem.values.country.en,
        cities_ar: elem.values.cities.ar,
        cities_en: elem.values.cities.en,
        telecode: elem.values.telecode,
        description: elem.description
      });
    }
    else if (elem.key === 'coupon_types'){
      setTypesModal(true);
      setTypeState({
        id: elem.id,
        label: elem.label,
        name: elem.name,
        key: elem.key,
        description: elem.description,
        values_ar: elem.values.ar,
        values_en: elem.values.en,
      });
    }

    
  }

  const SearchElements = async(e) => {
    setSearch({
      ...search,
      [e.target.id]: e.target.value
    })
    await fetch('/api/panel/labels', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      },
      body: JSON.stringify({...search, [e.target.id]: e.target.value})
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setElements({
            ...elements,
            elements: data.labels
          })
        } else {
          if ([
            'INVALID_AUTH_HEADER', 
            'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
            'ACCESS_DENIED', 'AUTH_ERROR'
          ].includes(data.code)) {
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
          if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
            localStorage.clear();
            sessionStorage.clear();
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
        }
      })
    })
  }

  const LoadElements = () => {

    const elements_patch = elements.elements && elements.elements.slice(0, elements.limit);

    return(
        <tbody>
          <tr>
            <th className="table-header font-weight-bold">
              #
            </th>

            <td className="table-body">
              <input onChange={SearchElements} id="name" type="text" placeholder=" : " className="form-contro"/>
            </td>

            <td className="table-body"/>

            <td className="table-body">
              <input onChange={SearchElements} id="label" type="text" placeholder=" () : " className="form-contro"/>
            </td>

            <td className="table-body"/>
          </tr>
          {
            elements_patch ? elements_patch.map((elem, index) => {
              
              return(
                <tr className="" key={index}>
                  <th className="table-header">{index+1}</th>
                  <td className="table-body">{elem.name}</td>
                  <td className="table-body">{elem.description}</td>
                  <td className="table-body">{elem.label}</td>
                  <td className="table-body">
                    <button onClick={() => {handleSettings(elem)}} className="btn btn-sm btn-outline-grey font-weight-bold"></button>
                  </td>
                </tr>
              )
            }) : null
          }
        </tbody>
    )
  }

  const AddLimit = () => {
    if (elements.limit < elements.elements.length){
      let new_limit = elements.limit + 50;
      setElements({...elements, limit: new_limit})
    }
  }

  const ReduceLimit = () => {
    if (elements.limit > 50){
      let new_limit = elements.limit - 50;
      setElements({...elements, limit: new_limit})
    }
  }

  const resetModals = () => {
    // Reset All Modals & Element State
    setCountryState({});
    setTypeState({});
    setErrors({});

    setCountryModal(false);
    setNewModal(false);
    setTypesModal(false);
  }

  const loadErrors = () => {
    if (Object.keys(errors).length !== 0 && errors.constructor === Object){
      return (
        <div>
          {errors ? Object.entries(errors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  /* Modals Handlers */
  const handleNewCountrySet = (e) => {
    e.preventDefault();

    setCountryState({
      cities_ar: [],
      cities_en: [],
    });

    setNewModal(true);
  }

  const handleEditCountrySubmit = (e) => {
    e.preventDefault();

    const UpdateCountry = async() => {
      await fetch(`/api/panel/country/${countryState.id}`, {
        method: 'PATCH',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
        },
        body: JSON.stringify(countryState)
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            setElements({
              ...elements,
              elements: data.labels,
            });
            // Reset Modals
            resetModals();
          } else {
            if ([
              'INVALID_AUTH_HEADER', 
              'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
              'ACCESS_DENIED', 'AUTH_ERROR'
            ].includes(data.code)) {
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      .'
                }
              })
            }
            if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
              localStorage.clear();
              sessionStorage.clear();
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      .'
                }
              })
            }
          }
        })
      })
    }

    if (countryState.label && countryState.label.length > 2){
      setErrors({
        label: {
          name: ' ',
          error: '       (SA, AE, KW, BH),    .',
          type: 'danger'
        }
      })
    } else {
      UpdateCountry();
    }

  }

  const handleNewCountrySubmit = (e) => {
    e.preventDefault();

    const NewCountry = async() => {
      await fetch('/api/panel/country', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
        },
        body: JSON.stringify(countryState)
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            setElements({
              ...elements,
              elements: data.labels,
            });
            // Reset Modals
            resetModals();
          } else {
            if ([
              'INVALID_AUTH_HEADER', 
              'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
              'ACCESS_DENIED', 'AUTH_ERROR'
            ].includes(data.code)) {
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      .'
                }
              })
            }
            if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
              localStorage.clear();
              sessionStorage.clear();
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      .'
                }
              })
            }
          }
        })
      })
    }

    if (countryState.label && countryState.label.length > 2){
      setErrors({
        label: {
          name: ' ',
          error: '       (SA, AE, KW, BH),    .',
          type: 'danger'
        }
      })
    } else {
      NewCountry();
    }
  }

  const handleEditCouponTypeSubmit = (e) => {
    e.preventDefault();

    const UpdateCountry = async() => {
      await fetch(`/api/panel/coupons/${typeState.id}`, {
        method: 'PATCH',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
        },
        body: JSON.stringify(typeState)
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            setElements({
              ...elements,
              elements: data.labels,
            });
            // Reset Modals
            resetModals();
          } else {
            if ([
              'INVALID_AUTH_HEADER', 
              'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
              'ACCESS_DENIED', 'AUTH_ERROR'
            ].includes(data.code)) {
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      .'
                }
              })
            }
            if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
              localStorage.clear();
              sessionStorage.clear();
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      .'
                }
              })
            }
          }
        })
      })
    }

    UpdateCountry();
  }

  // Type Country

  const handleCountryChange = (e) => {
    e.preventDefault();

    // Clear Errors
    setErrors({});

    setCountryState({
      ...countryState,
      [e.target.id]: e.target.value
    })

  }

  const handleCitiesChange = (e) => {
    e.preventDefault();
    var key = e.target.id.split('-')[0];
    var id = e.target.id.split('-')[1];

    var citiesAr = [...countryState.cities_ar];
    var citiesEn = [...countryState.cities_en];

    if (key === "cities_ar") {
      citiesAr[id] = e.target.value;

    } else if (key === "cities_en") {
      citiesEn[id] = e.target.value;
    }

    setCountryState({
      ...countryState,
      cities_ar: citiesAr,
      cities_en: citiesEn
    });

  }

  const handleNewCity = (e) => {
    e.preventDefault();

    var citiesAr = [...countryState.cities_ar];
    var citiesEn = [...countryState.cities_en];

    citiesAr.push("");
    citiesEn.push("");

    setCountryState({
      ...countryState,
      cities_ar: citiesAr,
      cities_en: citiesEn
    });

  }

  const handleDeleteCity = (id) => {

    var citiesAr = [...countryState.cities_ar];
    var citiesEn = [...countryState.cities_en];

    citiesAr.splice(id, 1);
    citiesEn.splice(id, 1);

    // Change values of Elements in cities table
    for (let i = 0; i < citiesAr.length; i++) {
      document.getElementById(`cities_ar-${i}`).value = citiesAr[i];
      document.getElementById(`cities_en-${i}`).value = citiesEn[i];
    }

    setCountryState({
      ...countryState,
      cities_ar: citiesAr,
      cities_en: citiesEn
    });
  }

  // Type CouponTypes

  const handleCouponTypesChange = (e) => {
    e.preventDefault();

    setTypeState({
      ...typeState,
      [e.target.id]: e.target.value
    })
  }

  const handleTypesChange = (e) => {
    e.preventDefault();
    var key = e.target.id.split('-')[0];
    var id = e.target.id.split('-')[1];

    var valuesAr = [...typeState.values_ar];
    var valuesEn = [...typeState.values_en];

    if (key === "values_ar") {
      valuesAr[id] = e.target.value;

    } else if (key === "values_en") {
      valuesEn[id] = e.target.value;
    }

    setTypeState({
      ...typeState,
      values_ar: valuesAr,
      values_en: valuesEn
    });

  }

  const handleNewType = (e) => {
    e.preventDefault();

    var valuesAr = [...typeState.values_ar];
    var valuesEn = [...typeState.values_en];

    valuesAr.push("");
    valuesEn.push("");

    setTypeState({
      ...typeState,
      values_ar: valuesAr,
      values_en: valuesEn
    });

  }

  const handleDeleteType = (id) => {

    var valuesAr = [...typeState.values_ar];
    var valuesEn = [...typeState.values_en];

    valuesAr.splice(id, 1);
    valuesEn.splice(id, 1);

    // Change values of Elements in Types table
    for (let i = 0; i < valuesAr.length; i++) {
      document.getElementById(`values_ar-${i}`).value = valuesAr[i];
      document.getElementById(`values_en-${i}`).value = valuesEn[i];
    }

    setTypeState({
      ...typeState,
      values_ar: valuesAr,
      values_en: valuesEn
    });
  }
  
  
  /* Modals Handlers */

  if (redirect.redirect){
    return <Redirect to={{
      pathname: '/',
      state: {flash: redirect.flash}
    }}/>
  }

  return(
    <div className="table-responsive mt-4">
      
      <table className="table table-sm table-bordered">
        <thead>
          <tr className="bg-light-grey">
            <th className="font-weight-bold" scope="col">ID</th>
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col"> ()</th>
            <th className="font-weight-bold" scope="col"></th>
          </tr>
        </thead>
          {LoadElements()}
      </table>
      <div className="justify-content-center">
        {(elements.elements && elements.limit < elements.elements.length) ? (<button onClick={AddLimit} className="btn btn-sm btn-outline-primary"> </button>) : null}
        {elements.limit > 50 ? (<button onClick={ReduceLimit} className="btn btn-sm btn-outline-secondary"> </button>) : null}
        <div className="float-right">
          <button onClick={handleNewCountrySet} className="btn btn-sm btn-outline-success font-weight-bold">
            <i className="fa fa-plus"/>  
          </button>
        </div>
      </div>
      
      {/* New Element Modal */}
      <Modal show={isNew} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8"> () </Modal.Title>
        </Modal.Header>
        
        <Modal.Body className="text-center mt-4">
          {loadErrors()}

          <form id="CountryForm" onSubmit={handleNewCountrySubmit}>

            <div className="container">

              <div className="row text-right">

                <div className="col-sm-12 mt-2">

                  <div className="row">

                    <div className="col-sm-6">
                      <label htmlFor="label" className="font-weight-bold">*  () : </label>
                      <div className="input-group">
                        <input onChange={handleCountryChange} required id="label" type="text" placeholder=" (  : SA)" className={"form-control col-sm-12 " + (errors['label'] ? 'is-invalid' : '')}/>
                      </div>
                    </div>

                    <div className="col-sm-6">
                      <label htmlFor="telecode" className="font-weight-bold">*   (3 ) : </label>
                      <div className="input-group">
                        <input onChange={handleCountryChange} required id="telecode" inputMode="tel" type="text" placeholder="  : 966" className="form-control col-sm-12"/>
                      </div>
                    </div>
                  
                  </div>

                </div>

                <div className="col-sm-12 mt-3">

                  <div className="row">

                    <div className="col-sm-6">
                      <label htmlFor="country_ar" className="font-weight-bold">*   (AR) : </label>
                      <div className="input-group">
                        <input onChange={handleCountryChange} required id="country_ar" type="text" placeholder=" " className="form-control col-sm-12"/>
                      </div>
                    </div>

                    <div className="col-sm-6">
                      <label htmlFor="country_en" className="font-weight-bold">*   (EN) : </label>
                      <div className="input-group">
                        <input onChange={handleCountryChange} required id="country_en" dir='ltr' type="text" placeholder="Country Name" className="form-control col-sm-12"/>
                      </div>
                    </div>

                  </div>

                </div>

                <div className="col-sm-12 mt-3">
                  <label htmlFor="description" className="font-weight-bold"> (64 ) : </label>
                  <div className="input-group">
                    <textarea onChange={handleCountryChange} id="description" type="text" placeholder=" " className="form-control col-sm-12"/>
                  </div>
                </div>

                <div className="col-sm-12 mt-4">
                  <label htmlFor="cities" className="font-weight-bold">*   : </label>
                  <table className="table table-sm table-bordered">
                    <thead>
                      <tr className="bg-white">
                        <th className="font-weight-bold" scope="col">#</th>
                        <th className="font-weight-bold" scope="col">  (AR)</th>
                        <th className="font-weight-bold" scope="col">  (EN)</th>
                        <th className="font-weight-bold" scope="col"></th>
                      </tr>
                    </thead>
                    <tbody className="text-center">
                      {
                        countryState.cities_ar && countryState.cities_ar.map((_, index) => (
                          <tr key={index}>
                            <th className="table-header">{index+1}</th>
                            <td className="table-header">
                              <input id={`cities_ar-${index}`} onChange={handleCitiesChange} required placeholder=" " defaultValue={countryState.cities_ar[index]} type="text" className="col-sm-8"/>
                            </td>
                            <td className="table-header">
                              <input id={`cities_en-${index}`} onChange={handleCitiesChange} dir='ltr' required placeholder="City name" defaultValue={countryState.cities_en[index]} type="text" className="col-sm-8"/>
                            </td>
                            <td>
                              <Link onClick={() => {handleDeleteCity(index)}} to="#">
                                <i className="fa fa-times text-danger fa-2x"/>
                              </Link>
                            </td>
                          </tr>
                        ))
                      }
                    </tbody>
                      
                  </table>
                  <div className="float-right">
                    <button onClick={handleNewCity} className="btn btn-sm btn-outline-primary font-weight-bold">
                      <i className="fa fa-plus"/>  
                    </button>
                  </div>
                </div>

              </div>

            </div>

          </form>
          
        </Modal.Body>
        
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="outline-grey" onClick={resetModals}>
              
            </Button>
            <Button variant="outline-success" form="CountryForm" type="submit">
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>

      {/* Edit Country Modal */}
      <Modal show={isCountry} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">  ({countryState.name})</Modal.Title>
        </Modal.Header>
        
        <Modal.Body className="text-center mt-4">
          {loadErrors()}

          <form id="CountryForm" onSubmit={handleEditCountrySubmit}>

            <div className="container">

              <div className="row text-right">

                <div className="col-sm-12 mt-2">
                  
                  <div className="row">
                    <div className="col-sm-6">
                      <label htmlFor="label" className="font-weight-bold">*  () : </label>
                      <div className="input-group">
                        <input onChange={handleCountryChange} required id="label" type="text" placeholder=" (  : SA)" defaultValue={countryState.label} className="form-control col-sm-12"/>
                      </div>
                    </div>

                    <div className="col-sm-6">
                      <label htmlFor="telecode" className="font-weight-bold">*   (3 ) : </label>
                      <div className="input-group">
                        <input onChange={handleCountryChange} required id="telecode" dir='ltr' inputMode="tel" type="text" placeholder=" " defaultValue={countryState.telecode} className="form-control col-sm-12"/>
                      </div>
                    </div>

                  </div>

                </div>

                <div className="col-sm-12 mt-3">

                  <div className="row">
                    <div className="col-sm-6">
                      <label htmlFor="country_ar" className="font-weight-bold">*   (AR) : </label>
                      <div className="input-group">
                        <input onChange={handleCountryChange} required id="country_ar" type="text" placeholder=" " defaultValue={countryState.country_ar} className="form-control col-sm-12"/>
                      </div>
                    </div>

                    <div className="col-sm-6">
                      <label htmlFor="country_en" className="font-weight-bold">*   (EN) : </label>
                      <div className="input-group">
                        <input onChange={handleCountryChange} required id="country_en" dir='ltr' type="text" placeholder="Country Name" defaultValue={countryState.country_en} className="form-control col-sm-12"/>
                      </div>
                    </div>

                  </div>

                </div>

                <div className="col-sm-12 mt-3">
                  <label htmlFor="description" className="font-weight-bold"> (64 ) : </label>
                  <div className="input-group">
                    <textarea onChange={handleCountryChange} id="description" type="text" placeholder=" " defaultValue={countryState.description} className="form-control col-sm-12"/>
                  </div>
                </div>

                <div className="col-sm-12 mt-4">
                  <label htmlFor="cities" className="font-weight-bold">*   : </label>
                  <table className="table table-sm table-bordered">
                    <thead>
                      <tr className="bg-white">
                        <th className="font-weight-bold" scope="col">#</th>
                        <th className="font-weight-bold" scope="col">  (AR)</th>
                        <th className="font-weight-bold" scope="col">  (EN)</th>
                        <th className="font-weight-bold" scope="col"></th>
                      </tr>
                    </thead>
                    <tbody className="text-center">
                        {
                          countryState.cities_ar && countryState.cities_ar.map((_, index) => (
                            <tr key={index}>
                              <th className="table-header">{index+1}</th>
                              <td className="table-header">
                                <input id={`cities_ar-${index}`} onChange={handleCitiesChange} required placeholder=" " defaultValue={countryState.cities_ar[index]} type="text" className="col-sm-8"/>
                              </td>
                              <td className="table-header">
                                <input id={`cities_en-${index}`} onChange={handleCitiesChange} dir='ltr' required placeholder="City name" defaultValue={countryState.cities_en[index]} type="text" className="col-sm-8"/>
                              </td>
                              <td>
                                <Link onClick={() => {handleDeleteCity(index)}} to="#">
                                  <i className="fa fa-times text-danger fa-2x"/>
                                </Link>
                              </td>
                            </tr>
                          ))
                        }
                    </tbody>
                      
                  </table>
                  <div className="float-right">
                    <button onClick={handleNewCity} className="btn btn-sm btn-outline-primary font-weight-bold">
                      <i className="fa fa-plus"/>  
                    </button>
                  </div>
                </div>

              </div>

            </div>

          </form>
          
        </Modal.Body>
        
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="outline-grey" onClick={resetModals}>
              
            </Button>
            <Button variant="outline-success" form="CountryForm" type="submit">
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>

      {/* Edit GroupTypes Modal */}
      <Modal show={isTypes} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">  ({typeState.name})</Modal.Title>
        </Modal.Header>
        
        <Modal.Body className="text-center mt-4">
          {loadErrors()}

          <form id="GroupTypesForm" onSubmit={handleEditCouponTypeSubmit}>

            <div className="container">

              <div className="row text-right">

                <div className="col-sm-12 mt-3">
                  <label htmlFor="description" className="font-weight-bold"> (64 ) : </label>
                  <div className="input-group">
                    <textarea onChange={handleCouponTypesChange} id="description" type="text" placeholder=" " defaultValue={typeState.description} className="form-control col-sm-12"/>
                  </div>
                </div>
                
                <div className="col-sm-12 mt-4">
                  <label htmlFor="values" className="font-weight-bold">*    : </label>
                  <table className="table table-sm table-bordered">
                    <thead>
                      <tr className="bg-white">
                        <th className="font-weight-bold" scope="col">#</th>
                        <th className="font-weight-bold" scope="col">  (AR)</th>
                        <th className="font-weight-bold" scope="col">  (EN)</th>
                        <th className="font-weight-bold" scope="col"></th>
                      </tr>
                    </thead>
                    <tbody className="text-center">
                        {
                          typeState.values_ar && typeState.values_ar.map((_, index) => (
                            <tr key={index}>
                              <th className="table-header">{index+1}</th>
                              <td className="table-header">
                                <input id={`values_ar-${index}`} onChange={handleTypesChange} required placeholder=" " defaultValue={typeState.values_ar[index]} type="text" className="col-sm-8"/>
                              </td>
                              <td className="table-header">
                                <input id={`values_en-${index}`} onChange={handleTypesChange} dir='ltr' required placeholder="Type name" defaultValue={typeState.values_en[index]} type="text" className="col-sm-8"/>
                              </td>
                              <td>
                                <Link onClick={() => {handleDeleteType(index)}} to="#">
                                  <i className="fa fa-times text-danger fa-2x"/>
                                </Link>
                              </td>
                            </tr>
                          ))
                        }
                    </tbody>
                      
                  </table>
                  <div className="float-right">
                    <button onClick={handleNewType} className="btn btn-sm btn-outline-primary font-weight-bold">
                      <i className="fa fa-plus"/>  
                    </button>
                  </div>
                </div>


              </div>

            </div>

          </form>
          
        </Modal.Body>
        
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="outline-grey" onClick={resetModals}>
              
            </Button>
            <Button variant="outline-success" form="GroupTypesForm" type="submit">
              
            </Button>
          </div>
        </Modal.Footer>

      </Modal>

    </div>
  )
}


export default ElementsTable