import React, { useEffect, useState } from 'react';
import { withRouter } from 'react-router-dom';

// Modals
import { Modal, Button } from 'react-bootstrap';



const FetchData = () => {

  const [state, setState] = useState({
    flash: false,
    title: null,
    message: null,
    state: null
  });

  useEffect(() => {
    fetch('/api/labels', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok) {
          if (data.user){
            console.log(data.notifications)
            localStorage.removeItem('maidan_user');
            localStorage.removeItem('maidan_user_access');
            localStorage.setItem('maidan_user', data.user);
            localStorage.setItem('notifs', data.notifications);
            localStorage.setItem('maidan_user_access', data.access_level);
          }
          localStorage.setItem('maidan_elements', data.labels);
          localStorage.setItem('maidan_files', data.files);
        } else {
          if (data.code === 'DISABLED'){
            if (localStorage.maidan_user){
              setState({
                flash: true,
                title: '',
                message: '   ,    .',
                state: 'error'
              })
            }
          }
        }
      })
    })
  })

  const resetModals = () => {
    setState({
      flash: false,
      title: null,
      message: null,
      state: null
    })

    // Reset Page
    localStorage.removeItem('maidan_jwt_$m3');
    localStorage.removeItem('maidan_user');
    localStorage.removeItem('logged');
    localStorage.removeItem('maidan_user_access');
    localStorage.removeItem('profile_img');
    sessionStorage.clear();
    window.location.href = window.location.origin
  }

  return(
    <div className="mt-5">
      <Modal show={state.flash} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">{state.title}</Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">
          {state.state === 'success' ? (
            <i className="fa fa-check-circle text-success fa-3x"/>
          ) : state.state === 'error' ? (
            <i className="fa fa-times-circle text-danger fa-3x"/>
          ) : null}
          <p className="mt-2 lead text-muted">
            {state.message}
          </p>
        </Modal.Body>
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="outline-primary" onClick={resetModals}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>

    </div>
  )
}

export default withRouter(FetchData);