import React, { useState, useEffect } from 'react';
import { Redirect } from 'react-router-dom';

// Modals
import { Modal, Button } from 'react-bootstrap';


export const NewGroup = () => {

  // Country/Cities
  const [cities, setCities] = useState([]);
  const [couponTypes, setTypes] = useState([]);

  const [form, setForm] = useState(1);
  const [errors, setErrors] = useState({});

  const [state, setState] = useState({
    company: '',
    company_type: '',
    coupons_num: 1,
    coupon_price: 10,
    coupon_code: '',
    start: '',
    expire: 0,
    onsite: 1,
    description: '',
    country: '',
    branches_num: 1,
    branches: [],
    images: []
  });

  const [uploaded, setUploaded] = useState(false);

  const [redirect, setRedirect] = useState({redirect: false, flash: null})

  const [balanceModal, setBalanceModal] = useState({view: false, user_balance: null});
  
  /*eslint-disable */
  //suppress all warnings between comments
  useEffect(() => {
    fetch('/api/authenticate/provider', {
      method: 'GET',
      headers: {
        'Content_Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          var userData = JSON.parse(localStorage.maidan_user)
          // Getting Cities Names (AR).
          var elements = JSON.parse(localStorage.maidan_elements);
          elements.forEach(element => {
            if (element.key === 'country'){
              if (element.values.country.ar === userData.country){
                setCities(element.values.cities.ar)
              }
            }
            if (element.key === 'coupon_types'){
              setTypes(element.values.ar)
            }
          });

          // Check Ads Images
          var images = userData.ad_images || []
          if (userData.main_ad_image) {
            images.push(userData.main_ad_image);
          }
          if (images <= 0){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: ' ',
                message: '            .'
              }
            })
          }

        } else {
          if (data.code === 'AUTH_ERROR'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: ' ',
                message: '     '
              }
            })
          }
          if (data.code === 'ACCESS_DENIED'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: ' ',
                message: '       .'
              }
            })
          }
          if (data.code === 'INVALID_ACCESS_LEVEL'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '     ,    .'
              }
            })
          }
          if (data.code === 'SERVER'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      ,     '
              }
            })
          }
          if (data.code === 'NO_TOKEN'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '    ,       '
              }
            })
          }
          if (data.code === 'INVALID_AUTH_HEADER'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '     ,    .'
              }
            })
          }
          if (data.code === 'NOT_LOGGED_IN'){
            localStorage.clear();
            sessionStorage.clear();
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '        .'
              }
            })
          }
          
        }
      })
    })

    const SubmitState = async() => {
      await fetch('/api/groups/create', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
        },
        body: JSON.stringify({...state, offset: new Date().getTimezoneOffset()})
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            setRedirect({
              redirect: true,
              flash: {
                type: 'success',
                title: ' ',
                message: '   ,     ,    .'
              }
            })
            // We Will Display A Card Later On To Showcase Successful Submition
          } else {
            if (data.code === 'AUTH_ERROR'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: ' ',
                  message: '     '
                }
              })
            }
            if (data.code === 'ACCESS_DENIED'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: ' ',
                  message: '       .'
                }
              })
            }
            if (data.code === 'INVALID_ACCESS_LEVEL'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '     ,    .'
                }
              })
            }
            if (data.code === 'SERVER'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '     ,     '
                }
              })
            }
            if (data.code === 'NO_TOKEN'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '    ,       '
                }
              })
            }
            if (data.code === 'INVALID_AUTH_HEADER'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '     ,    .'
                }
              })
            }
            if (data.code === 'NOT_LOGGED_IN'){
              localStorage.clear();
              sessionStorage.clear();
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '        .'
                }
              })
            }
            if (data.code === 'INVALID_REQUEST'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '     ,    .'
                }
              })
            }
            if (data.code === 'MISSING_VALUES'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '   ,   '
                }
              })
            }
            if (data.code === 'NO_COMPANY'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '     ,    '
                }
              })
            }
            if (data.code === 'NO_COUNTRY'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '    ,     ,  .'
                }
              })
            }
            if (data.code === 'NO_IMAGES'){
              document.getElementById('UploadBtn').disabled = false;
              setErrors({
                Images: {
                  type: 'danger',
                  name: '',
                  error: '      ,       .'
                }
              })
            }
          }
        })
      })
    }

    if (uploaded){
      setUploaded(false);
      SubmitState();
    }
  }, [uploaded])
  /*eslint-enable */

  const newGroupFormFirst = () => {

    const handleNext = (e) => {
      e.preventDefault();

      const checkBalance = async() => {
        await fetch("/api/groups/balance", {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json',
            'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
          },
          body: JSON.stringify({full_price: parseFloat(state.coupon_price*state.coupons_num)})
        }).then(resp => {
          resp.json().then(data => {
            if (resp.ok) {
              if (data.eligible) {
                setForm(2);
              } else {
                setBalanceModal({view: true, user_balance: data.user_balance});
              }
  
            } else {
              if (data.code === 'AUTH_ERROR'){
                setRedirect({
                  redirect: true,
                  flash: {
                    type: 'error',
                    title: ' ',
                    message: '     '
                  }
                })
              }
              if (data.code === 'ACCESS_DENIED'){
                setRedirect({
                  redirect: true,
                  flash: {
                    type: 'error',
                    title: ' ',
                    message: '       .'
                  }
                })
              }
              if (data.code === 'INVALID_ACCESS_LEVEL'){
                setRedirect({
                  redirect: true,
                  flash: {
                    type: 'error',
                    title: '',
                    message: '     ,    .'
                  }
                })
              }
              if (data.code === 'SERVER'){
                setRedirect({
                  redirect: true,
                  flash: {
                    type: 'error',
                    title: '',
                    message: '      ,     '
                  }
                })
              }
              if (data.code === 'NO_TOKEN'){
                setRedirect({
                  redirect: true,
                  flash: {
                    type: 'error',
                    title: '',
                    message: '    ,       '
                  }
                })
              }
              if (data.code === 'INVALID_AUTH_HEADER'){
                setRedirect({
                  redirect: true,
                  flash: {
                    type: 'error',
                    title: '',
                    message: '     ,    .'
                  }
                })
              }
              if (data.code === 'NOT_LOGGED_IN'){
                localStorage.clear();
                sessionStorage.clear();
                setRedirect({
                  redirect: true,
                  flash: {
                    type: 'error',
                    title: '',
                    message: '        .'
                  }
                })
              }
              if (data.code === 'INVALID_REQUEST'){
                setRedirect({
                  redirect: true,
                  flash: {
                    type: 'error',
                    title: '',
                    message: '     ,    .'
                  }
                })
              }
            }
          })
        })
      }
      
      // Validate Data.
      if (!couponTypes.includes(state.coupon_type)){
        setErrors({
          ...errors,
          coupon_type: {
            name: " ",
            error: "   ,    .",
            type: "warning"
          }
        })
      } else if (state.coupons_num < 1) {
        setErrors({
          ...errors,
          coupons_num: {
            name: " ",
            error: "     1  ",
            type: "warning"
          }
        })
      } else if (state.coupon_price < 10) {
        setErrors({
          ...errors,
          coupon_price: {
            name: " ",
            error: "     10$  .",
            type: "warning"
          }
        })
      } else if (state.branches_num < 1) {
        setErrors({
          ...errors,
          branches_num: {
            name: " ",
            error: "    1,      .",
            type: "warning"
          }
        })
      } else if (state.branches_num > 5) {
        setErrors({
          ...errors,
          branches_num: {
            name: " ",
            error: "    5,      .",
            type: "warning"
          }
        })
      } else if (new Date(state.start) < new Date(minDateToToday())) {
        setErrors({
          ...errors,
          start: {
            name: "  ",
            error: "   ,       .",
            type: "warning"
          }
        })
      } else if (state.expire < 0) {
        setErrors({
          ...errors,
          expire: {
            name: " ",
            error: "    0   .",
            type: "warning"
          }
        })
      } else if (state.expire > 20) {
        setErrors({
          ...errors,
          expire: {
            name: " ",
            error: "  30   ",
            type: "warning"
          }
        })
      } else if (state.onsite < 1) {
        setErrors({
          ...errors,
          onsite: {
            name: " ",
            error: "        .",
            type: "warning"
          }
        })
      } else {
        setErrors({});
        checkBalance();
      }

    }
  
    const handleChange = (e) => {
      e.preventDefault();

      // Reset Errors
      setErrors({});
  
      setState({
        ...state,
        [e.target.id]: e.target.value
      });
    }

    const handleBranchesNum = (e) => {
      e.preventDefault();

      // Edit Branches Array based On Value Of Branches Num.
      var num = parseInt(e.target.value);
      let loopList = [...Array(parseInt(num))]
      let branches_list = []

      loopList.forEach(_ => {
        branches_list.push({city: '', name: '', map: ''})
      });

      setState({
        ...state,
        [e.target.id]: e.target.value,
        branches: branches_list
      });

    }

    const minDateToToday = () => {
      var today = new Date();

      var dd = String(today.getDate());
      var mm = String(today.getMonth() + 1);
      var yy = today.getFullYear();

      return `${yy}-${(mm>9?'':'0')+mm}-${(dd>9?'':'0')+dd}T00:00:00`;
    }

    return(
      <form onSubmit={handleNext}>
        
        <p className="font-weight-lighter font-info text-muted text-right mt-4">
          <i className="fa fa-star-of-life fa-xs"/>    * .
          <br/>
          <i className="fa fa-star-of-life fa-xs"/>   5  .
          <br/>
          <i className="fa fa-star-of-life fa-xs"/>      10 .
          <br/>
          <i className="fa fa-star-of-life fa-xs"/>   10  ,   20  .
        </p>
        
        <div className="row mt-2">
          
          <div className="form-group col-sm-12">
            <label htmlFor="coupon_type" className="font-weight-bold float-right">*   : </label>
            <div className="input-group">
              <select onChange={handleChange} required id="coupon_type" defaultValue={state.coupon_type} className={"custom-select col-sm-6 " + (errors["coupon_type"] ? "is-invalid" : "")}>
                {state.coupon_type ? null : (<option value="" defaultValue> </option>)}
                {
                  couponTypes.map((type, index) => (
                    <option key={index} value={type}>{type}</option>
                  ))
                }
              </select>
            </div>
          </div>
          
        </div>

        <div className="row mt-2">
          
          <div className="form-group col-sm-4">
            
            <label htmlFor="coupons_num" className="font-weight-bold float-right">
            *   : 
            </label>
            <div className="input-group">
              <input required id="coupons_num" onChange={handleChange} type="number" min="1" defaultValue={state.coupons_num} className={"form-control col-sm-6 " + (errors["coupons_num"] ? "is-invalid" : "")}/>
            </div>
          
          </div>

          <div className="form-group col-sm-4">
            
            <label htmlFor="coupon_price" className="font-weight-bold float-right">
            *   ($) : 
            </label>
            <div className="input-group">
              <input required id="coupon_price" onChange={handleChange} type="number" defaultValue={state.coupon_price} min="10" step="0.01" className={"form-control col-sm-6 " + (errors['coupon_price'] ? "is-invalid" : "")}/>
            </div>
          
          </div>
          
          <div className="form-group col-sm-4">
            
            <label htmlFor="full_price" className="font-weight-bold float-right">
            *   ($) : 
            </label>
            <div className="input-group">
              <input disabled value={(state.coupons_num * state.coupon_price) || 10} type="number" className="form-control col-sm-6"/>
            </div>
          
          </div>
          
        </div>

        <div className="row mt-2">
          
          <div className="form-group col-sm-4">
            
            <label htmlFor="coupon_code" className="font-weight-bold float-right">
              *   :  
            </label>
            <div className="input-group">
              <input onChange={handleChange} defaultValue={state.coupon_code} type="text" required id="coupon_code" placeholder=" : F10e" className="form-control col-sm-6"/>
            </div>
          
          </div>
          
          <div className="form-group col-sm-4">
            
            <label htmlFor="branches_num" className="font-weight-bold float-right">*   : </label>
            <div className="input-group">
              <input onChange={handleBranchesNum} id="branches_num" type="number" min="1" max="5" defaultValue={state.branches_num} className={"form-control col-sm-6 " + (errors["branches_num"] ? "is-invalid" : "")}/>
            </div>
          
          </div>

        </div>

        <div className="row mt-2">
          
          <div className="form-group col-sm-12">
            
            <label htmlFor="description" className="font-weight-bold float-right">
            *   (300 ) : 
            </label>
            <div className="input-group">
              <textarea onChange={handleChange} defaultValue={state.description} type="text" required id="description" maxLength="300" placeholder="   ...." className="form-control col-sm-6"/>
            </div>
          
          </div>
        
        </div>

        <div className="row mt-2">

          <div className="form-group col-sm-4">
            
            <label htmlFor="start" className="font-weight-bold float-right">
              *    : 
            </label>
            <div className="input-group">
              <input required id="start" onChange={handleChange} defaultValue={state.start} type="datetime-local" min={minDateToToday()} className={"form-control col-sm-8 " + (errors["start"] ? "is-invalid" : "")}/>
            </div>
          
          </div>   
          
          <div className="form-group col-sm-4">
            
            <label htmlFor="expire" className="font-weight-bold float-right">
                () : 
            </label>
            <div className="input-group">
              <input id="expire" onChange={handleChange} defaultValue={state.expire} type="number" min="0" max="20" className="form-control col-sm-3"/>
              <div className="input-group-append">
                <input type="text" disabled value="+10 " className={"form-control col-5 " + (errors["expire"] ? "is-invalid" : "")}/>
              </div>
            </div>
          
          </div>   
          
          <div className="form-group col-sm-4">
            
            <label htmlFor="onsite" className="font-weight-bold float-right">
              *   () : 
            </label>
            <div className="input-group">
              <input required id="onsite" onChange={handleChange} type="number" min="1" defaultValue={state.onsite} className={"form-control col-sm-6 " + (errors["onsite"] ? "is-invalid" : "")}/>
            </div>
          
          </div>

        </div>

        <div className="input-group justify-content-center mt-4">
          <button type="submit" className="btn btn-md btn-outline-primary font-weight-bold"></button>
        </div>

      </form>
    )
  }

  const newGroupFormSecond = () => {

    const handleChange = (e) => {
      e.preventDefault();

      if (e.target.id.split("-")[0] === "city" && !cities.includes(e.target.value)){
        setErrors({
          ...errors,
          [e.target.id]: {
            name: "",
            error: "  ,    .",
            type: "warning"
          }
        })
      } else {
        setErrors({});
        // Get keys => NameSpace & ID
        var keys = e.target.id.split('-')
        let namespace = keys[0];
        let id = keys[1];

        // Keeping The State, & Changing Branches
        let CurState = {...state}
        let CurBranches = [...CurState.branches]

        let TargetBranch = {...CurBranches[id], [namespace]: e.target.value}

        CurBranches[id] = TargetBranch

        setState({
          ...state,
          branches: CurBranches
        });
      }

    }

    const handleSubmit = (e) => {
      e.preventDefault();

      // Validate Data.
      var validated = true;
      if (state.branches.length < 1) {
        validated = false;
        setErrors({
          ...errors,
          'city-0': {
            name: '',
            error: '    1',
            type: 'warning'
          }
        })
      }
      for (let i = 0; i < state.branches.length; i++) {
        if (!state.branches[i].city || state.branches[i].city === "") {
          validated = false;
          setErrors({
            [`city-${i}`]: {
              name: '',
              error: `    ${i+1}`,
              type: 'warning'
            }
          });
          break;
        } else if (!state.branches[i].name || state.branches[i].name === "") {
          validated = false;
          setErrors({
            [`name-${i}`]: {
              name: ' ',
              error: `     ${i+1}`,
              type: 'warning'
            }
          });
          break;
        } else if (!state.branches[i].map || state.branches[i].map === "") {
          validated = false;
          setErrors({
            [`map-${i}`]: {
              name: ' ',
              error: `     ${i+1}`,
              type: 'warning'
            }
          });
          break;
        }
      }

      if (validated) {
        setUploaded(true);
      }
    }

    return(
      <form onSubmit={handleSubmit}>
        <p className="font-weight-lighter font-info text-muted text-right mt-4">
          <i className="fa fa-star-of-life fa-xs"/>    * .
        </p>

        <div className="mt-2">

          {[...Array(parseInt(state.branches_num))].map((_, ind) => (
            
            <div key={ind}>
              <p className="text-right font-weight-bold">- ({ind+1}) : </p>

              <div className="row mt-5">
                
                <div className="form-group col-sm-4">
                  <label htmlFor={`city-${ind}`} className="font-weight-bold float-right">*  :</label>
                  <div className="input-group">
                    <select required onChange={handleChange} defaultValue={state.branches[ind] && state.branches[ind].city} name="branches[]" id={`city-${ind}`} className={"custom-select font-weight-bold col-sm-8 " + (errors[`city-${ind}`] ? "is-invalid" : "")}>
                      {state.branches[ind] && state.branches[ind].city ? null : (<option value="" defaultValue> </option>)}
                      {cities && cities.map((city, index) => (
                        <option className="font-weight-bold" key={index} value={city}>{city}</option>
                      ))}
                    </select>
                  </div>
                </div>

                <div className="form-group col-sm-4">
                  <label htmlFor={`name-${ind}`} className="font-weight-bold float-right">*   :</label>
                  <div className="input-group">
                    <input required onChange={handleChange} defaultValue={state.branches[ind] && state.branches[ind].name} type="text" id={`name-${ind}`} className={"form-control col-sm-8 " + (errors[`name-${ind}`] ? 'is-invalid' : '')}/>
                  </div>
                </div>
                
                <div className="form-group col-sm-10">
                  <label htmlFor={`map-${ind}`} className="float-right font-weight-bold">*   ( Google Maps) : </label>
                  <div className="input-group">
                    <input required onChange={handleChange} defaultValue={state.branches[ind] && state.branches[ind].map ? state.branches[ind].map : "https://www.google.com/maps/place/"} dir='ltr' id={`map-${ind}`}
                     type="text" className={"form-control col-sm-8 " + (errors[`map-${ind}`] ? 'is-invalid' : '')}/>
                  </div>
                </div>
              
              </div>

              <hr/>
            </div>

          ))}

        </div>
        
        <div className="input-group justify-content-center mt-4">
          <button type="submit" className="btn btn-md btn-outline-success font-weight-bold"> </button>
          <button onClick={() => setForm(1)} className="btn btn-md btn-outline-grey font-weight-bold"></button>
        </div>

      </form>
    )
  }

  const LoadForms = () => {

    switch(form){
      case 1:
        return newGroupFormFirst();
      case 2:
        return newGroupFormSecond();
      default:
        return newGroupFormFirst();
    }
  }

  const loadErrors = () => {
    if (Object.keys(errors).length !== 0 && errors.constructor === Object){
      return (
        <div>
          {errors ? Object.entries(errors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  if (redirect.redirect){
    return <Redirect to={{
      pathname: '/',
      state: {flash: redirect.flash}
    }}/>
  }

  return(
    <div className="mt-5 pt-5 pb-5">

      <div className="container text-center mt-5">
        <span className="display-6 qsemh-turq shadow-text">
             
        </span>
      </div>

      <hr/>

      <div className="container text-center mt-4">
        <div className="card shadow-md p-3 bg-light-grey">
          <p className="card-title text-info">
            <span className="display-7">  : </span>
            <span className="display-8 float-left text-dark">({form}/2)</span>
          </p>
          <hr/>
          <div className="container mb-2">
            {loadErrors()}
          </div>
          <div className="card-body">
            {LoadForms()}
          </div>
        </div>
      </div>

      {/* Flash State Modal */}
      <Modal show={balanceModal.view} onHide={() => {setBalanceModal({view: false, user_balance: null})}}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8"> </Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">
          <i className="fa fa-times-circle text-danger fa-3x"/>
          <p className="font-weight-bold">
              : {balanceModal.user_balance}$
            <br/>
                    ({state.coupon_price*state.coupons_num}$).
          </p>
        </Modal.Body>
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={() => {setBalanceModal({view: false, user_balance: null})}}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>
   
    </div>
  )
}
