import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

// Modals
import { Modal, Button } from 'react-bootstrap';


/*
  This Page Will Display The Best 6 Coupon Groups (Based on Full Price)
*/

export const Home = (props) => {

  const [AdsFiles, setAdsFiles] = useState({carousel: [], small: []});
  const curState = props.location && props.location.state;

  const flashMessage = curState && curState.flash;
  const [flashState, setFlashState] = useState({
    flash: flashMessage,
    view: (flashMessage ? true : false)
  })
  

  const [groups, setGroups] = useState();
  const [errors, setErrors] = useState({});
  


  useEffect(() => {
    fetch('/api/groups', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      },
      body: JSON.stringify({user: localStorage.maidan_user ? JSON.parse(localStorage.maidan_user) : null})
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setGroups(data.groups);
          var files = JSON.parse(localStorage.maidan_files);
          var carouselAds = [];
          var smallAds = [];
          // Loop Through Saved Files.

          for (let i = 0; i < files.length; i++){
            if (files[i].key === 'carouselAds'){
              carouselAds.push(files[i]);
            }
            if (files[i].key === 'smallAds'){
              smallAds.push(files[i]);
            }
          }

          setAdsFiles({
            carousel: carouselAds,
            small: smallAds
          })
        } else {
          setErrors({
            QueryError: {
              name: '',
              error: '      ,   .',
              type: 'danger'
            }
          })
        }
      })
    })
  }, [])


  const loadCards = () => {
    return(
      <div className="row justify-content-center mt-4">
        {groups.map((group, index) => (
          <div key={index} className="col-sm-10 col-md-4">

            <div className="card hoverable mt-4 text-center bg-light-grey img-thumbnail">
              
              <div className="view overlay zoom">
                <img loading="lazy" src={`https://maidancom.s3.us-east-2.amazonaws.com/${group.images[0]}`} alt={group.images[0]} className="card-top-img card-normal-img img-fluid"/>
                <Link to={`/ads/${group.id}`}>
                  <div className="mask flex-center rgba-black-slight">
                    <div className="card-body">
                      <span className="display-7 card-title shadow-text qsemh-turq">{group.company}</span>
                      <p className="card-text font-weight-bold  text-dark mt-4">
                           <i className="fa fa-external-link-alt "/>
                      </p>
                    </div>
                  </div>
                </Link>
              </div>
              
              <div className="card-body">
                
                <Link to={`/ads/${group.id}`} className="text-dark">
                  <p className="display-8 font-weight-bold">
                    {group.company}
                  </p>
                </Link>

              </div>

            </div>

          </div>
        ))}
      </div>
    )
  }

  const loadSmallAds = () => {
    let smallAds = AdsFiles.small;

    return(
      smallAds.sort(() => 0.5 - Math.random()).slice(0, 2).map((file, index) => {
        return(
          <div key={index} className="col-sm-6 col-gap pr-md-1 pt-mobile">
            <img loading="lazy" src={`https://maidancom.s3.us-east-2.amazonaws.com/${file.file_name}`} alt={file.file_name} className="ads-images img-fluid"/>
          </div>
        )
      })
    )
  }

  const loadErrors = () => {
    if (Object.keys(errors).length !== 0 && errors.constructor === Object){
      return (
        <div>
          {errors ? Object.entries(errors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  return(
    <div className="mb-5 pb-5">

      <div className="pt-4">
              
        <div id="carousel-example-1z" className="mt-5 pt-5 pr-md-2 pl-md-2 carousel slide carousel-fade" data-ride="carousel">

          <ol className="carousel-indicators">
            <li data-target="#carousel-example-1z" data-slide-to="0" className="active"></li>
            <li data-target="#carousel-example-1z" data-slide-to="1"></li>
            {/* <li data-target="#carousel-example-1z" data-slide-to="2"></li> */}
          </ol>

          <div className="carousel-inner" role="listbox">

            <div className="carousel-item carousel-sitem active">
              <img loading="lazy" className="w-100 h-100 img-fluid" src="https://sites.google.com/site/moh897/banner1.png"
                alt="First slide"/>
            </div>

            <div className="carousel-item carousel-sitem">
              <img loading="lazy" className="w-100 h-100 img-fluid" src="https://sites.google.com/site/moh897/banner2.png"
                alt="Second slide"/>
            </div>

          </div>

          <a className="carousel-control-prev" href="#carousel-example-1z" role="button" data-slide="prev">
            {/* <span className="carousel-control-prev-icon" aria-hidden="true"></span> */}
            <i className="fa fa-arrow-alt-circle-left carousel-control-prev-icons" aria-hidden="true"></i>
            <span className="sr-only"></span>
          </a>

          <a className="carousel-control-next" href="#carousel-example-1z" role="button" data-slide="next">
            {/* <span className="carousel-control-next-icon" aria-hidden="true"></span> */}
            <i className="fa fa-arrow-alt-circle-right carousel-control-next-icons" aria-hidden="true"></i>
            <span className="sr-only"></span>
          </a>

        </div> 
        
      </div>

      <div className="mt-5 container">

        <div className="row justify-content-center">

          {loadSmallAds()}

        </div>

      </div>

      <div className="mt-5 text-center container">
        {loadErrors()}
      </div>
     
      <div className="mt-5 text-center container">
        <span className="display-6 header-shadow qsemh-turq">
           
        </span>
        <hr/>
        {groups && groups.length > 0 ? loadCards() : (<p className="font-weight-bold text-danger">   </p>)}
      </div>
    
      <div id="carousel-example-2z" className="mt-5 pt-5 pr-md-3 pl-md-3 carousel slide" data-ride="carousel">

        <div className="carousel-inner" role="listbox">

          {
            AdsFiles.carousel.map((file, index) => {
              if (index === 0){
                return(
                  <div key={index} className="carousel-item carousel-ad-item active">
                    <img loading="lazy"  className="w-100 h-100 img-fluid" src={`https://maidancom.s3.us-east-2.amazonaws.com/${file.file_name}`}
                      alt={file.file_name}/>
                  </div>
                )
              } else {
                return(
                  <div key={index} className="carousel-item carousel-ad-item">
                    <img loading="lazy"  className="w-100 h-100 img-fluid" src={`https://maidancom.s3.us-east-2.amazonaws.com/${file.file_name}`}
                      alt={file.file_name}/>
                  </div>
                )
              }
            })
          }

        </div>

        <a className="carousel-control-prev" href="#carousel-example-2z" role="button" data-slide="prev">
          {/* <span className="carousel-control-prev-icon" aria-hidden="true"></span> */}
          <i className="fa fa-arrow-alt-circle-left carousel-control-prev-icons" aria-hidden="true"></i>
          <span className="sr-only"></span>
        </a>
        
        <a className="carousel-control-next" href="#carousel-example-2z" role="button" data-slide="next">
          {/* <span className="carousel-control-next-icon" aria-hidden="true"></span> */}
          <i className="fa fa-arrow-alt-circle-right carousel-control-next-icons" aria-hidden="true"></i>
          <span className="sr-only"></span>
        </a>

      </div>     
      
      <div className="input-group justify-content-center mt-4">
        <Link to="/ads" className="btn btn-lg btn-outline-turq font-weight-bold"> </Link>
      </div>
      
      {/* Flash Messages Modal */}
      <Modal show={flashState.view} onHide={() => {setFlashState({flash: {}, view: false})}}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">{flashState.flash && flashState.flash.title}</Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">
          {flashState.flash && flashState.flash.type === 'error' ? (
            <i className="fa fa-times-circle text-danger fa-3x"/>
          ) : (
            <i className="fa fa-info-circle text-info fa-3x"/>
          )}
          <p className="mt-2 lead text-muted">
            {flashState.flash && flashState.flash.message}
          </p>
        </Modal.Body>
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="outline-primary" onClick={() => {setFlashState({flash: {}, view: false})}}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>
   

    </div>
  )
}

