import React, { Component } from 'react';
import {BrowserRouter as Router, Route, Switch, Redirect} from 'react-router-dom';


/* Main Layout */ 
import Navbar from './components/layouts/Navbar';
import { Footer } from './components/layouts/Footer';
import SyncApp from './SyncApp';
import FetchData from './SyncData'

import { Home } from './components/Home/Home';
import { Terms } from './components/Home/Terms';

/* Coupon Groups */
import { ViewGroup } from './components/CouponsGroup/Group';
import { ViewGroups } from './components/CouponsGroup/Groups';

/* Offers */
import { ViewOffers } from './components/CouponsOffer/Offers'

/* User */
import { Profile } from './components/User/Profile';
import { Notifications } from './components/User/Notifications';
import { Balance } from './components/User/Balance';
import { MyCoupons } from './components/User/MyCoupons';
import { Activation } from './components/User/Activation';
import { Settings } from './components/User/Settings';
import { Ticket } from './components/User/Ticket';
import { MyTickets } from './components/User/MyTickets';

/* Admins/Providers */
import { NewGroup } from './components/Admins/Provider/NewGroup';
import { RedeemCoupons } from './components/Admins/Provider/Redeem';
import MyGroups from './components/Admins/Provider/Panel';

/* Admins/Admin */ 
import { AdminViewGroup } from './components/CouponsGroup/AdminGroup';
import { AdminPanel } from './components/Admins/Admin/Panel';
import Histories from './components/Admins/Panels/Histories';

/* Admins/SuperAdmin */
import { SuperAdminPanel } from './components/Admins/SuperAdmin/Panel';




class App extends Component {

  render() {

    const Default = () => (
      <div className="App">
        <Navbar/>
        <Switch>
          {/* Default Routes */}
          <Route exact path="/" component={Home} />
          <Route exact path="/terms" component={Terms} />
          <Route exact path="/coupons/offers" component={ViewOffers} />
          <Route exact path="/ads" component={ViewGroups} />
          <Route exact path="/ads/:gid" component={ViewGroup} />
          
          {/* Admins Routes */}
          <Route exact path="/admin" render={() => <Redirect to="/admin/panel"/>}/>
          <Route exact path="/admin/panel" component={SuperAdminPanel}/>
          {/* Admins/Provider */}
          <Route exact path="/admin/groups/new" component={NewGroup} />
          <Route exact path="/admin/redeem" component={RedeemCoupons} />
          <Route exact path="/providers/panel" component={MyGroups} />
          {/* Admins/Admin */}
          <Route exact path="/admin/histories" component={Histories} />
          <Route exact path="/admins/panel" component={AdminPanel} />
          <Route exact path="/admin/ads/:gid" component={AdminViewGroup} />
          
          {/* Users Routes */}
          <Route exact path="/user/profile" component={Profile}/>
          <Route exact path="/user/notifications" component={Notifications}/>
          <Route exact path="/settings" component={Settings}/>
          <Route exact path="/balance" component={Balance}/>
          <Route exact path="/user/coupons" component={MyCoupons}/>
          <Route exact path="/user/tickets" component={MyTickets}/>
          <Route exact path="/user/tickets/:id" component={Ticket}/>
          <Route exact path="/activate" component={Activation}/>
        </Switch>
        <Footer/>
      </div>
    )
    
    return(
      <Router>
        <FetchData/>
        <SyncApp/>
        <Switch>
          <Route component={Default}/>
        </Switch>
      </Router>
    )
  }
}

export default App;
