import React, { useState, useEffect } from 'react';

// Phone Input
import { getCountryCallingCode } from 'react-phone-number-input';

// Modals
import { Modal, Button } from 'react-bootstrap';
import { Link, Redirect } from 'react-router-dom';


const UsersTable = () => {

  const [basic, setBasicData] = useState([]);

  const [users, setUsers] = useState({users: null, limit: 50});

  const [redirect, setRedirect] = useState({redirect: false, flash: null});
  const [errors, setErrors] = useState({});

  // Search Users
  const [search, setSearch] = useState();  

  // Modals
  const [state, setState] = useState({
    amount: 0,
    access_level: null,
    company_name: null,
    username: null,
    phone: null,
    pre_phone: null,
    country: null
  });
  
  const [modalState, setModalState] = useState({});

  const [isSetting, setSettingModal] = useState(false);
  const [isRaise, setRaiseModal] = useState(false);
  const [isDelete, setDeletingModal] = useState(false);

  useEffect(() => {
    fetch('/api/panel/users', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setUsers({
            users: data.users,
            limit: 50
          })
        } else {
          if ([
            'INVALID_AUTH_HEADER', 
            'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
            'ACCESS_DENIED', 'AUTH_ERROR'
          ].includes(data.code)) {
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
          if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
            localStorage.clear();
            sessionStorage.clear();
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
        }
      })
    })

    // Fetch basic Data
    let basicData = JSON.parse(localStorage.maidan_elements);
    setBasicData(basicData);
  }, [])

  const handleSettings = (id, user) => {

    if (id === 'S'){
      setSettingModal(true);
      setModalState(user);
      setState({
        ...state,
        username: user.username,
        phone: user.phone.slice(4),
        pre_phone: user.phone.substring(0, 4),
        country: user.country
      })
    }
    if (id === 'R'){
      setRaiseModal(true);
      setModalState(user);
    }
    if (id === 'D'){
      setDeletingModal(true);
      setModalState(user);
    }
  }

  const SearchUsers = async(e) => {
    setSearch({
      ...search,
      [e.target.id]: e.target.value
    })
    await fetch('/api/panel/users', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      },
      body: JSON.stringify({...search, [e.target.id]: e.target.value})
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setUsers({
            ...users,
            users: data.users
          })
        } else {
          if ([
            'INVALID_AUTH_HEADER', 
            'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
            'ACCESS_DENIED', 'AUTH_ERROR'
          ].includes(data.code)) {
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
          if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
            localStorage.clear();
            sessionStorage.clear();
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
        }
      })
    })
  }

  const LoadUsers = () => {

    const users_patch = users.users && users.users.slice(0, users.limit);

    return(
      <tbody>
        <tr>
          <th className="table-header font-weight-bold">
            #
          </th>

          <td className="table-body">
            <input onChange={SearchUsers} id="username" type="text" placeholder="  : " className="form-contro"/>
          </td>
          <td className="table-body">
            <input onChange={SearchUsers} id="fullname" type="text" placeholder="  : " className="form-contro"/>
          </td>
          <td className="table-body">
            <input onChange={SearchUsers} id="email" type="email" placeholder=" : " className="form-contro"/>
          </td>
          <td className="table-body">
            <input onChange={SearchUsers} id="phone" type="text" inputMode="tel" placeholder="  : " className="form-contro"/>
          </td>

          <td className="table-body"/>
          <td className="table-body"/>
          <td className="table-body"/>
        </tr>
        {
          users_patch ? users_patch.map((user, index) => (
            <tr className="" key={index}>
              <th className="table-header">{user.id}</th>
              <td className="table-body">{user.username}</td>
              <td className="table-body">{user.fullname}</td>
              <td className="table-body">{user.email}</td>
              <td dir='ltr' className="table-body">{user.phone}</td>
              <td className="table-body">{user.claimed_coupons}</td>
              <td className="table-body">{user.balance} $</td>
              <td className="table-body">
                <button onClick={() => {handleSettings('S', user)}} className="btn btn-sm btn-outline-grey font-weight-bold"></button>
                <button onClick={() => {handleSettings('R', user)}} className="btn btn-sm btn-outline-info font-weight-bold"></button>
                <button onClick={() => {handleSettings('D', user)}} className="btn btn-sm btn-danger font-weight-bold"></button>
              </td>
            </tr>
          )) : null
        }
      </tbody>
    )
  }

  const AddLimit = () => {
    if (users.limit < users.users.length){
      let new_limit = users.limit + 50;
      setUsers({...users, limit: new_limit})
    }
  }

  const ReduceLimit = () => {
    if (users.limit > 50){
      let new_limit = users.limit - 50;
      setUsers({...users, limit: new_limit})
    }
  }

  // Settings

  const handleDelete = (e) => {
    e.preventDefault();

    const DeleteUser = async() => {
      await fetch(`/api/panel/users/${modalState.id}`, {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
        }
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            setUsers({
              ...users,
              users: data.users
            });
            resetModals();
          } else {
            if ([
              'INVALID_AUTH_HEADER', 
              'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
              'ACCESS_DENIED', 'AUTH_ERROR'
            ].includes(data.code)) {
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      .'
                }
              })
            }
            if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
              localStorage.clear();
              sessionStorage.clear();
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      .'
                }
              })
            }
            if (data.code === 'NO_MATCH_ID'){
              setErrors({
                ID: {
                  name: '',
                  error: '  ,     ,    .',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'SERVER'){
              setErrors({
                Server: {
                  name: '',
                  error: '     ,    .',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'DISABLED'){
              setErrors({
                Server: {
                  name: '',
                  error: '  ,    .',
                  type: 'warning'
                }
              })
            }
          }
        })
      })
    }

    DeleteUser();
  }

  const handleSettingsChange = (e) => {
    e.preventDefault();

    setErrors({});

    if (e.target.id === 'pre_phone'){
      setState({
        ...state,
        [e.target.id]: e.target.name,
        country: e.target.title
      });

      document.getElementById('pre_phone_selector').innerText = e.target.name;
    } else {
      setState({
        ...state,
        [e.target.id]: e.target.value
      })
    }

  }

  const handleSettingsSubmit = (e) => {
    e.preventDefault();

    const SubmitSettings = async() => {
      await fetch(`/api/panel/users/${modalState.id}/settings`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
        },
        body: JSON.stringify(state)
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            setUsers({
              ...users,
              users: data.users
            });
            resetModals();
          } else {
            if ([
              'INVALID_AUTH_HEADER', 
              'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
              'ACCESS_DENIED', 'AUTH_ERROR'
            ].includes(data.code)) {
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      .'
                }
              })
            }
            if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)) {
              localStorage.clear();
              sessionStorage.clear();
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      .'
                }
              })
            }
            if (data.code === 'NO_MATCH_ID') {
              setErrors({
                ID: {
                  name: '',
                  error: '  ,     ,    .',
                  type: 'danger'
                }
              })
            }
            if (data.code === "USERNAME_TAKEN") {
              setErrors({
                username: {
                  name: ' ',
                  error: '   ,     .',
                  type: 'warning'
                }
              })
            }
            if (data.code === "PHONE_TAKEN") {
              setErrors({
                fullPhone: {
                  name: ' ',
                  error: '   ,     .',
                  type: 'warning'
                }
              })
            }
            if (data.code === "INVALID_REQUEST") {
              setErrors({
                request: {
                  name: '',
                  error: '     ,    .',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'SERVER'){
              setErrors({
                Server: {
                  name: '',
                  error: '     ,    .',
                  type: 'danger'
                }
              })
            }
          }
        })
      })
    }

    if (state.amount < -modalState.balance) {
      setErrors({
        amount: {
          name: "",
          error: `     ${modalState.balance}- (    )`,
          type: "warning"
        }
      })
    }
    else if (!state.pre_phone) {
      setErrors({
        ...errors,
        pre_phone: {
          name: ' ',
          error: '   ,   .',
          type: 'warning'
        }
      })
    }
    else if (state.phone[0] !== "5" || state.phone.length !== 9){
      setErrors({
        ...errors,
        phone: {
          name: ' ',
          error: '      5     9 .',
          type: 'warning'
        }
      })
    }
    else {
      SubmitSettings();
    }
  }

  const handleRaiseSubmit = (e) => {
    e.preventDefault();

    const SubmitRaise = async() => {
      await fetch(`/api/panel/users/${modalState.id}/access_level`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
        },
        body: JSON.stringify(state)
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            setUsers({
              ...users,
              users: data.users
            });
            resetModals();
          } else {
            if ([
              'INVALID_AUTH_HEADER', 
              'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
              'ACCESS_DENIED', 'AUTH_ERROR'
            ].includes(data.code)) {
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      .'
                }
              })
            }
            if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
              localStorage.clear();
              sessionStorage.clear();
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      .'
                }
              })
            }
            if (data.code === 'NO_MATCH_ID'){
              setErrors({
                ID: {
                  name: '',
                  error: '  ,     ,    .',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'SERVER'){
              setErrors({
                Server: {
                  name: '',
                  error: '     ,    .',
                  type: 'danger'
                }
              })
            }
          }
        })
      })
    }

    if (state.access_level === "PROVIDER" && !state.company_name) {
      setErrors({
        company_name: {
          name: " ",
          error: "     .",
          type: 'warning'
        }
      })
    } else if (state.access_level !== "PROVIDER" && state.company_name) {
      setErrors({
        company_name: {
          name: " ",
          error: "          .",
          type: 'warning'
        }
      })
    } else {
      SubmitRaise();
    }
  }

  const loadErrors = () => {
    if (Object.keys(errors).length !== 0 && errors.constructor === Object){
      return (
        <div>
          {errors ? Object.entries(errors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  const resetModals = () => {

    // Reset Modals
    setSettingModal(false);
    setRaiseModal(false);
    setDeletingModal(false);

    // Reset State, Errors
    setErrors({});
    setModalState({});
    setState({
      amount: 0,
      access_level: null,
      company_name: null,
      username: null,
      phone: null,
      pre_phone: null,
      country: null
    });
  }

  if (redirect.redirect){
    return <Redirect to={{
      pathname: '/',
      state: {flash: redirect.flash}
    }} />
  }

  return(
    <div className="table-responsive mt-4">
    
      <table className="table table-sm table-bordered">
        <thead>
          <tr className="bg-light-grey">
            <th className="font-weight-bold" scope="col">ID</th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"></th>
            <th className="font-weight-bold" scope="col"></th>
          </tr>
        </thead>
        {LoadUsers()}
      </table>
      <div className="justify-content-center">
        {(users.users && users.limit < users.users.length) ? (<button onClick={AddLimit} className="btn btn-sm btn-outline-primary"> </button>) : null}
        {users.limit > 50 ? (<button onClick={ReduceLimit} className="btn btn-sm btn-outline-secondary"> </button>) : null}
      </div>
    
      {/* Settings Modal */}
      <Modal show={isSetting} onHide={resetModals}>
        
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">   ({modalState.username}) : </Modal.Title>
        </Modal.Header>
        
        <Modal.Body className="text-center">

          <div className="container mt-2">
            {loadErrors()}
          </div>

          <form onSubmit={handleSettingsSubmit} id="mainForm">

            <div className="container text-right">
              <p className="info-weight text-muted">
                <i className="fa fa-star-of-life fa-xs ml-2"/>
                     .
              </p>
            </div>

            <div className="container mt-4">

              <div className="row text-right">

                <div className="col-sm-12">
                  <label htmlFor="username" className="font-weight-bold">*   :</label>
                  <div className="input-group">
                    <input onChange={handleSettingsChange} required id="username" defaultValue={modalState.username} type="text" placeholder=" " className={"form-control col-sm-10 " + (errors.username ? 'is-invalid' : '')}/>
                  </div>
                </div>

                <div className="col-sm-12 mt-3">
                  <label htmlFor="phone" className="ml-sm-4 font-weight-bold">*   : </label>
                  <div className="input-group">

                    <input onChange={handleSettingsChange} dir='ltr' defaultValue={modalState.phone ? modalState.phone.slice(4) : null } required id="phone" type="text" placeholder="5xxxxxxxx" className={"form-control col-sm-8 " + (errors.phone || errors.fullPhone ? "is-invalid" : '')}/>
                    
                    <div className="btn-group">
                      <Link dir='ltr' id="pre_phone_selector" className={"form-control btn-white dropdown-toggle " + (errors.pre_phone || errors.fullPhone ? 'is-invalid' : '')} data-toggle="dropdown" to="#">{modalState.phone ? modalState.phone.substring(0,4) : null}</Link>
                      <ul className="dropdown-menu font-weight-bold">
                        {
                          basic.map((data, index) => {
                            if (data.key === 'country'){
                              return(
                                <li key={index}>
                                  <Link to="#" className="text-dark ml-2" id="pre_phone" name={`+${data.values.telecode}`} title={data.values.country.ar} onClick={handleSettingsChange}>
                                    {getCountryCallingCode(data.label)}+ <img src={`http://catamphetamine.gitlab.io/country-flag-icons/3x2/${data.label}.svg`} width="24" alt={data.label}/>
                                  </Link>
                                </li>
                              )
                            }
                          })
                        }
                      </ul>
                    </div>

                  </div>
                </div>

                <div className="col-sm-12 mt-3">
                  <label htmlFor="country" className="font-weight-bold">*  :</label>
                  <div className="input-group">
                    <select onChange={handleSettingsChange} id="country" required defaultValue={modalState.country} className="custom-select col-sm-10">
                      {basic.map((data, index) => {
                        if (data.key === 'country'){
                          return(
                            <option key={index} value={data.values.country.ar}>{data.values.country.ar}</option>
                          )
                        }
                      })}
                    </select>
                  </div>
                </div>

                <div className="col-sm-12 mt-3">
                  <label htmlFor="amount" className="font-weight-bold">* (/)  $ : </label>
                  <div className="input-group">
                    <input onChange={handleSettingsChange} required id="amount" type="number" min={-modalState.balance} step="0.01" defaultValue={0} placeholder=" " className="form-control col-sm-10"/>
                  </div>
                </div>

                <div className="container text-right mt-2">
                  <p className="info-weight">
                    -  {modalState.username}  : {modalState.balance}$
                  <br/>
                    -  {modalState.username}   : {parseFloat(parseFloat(modalState.balance) + parseFloat(state.amount)).toFixed(2)}$
                  </p>
                </div>

              </div>

            </div>

          </form>

        </Modal.Body>
        
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="outline-grey" onClick={resetModals}>
              
            </Button>
            <Button type='submit' form="mainForm" variant="outline-primary">
              
            </Button>
          </div>
        </Modal.Footer>
      
      </Modal>
      
      {/* Raise Modal */}
      <Modal show={isRaise} onHide={resetModals}>
        
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">   ({modalState.username}) : </Modal.Title>
        </Modal.Header>
        
        <Modal.Body className="text-center">

          <div className="container mt-2">
            {loadErrors()}
          </div>

          <form onSubmit={handleRaiseSubmit} id="mainForm">

            <div className="container">
              <p className="info-weight text-muted">
                <i className="fa fa-star-of-life fa-xs ml-2"/>
                      .
              </p>
            </div>

            <div className="container mt-4">

              <div className="row text-right">

                <div className="col-sm-10">
                  <label htmlFor="access_level" className="font-weight-bold">*   : </label>
                  <div className="input-group">
                    <select onChange={handleSettingsChange} required id="access_level" defaultValue={modalState.access_level} className="custom-select font-weight-bold col-sm-10">
                      <option className="font-weight-bold text-info" value="USER"></option>
                      <option className="font-weight-bold text-secondary" value="PROVIDER"></option>
                      <option className="font-weight-bold text-danger" value="ADMIN"></option>
                    </select>
                  </div>
                </div>

                <div className="col-sm-10 mt-2">
                  <label htmlFor="company_name" className="font-weight-bold">  : </label>
                  <div className="input-group">
                    <input onChange={handleSettingsChange} id="company_name" type="text" placeholder="( )" className={"form-control col-sm-10 " + (errors.company_name ? "is-invalid" : '')}/>
                  </div>
                </div>

              </div>

            </div>

          </form>

        </Modal.Body>
        
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="outline-grey" onClick={resetModals}>
              
            </Button>
            <Button type="submit" form='mainForm' variant="outline-primary">
              
            </Button>
          </div>
        </Modal.Footer>

      </Modal>
      
      {/* Delete Modal */}
      <Modal show={isDelete} onHide={resetModals}>
        
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">  ({modalState.username}) : </Modal.Title>
        </Modal.Header>
        
        <Modal.Body className="text-center">

          <div className="container mt-2">
            {loadErrors()}
          </div>

          <div className="container">
            <p className="text-danger font-weight-bold">
                   
            </p>
            <p className="text-dark font-info">
                 ,    .
            </p>
          </div>

        </Modal.Body>
        
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="outline-grey" onClick={resetModals}>
              
            </Button>
            <Button onClick={handleDelete} variant="danger">
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>
      
    </div>
  )
}


export default UsersTable