import React, { useEffect, useState } from 'react';
import { Redirect, Link } from 'react-router-dom';
// Modals
import { Modal, Button } from 'react-bootstrap';


export const Activation = (props) => {
  const URLQuery = new URLSearchParams(props.location.search)

  const token = URLQuery.get("token") || null;

  const [errors, setErrors] = useState({});
  const [modalErrors, setModalErrors] = useState({});
  const [status, setStatus] = useState({
    status: 'waiting',
    msg: ' ,   ',
    type: 'dark'
  });

  // Modal
  const [isModal, setModal] = useState(false);
  const [email, setEmail] = useState(null);

  /*eslint-disable */
  //suppress all warnings between comments
  useEffect(() => {
    fetch('/api/activate', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      },
      body: JSON.stringify({token: token})
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setStatus({
            status: 'success',
            msg: '  !',
            type: 'success'
          });
        } else {
          if (data.code === 'INVALID_REQUEST'){
            setStatus({
              status: 'error',
              msg: '    !',
              type: 'danger'
            });
            setErrors({
              InvalidRequest: {
                name: '',
                error: '     !',
                type: 'warning'
              }
            })
          }
          if (data.code === 'INVALID_TOKEN'){
            setStatus({
              status: 'error',
              msg: '    !',
              type: 'danger'
            });
            setErrors({
              InvalidToken: {
                name: '',
                error: '   ,     ',
                link: {
                  text: ' ',
                  id: 'ActivationLink'
                },
                type: 'warning'
              }
            })
          }
          if (data.code === 'EXPIRED_TOKEN'){
            setStatus({
              status: 'error',
              msg: '    !',
              type: 'danger'
            });
            setErrors({
              InvalidToken: {
                name: '',
                error: '   ,     ',
                link: {
                  text: ' ',
                  id: 'ActivationLink'
                },
                type: 'warning'
              }
            })
          }
          if (data.code === 'USED_TOKEN'){
            setStatus({
              status: 'error',
              msg: '  !',
              type: 'info'
            });
            setErrors({
              InvalidToken: {
                name: '',
                error: '          ',
                type: 'info'
              }
            })
          }
          if (data.code === 'WRONG_EMAIL'){
            setStatus({
              status: 'error',
              msg: '    !',
              type: 'danger'
            });
            setErrors({
              InvalidToken: {
                name: '',
                error: '   ,       .',
                type: 'danger'
              }
            })
          }
        }
      })
    })
  }, []);
  /*eslint-enable */

  const redirectOnTime = () => {
    setStatus({...status, status: 'redirect'})
  }

  const ReSendActivationLink = async() => {
    await fetch('/api/activation', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      },
      body: JSON.stringify({email: email, hostname: window.location.origin})
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setModalErrors({
            success: {
              name: ' ',
              error: '    !',
              type: 'success'
            }
          });
          document.getElementById('activationBtn').disabled = true;
          setStatus({
            status: 'success'
          })
        } else {
          if (data.code === 'NO_EMAIL'){
            setModalErrors({
              email: {
                name: ' ',
                error: '     ,   .',
                type: 'danger'
              }
            })
          }
          if (data.code === 'ACTIVATED'){
            setModalErrors({
              activated: {
                name: ' ',
                error: '     ,   .',
                type: 'info'
              }
            })
            setTimeout(redirectOnTime, 5000)
          }
        }
      })
    })
  }

  const handleSubmit = (e) => {
    e.preventDefault();

    // Resend Activation To Given Email
    ReSendActivationLink();
  }

  const handleChange = (e) => {
    e.preventDefault();

    setEmail(e.target.value);
  }

  const handleErrorEvent = (e) => {
    e.preventDefault()

    if (e.target.id === 'ActivationLink'){
      setModal(true);
    }
  }

  const loadErrors = () => {
    if (Object.keys(errors).length !== 0 && errors.constructor === Object){
      return (
        <div>
          {errors ? Object.entries(errors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error} {e[1].link ? (<Link to="#" id={e[1].link.id} onClick={handleErrorEvent}>{e[1].link.text}</Link>) : null}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  const loadModalErrors = () => {
    if (Object.keys(modalErrors).length !== 0 && modalErrors.constructor === Object){
      return (
        <div>
          {modalErrors ? Object.entries(modalErrors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error} {e[1].link ? (<Link to="#" id={e[1].link.id} onClick={handleErrorEvent}>{e[1].link.text}</Link>) : null}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  const resetModals = (e) => {
    e.preventDefault();

    setModal(false);
    setEmail(null);
  }


  if (status.status === 'success'){
    setTimeout(redirectOnTime, 1500);
  }

  if (status.status === 'redirect'|| !token){
    return <Redirect to="/"/>
  }

  return (
    <div className="mb-5 pb-5 pt-5">
      
      <div className="container mt-5 pt-5">
        
        <div className="card mt-5 text-center shadow-sm">

          <span className="display-7 qsemh-turq"> </span>

          <div className="card-body">
            <div className="mt-4">
              {loadErrors()}
            </div>
            <div className="mt-5">
              <p className={`display-8 text-${status.type}`}>{status.msg}</p>
              {status.status === 'waiting' ? (
                <div className="spinner-border mt-4" role="status">
                  <span className="sr-only">Loading...</span>
                </div>
              ) : null}
            </div>

          </div>

        </div>

      </div>
      
      {/* Resend Activation Modal */}
      <Modal show={isModal} onHide={resetModals}>
        
        <Modal.Header>
          <Modal.Title>   </Modal.Title>
          <Link to="#" onClick={resetModals}>
            <i className="fa fa-times text-warning"/>
          </Link>
        </Modal.Header>

        <Modal.Body>

          <div className="mt-2">
            {loadModalErrors()}
          </div>

          <form id="mainForm" onSubmit={handleSubmit}>
            <div className="row">

              <p className="text-muted font-info mr-2">
                <i className="fa fa-star-of-life fa-xs"/>       .
              </p>

              <div className="col-sm-12 text-right">
                <label htmlFor="email" className="ml-sm-4 font-weight-bold">   : </label>
                <div className="input-group">
                  <input onChange={handleChange} required id="email" type="email" placeholder=" " className={"form-control col-sm-8 " + (modalErrors.email ? 'is-invalid' : '')}/>
                </div>
              </div>


            </div>
          </form>

          <Modal.Footer className="text-center mt-4">

            <Button variant="grey" onClick={resetModals}>
              
            </Button>
            <Button type='submit' variant="success" form="mainForm" id="activationBtn">
              
            </Button>

          </Modal.Footer>

        </Modal.Body>

      </Modal>

    </div>
  )
}