import React, { useState, useEffect } from 'react';
import PH from '../../static/images/icon.png'
import { Redirect, Link } from 'react-router-dom';

// Modals
import { Modal, Button } from 'react-bootstrap';

// Phone Input
import { getCountryCallingCode } from 'react-phone-number-input';


export const Settings = () => {

  // Basic Data
  const [basic, setBasicData] = useState([]);

  // Modal Edits
  const [modalState, setModalState] = useState();
  const [modalErrors, setModalErrors] = useState({});
  const [tokenForm, setTokenForm] = useState(false);

  // Modals Set
  const [flashModal, setFlashModal] = useState({view: false, state: null, message: null, logout: false})
  const [isProvider, setEditProvider] = useState(false);
  const [isEmail, setEditEmail] = useState(false);
  const [isPhone, setEditPhone] = useState(false);
  const [resetPasswModal, setResetPassw] = useState(false);
  const [resetPasswState, setResetPasswState] = useState({
    email: null,
    old_password: null,
    password: null,
    confirm_password: null
  })
  
  // User Information
  const [user, setUser] = useState({});
  const [userErrors, setUserErrors] = useState({});
  
  // Redirect
  const [redirect, setRedirect] = useState(false);

  // Edits
  const [state, setState] = useState({
    fullname: null,
    username: null,
    country: null,
    city: null
  })

  const [cities, setCities] = useState();

  const [errors, setErrors] = useState({});

  /*eslint-disable */
  //suppress all warnings between comments
  useEffect(() => {

    // Pre-Phone Selector
    $("pre_phone").click(function(){
      var selText = $(this).text();
      $(this).parents('.btn-group').find('.dropdown-toggle').html(selText+' <span class="caret"></span>');
    });

    // Check If User is Logged-in
    if (!localStorage.maidan_jwt_$m3){
      setRedirect(true);
    }
    
    // Fetch User Data
    fetch('/api/user', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setUser(data.user)
          setState({
            fullname: data.user.fullname,
            username: data.user.username,
            country: data.user.country,
            city: data.user.city
          })
          // Assing Basic Data
          let basicData = JSON.parse(localStorage.maidan_elements);
          let country = data.user.country;
          setBasicData(basicData);
          
          // Loop Through Data
          for (let i = 0; i < basicData.length; i++){
            // Check if Key === 'country'
            if (basicData[i].key === 'country'){
              if (basicData[i].values.country.ar === country){
                // Setting Base Cities For Current Country
                setCities(basicData[i].values.cities.ar)
              }
            }
          }
          
        } else {
          if (data.code === 'SERVER'){
            setUserErrors({
              FetchError: {
                name: '',
                error: '      ,    .',
                type: 'danger'
              }
            });
            localStorage.clear();
            window.location.reload();
          }
        }
      })
    })
    
  }, [])
  /*eslint-enable */  

  /* Changing State */
  const handleChange = (e) => {
    e.preventDefault()

    // Reset Error
    setErrors({})

    if (e.target.id === 'country'){
      for (let i = 0; i < basic.length; i++){
        if (basic[i].key === 'country'){
          if (basic[i].values.country.ar === e.target.value){
            setCities(
              basic[i].values.cities.ar
            )
          }
        }
      }
    }
    setState({
      ...state,
      [e.target.id]: e.target.value
    })
  } 

  /* Fullname */
  const submitFullname = (e) => {
    e.preventDefault();

    const UpdateFullname = async() => {
      await fetch(`/api/user/${user.id}`, {
        method: 'PATCH',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        body: JSON.stringify({fullname: state.fullname})
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            // Delete Connected Error
            let CurErrors = errors;
            delete CurErrors['fullname']
            setErrors(CurErrors)

            setUser(data.user);
            setState({
              ...state,
              [data.updated.id]: data.updated.value
            });
            document.getElementById('fullname').value = ''
          } else {
            if (data.code === 'SERVER'){
              setErrors({
                Server: {
                  name: '',
                  error: '      ,   .',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'NO_MATCH_ID'){
              setErrors({
                ID: {
                  name: ' ',
                  error: ` ${user.id}  ,       .`,
                  type: 'warning'
                }
              })
            }
            if (data.code === 'INVALID_NAME'){
              setErrors({
                fullname: {
                  name: '',
                  error: '      ,       !',
                  type: 'warning'
                }
              })
            }
            if (data.code === 'INVALID_REQUEST'){
              setErrors({
                Request: {
                  name: ' ',
                  error: '      ,    .',
                  type: 'warning'
                }
              })
            }
            if (data.code === 'MISSING_VALUES'){
              setErrors({
                name: '',
                error: '     ,  .',
                type: 'warning'
              })
            }
          }
        })
      })
    }

    UpdateFullname();
  }
  /* Fullname */

  /* Country & City */ 
  const submitLocation = (e) => {
    e.preventDefault();

    const UpdateLocation = async() => {
      await fetch(`/api/user/${user.id}`, {
        method: 'PATCH',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        body: JSON.stringify({country: state.country, city: state.city})
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            setUser(data.user);
            setState({
              ...state,
              [data.updated.country_id]: data.updated.country_value,
              [data.updated.city_id]: data.updated.city_value,
            });
          } else {
            if (data.code === 'SERVER'){
              setErrors({
                Server: {
                  name: '',
                  error: '      ,   .',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'NO_MATCH_ID'){
              setErrors({
                ID: {
                  name: ' ',
                  error: ` ${user.id}  ,       .`,
                  type: 'warning'
                }
              })
            }
            if (data.code === 'INVALID_REQUEST'){
              setErrors({
                Request: {
                  name: ' ',
                  error: '      ,    .',
                  type: 'warning'
                }
              })
            }
            if (data.code === 'MISSING_VALUES'){
              setErrors({
                name: '',
                error: '     ,  .',
                type: 'warning'
              })
            }
          }
        })
      })
    }
    UpdateLocation();
  }
  /* Country & City */ 
  

  /* Image */
  const ImageChange = (e) => {
    let fileToUpload = e.target.files
      let id = e.target.id

      if (e.target.value) {

        // Reset File Errors
        setErrors({});
        
        // Image Size Validation
        let reader = new FileReader();

        reader.readAsDataURL(fileToUpload[0]);

        reader.onload = function(e) {

          let image = new Image();
          image.src = e.target.result;

          // Validate Width & Height

          image.onload = function() {
            let height = this.height;
            let width = this.width;

            if (width < 100 || height < 75){
              setErrors({
                ...errors,
                ImageSize: {
                  name: '',
                  error: `  (${fileToUpload[0].name})  ,        (75*100)`,
                  type: 'warning'
                }
              });
              document.getElementById(id).value = ''
            } else if (width > 300 || height > 300) {
              setErrors({
                ...errors,
                ImageSize: {
                  name: '',
                  error: `     ,      (300*300)`,
                  type: 'warning'
                }
              });
              document.getElementById(id).value = ''
            }
          }

        }
      }
  }

  const submitImage = (e) => {
    e.preventDefault();
    const formData = new FormData(e.target);

      const UploadImages = async() => {
        await fetch(`/api/user/image/${user.id}`, {
          method: 'PATCH',
          body: formData
        }).then(resp => {
          resp.json().then(data => {
            if (resp.ok){
              localStorage.setItem('profile_img', data.key);
              window.location.reload();
            } else {
              document.getElementById('UploadBtn').disabled = false;
              if (data.code === 'SERVER'){
                setErrors({
                  ...errors,
                  ImageUpload: {
                    name: '',
                    error: '      ,    .',
                    type: 'danger'
                  }
                })
              }
              if (data.code === 'NO_MATCH_ID'){
                setErrors({
                  ID: {
                    name: ' ',
                    error: ` ${user.id}  ,       .`,
                    type: 'warning'
                  }
                })
              }
              if (data.code === 'INVALID_REQUEST'){
                setErrors({
                  ...errors,
                  ImageUpload: {
                    name: '',
                    error: '       .',
                    type: 'warning'
                  }
                })
              }
            }
          })
        })
      }
      document.getElementById('UploadBtn').disabled = true;
      UploadImages();
  }
  /* Image */


  /* Modals */

  // Logout User After Successful Main Change
  const LogoutUser = async() => {
    await fetch('/api/logout', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setErrors({});
          localStorage.removeItem('maidan_jwt_$m3');
          localStorage.removeItem('maidan_user');
          localStorage.removeItem('logged');
          localStorage.removeItem('maidan_user_access');
          localStorage.removeItem('profile_img');
          setRedirect(true);
        } else {
          if (data.code === 'SERVER'){
            setErrors({
              Logout: {
                name: '',
                error: '     ,     .',
                type: 'danger'
              }
            })
          }
        }
      })
    })
  }

  const handleModalChange = (e) => {
    e.preventDefault();

    // Reset Errors
    setModalErrors({});

    if (e.target.id === 'pre_phone'){
      document.getElementById('pre_phone_selector').innerHTML = e.target.name;
      setModalState({
        ...modalState,
        [e.target.id]: e.target.name
      })
    } else {
      setModalState({
        ...modalState,
        [e.target.id]: e.target.value
      })
    }
  }
  
  // Email

  const sendResetEmailToken = (e) => {
    e.preventDefault()
    
    const SendToken = async() => {
      await fetch('/api/user/send_reset_email_token', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        body: JSON.stringify({email: modalState.email})
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            setTokenForm(true);
          } else {
            if (data.code === 'SERVER'){
              setModalErrors({
                Server: {
                  name: '',
                  error: '      ,   .',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'INVALID_EMAIL'){
              setModalErrors({
                email: {
                  name: ' ',
                  error: '   ,     ',
                  type: 'warning'
                }
              })
            }
            if (data.code === 'INVALID_REQUEST'){
              setModalErrors({
                Request: {
                  name: ' ',
                  error: '      ,    .',
                  type: 'warning'
                }
              })
            }
          }
        })
      })
    }

    SendToken();
  }

  const validateResetEmailToken = (e) => {
    e.preventDefault();

    const ValidateToken = async() => {
      await fetch(`/api/user/verify_reset_email_token/${user.id}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        body: JSON.stringify({
          email: modalState.email,
          token: modalState.token
        })
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            setEditEmail(false);
            setFlashModal({
              view: true,
              logout: true,
              state: 'success',
              title: '  ',
              message: '    !,     .'
            })
          } else {
            if (data.code === 'SERVER'){
              setEditEmail(false);
              setFlashModal({
                view: true,
                state: 'failure',
                title: '  ',
                message: '      ,   .'
              })
            }
            if (data.code === 'INVALID_EMAIL'){
              setEditEmail(false);
              setFlashModal({
                view: true,
                state: 'failure',
                title: '  ',
                message: '     ,   .'
              })
            }
            if (data.code === 'INVALID_TOKEN'){
              setModalErrors({
                resetToken: {
                  name: ' ',
                  error: '    ,    ',
                  type: 'warning'
                }
              })
            }
            if (data.code === 'EXPIRED_TOKEN'){
              setEditEmail(false);
              setFlashModal({
                view: true,
                state: 'failure',
                title: '  ',
                message: '    ,    .'
              })
            }
            if (data.code === 'USED_TOKEN'){
              setModalErrors({
                resetToken: {
                  name: ' ',
                  error: '     ,    .',
                  type: 'warning'
                }
              })
            }
            if (data.code === 'INVALID_REQUEST'){
              setEditEmail(false);
              setFlashModal({
                view: true,
                state: 'failure',
                title: '  ',
                message: '     ,     !'
              })
            }
            if (data.code === 'NO_MATCH_ID'){
              setModalErrors({
                ID: {
                  name: ' ',
                  error: ` ${user.id}  ,       .`,
                  type: 'warning'
                }
              })
            }
          }
        })
      })
    }

    ValidateToken();
  }

  // Phone

  // const sendResetPhoneToken = (e) => {
  //   e.preventDefault();
  // }

  // const validateResetPhoneToken = (e) => {
  //   e.preventDefault();
  // }

  // Turn To Provider

  const ValidateNoOpenTickets = async(e) => {
    e.preventDefault();
    await fetch(`/api/user/check_tickets/${user.id}`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      },
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setEditProvider(true);
        } else {
          if (data.code === 'SERVER') {
            setFlashModal({
              view: true,
              state: 'error',
              title: ' ',
              message: '     ,     '
            });
          } else if (data.code === 'NO_MATCH_ID') {
            setFlashModal({
              view: true,
              logout: true,
              state: 'error',
              title: '',
              message: '    ,    !'
            });
          } else {
            setFlashModal({
              view: true,
              state: 'error',
              title: ' ',
              message: '      ,       .'
            });
          }
        }
      })
    })
  }

  const handleDownload = (e) => {
    e.preventDefault();

    const GetDownloadLink = async(keys) => {
      await fetch('/api/panel/files/download', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
        },
        body: JSON.stringify({keys: keys})
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            for (let i=0; i<data.download_links.length; i++){
              window.open(data.download_links[i])
            }
          } else {
            if (data.code === 'NO_KEYS'){
              setModalErrors({
                flash: {
                  type: 'warning',
                  name: ' ',
                  error: '    ,    '
                }
              })
            }
            if (data.code === 'SERVER'){
              setModalErrors({
                flash: {
                  type: 'danger',
                  name: ' ',
                  error: '     ,   '
                }
              })
            }
            if ([
              'INVALID_AUTH_HEADER', 
              'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
              'ACCESS_DENIED', 'AUTH_ERROR'
              ].includes(data.code)) {
              setModalErrors({
                flash: {
                  type: 'danger',
                  name: ' ',
                  error: '     '
                }
              })
            }
          }
        })
      })
    }

    // Get Required Files From LocalStorage
    var files = JSON.parse(localStorage.maidan_files);
    let downloads = []
    for (let i=0; i < files.length; i++){
      // Check for Providers Agreement Files
      if (files[i].key === 'providersAgreement'){
        downloads.push(files[i].file_name)
      }
    }

    // Download All Files
    GetDownloadLink(downloads);

  }

  const handleMainAdImageChange = (e) => {
    let target_file = e.target;
    let fileToUpload = target_file.files;
    let id = e.target.id;

    document.getElementById('submitBtn').disabled = true;

    if (e.target.value) {
      // Reset Image Preview On Selected Image

      // Reset File Errors
      let prev_errors = {...modalErrors};
      delete prev_errors['ImageSize']
      setModalErrors(prev_errors)
      
      // Image Size Validation
      let reader = new FileReader();

      reader.readAsDataURL(fileToUpload[0]);

      reader.onload = function(e) {

        let image = new Image();
        image.src = e.target.result;

        // Validate Width & Height

        image.onload = function() {
          let height = this.height;
          let width = this.width;

          if (width < 800 || height < 450){
            setModalErrors({
              ...errors,
              ImageSize: {
                name: '',
                error: `  (${fileToUpload[0].name})  ,        (450*800)`,
                type: 'warning'
              }
            });
            document.getElementById(id).value = ''
          } else if (width > 1280 || height > 1280) {
            setModalErrors({
              ...errors,
              ImageSize: {
                name: '',
                error: `     ,      (1280*1280)`,
                type: 'warning'
              }
            });
            document.getElementById(id).value = ''
          } else {
            document.getElementById('ImageuploadStatus').innerHTML = "   "
            document.getElementById('ImageuploadStatus').classList.remove("text-success");
            document.getElementById('ImageuploadStatus').classList.add("text-primary");
            document.getElementById('adImageLabel').innerHTML = fileToUpload[0].name;
            submitMainAdImage();
          }
        }

      }
    }
  }

  const submitMainAdImage = async() => {
    let fileToUpload = document.getElementById('adImage').files[0];
    var data = new FormData();
    data.append('adImage', fileToUpload);
   
    await fetch('/api/user/files', {
      method: 'POST',
      body: data
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          document.getElementById('ImageuploadStatus').innerHTML = "   "
          // document.getElementById('ImageuploadStatus').classList.remove('text-primary')
          document.getElementById('ImageuploadStatus').classList.add('text-success')
          setModalState({
            ...modalState,
            adImage: data.key
          })
        } else {
          document.getElementById('ImageuploadStatus').innerHTML = "  "
          // document.getElementById('ImageuploadStatus').classList.remove('text-primary')
          document.getElementById('ImageuploadStatus').classList.add('text-danger')
          if (data.code === 'NOT_LOGGED'){
            setModalErrors({
              ...errors,
              ImageUpload: {
                name: '',
                error: '    ',
                type: 'danger'
              }
            })
          }
          if (data.code === 'SERVER'){
            setModalErrors({
              ...errors,
              ImageUpload: {
                name: '',
                error: '      ,    .',
                type: 'danger'
              }
            })
          }
          if (data.code === 'NO_MATCH_ID'){
            setModalErrors({
              ID: {
                name: ' ',
                error: ` ${user.id}  ,       .`,
                type: 'warning'
              }
            })
          }
          if (data.code === 'INVALID_REQUEST'){
            setModalErrors({
              ...errors,
              ImageUpload: {
                name: '',
                error: '       .',
                type: 'warning'
              }
            })
          }
        }
      })
    })

    document.getElementById('submitBtn').disabled = false;
  }

  const handlePDFFileChange = (e) => {
    let target_file = e.target;
    let fileToUpload = target_file.files[0];

    document.getElementById('submitBtn').disabled = true;

    if (e.target.value) {
      // Reset Image Preview On Selected Image

      // Reset File Errors
      let prev_errors = {...modalErrors};
      delete prev_errors['pdfFile']
      setModalErrors(prev_errors)
      
      var filename = fileToUpload.name;
      var ex = filename.split('.').reverse()[0];
      let acceptedTypes = ['application/pdf', 'image/png', 'image/jpg'];
      let acceptedEx = ['zip', 'rar', 'pdf', 'png', 'jpg'];
      if (acceptedTypes.includes(fileToUpload.type) || acceptedEx.includes(ex)){
        document.getElementById('pdfFileLabel').innerHTML = filename;
        submitPDFFile();
        document.getElementById('PDFuploadStatus').classList.remove("text-success");
        document.getElementById('PDFuploadStatus').classList.add("text-primary");
        document.getElementById('PDFuploadStatus').innerHTML = "   "
      } else {
        setModalErrors({
          pdfFile: {
            name: '',
            error: '     ,       (zip/rar/pdf/png/jpg)',
            type: 'danger'
          }
        })
      }
      
    }
  }

  const submitPDFFile = async() => {
    let fileToUpload = document.getElementById('pdfFile').files[0];
    var data = new FormData();
    data.append('formPDF', fileToUpload)
    
    await fetch('/api/user/files', {
      method: 'POST',
      body: data
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          document.getElementById('PDFuploadStatus').innerHTML = "   "
          // document.getElementById('PDFuploadStatus').classList.remove('text-primary')
          document.getElementById('PDFuploadStatus').classList.add('text-success')
          setModalState({
            ...modalState,
            pdfFile: data.key
          })
        } else {
          document.getElementById('PDFuploadStatus').innerHTML = "  "
          // document.getElementById('PDFuploadStatus').classList.remove('text-primary')
          document.getElementById('PDFuploadStatus').classList.add('text-danger')
          if (data.code === 'NOT_LOGGED'){
            setModalErrors({
              ...errors,
              ImageUpload: {
                name: '',
                error: '    ',
                type: 'danger'
              }
            })
          }
          if (data.code === 'SERVER'){
            setModalErrors({
              ...errors,
              ImageUpload: {
                name: '',
                error: '      ,    .',
                type: 'danger'
              }
            })
          }
          if (data.code === 'NO_MATCH_ID'){
            setModalErrors({
              ID: {
                name: ' ',
                error: ` ${user.id}  ,       .`,
                type: 'warning'
              }
            })
          }
          if (data.code === 'INVALID_REQUEST'){
            setModalErrors({
              ...errors,
              ImageUpload: {
                name: '',
                error: '       .',
                type: 'warning'
              }
            })
          }
        }
      })
    })

    document.getElementById('submitBtn').disabled = false;
  }

  const handleProviderChange = (e) => {
    e.preventDefault();

    setModalState({
      ...modalState,
      [e.target.id]: e.target.value
    })
  }

  const handleProviderSubmit = (e) => {
    e.preventDefault();

    const SubmitTicket = async() => {
      await fetch(`/api/user/upgrade_to_provider/${user.id}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        body: JSON.stringify(modalState)
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            setEditProvider(false);
            setFlashModal({
              view: true,
              state: 'success',
              title: ' ',
              message: '   ,    .'
            });
          } else {
            if (data.code === 'SERVER'){
              setEditProvider(false);
              setFlashModal({
                view: true,
                state: 'error',
                title: ' ',
                message: '     ,     '
              });
            }
            if (data.code === 'NO_MATCH_ID'){
              setEditProvider(false);
              setFlashModal({
                view: true,
                logout: true,
                state: 'error',
                title: '',
                message: '    ,    !'
              });
            }
            if (data.code === 'OPEN_TICKET'){
              setEditProvider(false);
              setFlashModal({
                view: true,
                state: 'error',
                title: ' ',
                message: '      ,       .'
              });
            }
            if (data.code === 'INVALID_REQUEST'){
              setModalErrors({
                Request: {
                  name: '',
                  error: '     ,     ',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'MISSING_VALUES'){
              setModalErrors({
                Values: {
                  name: '',
                  error: '     ,    ',
                  type: 'warning'
                }
              })
            }
          }
        })
      })
    }

    SubmitTicket();
  }

  /* Reset Password */

  const handleResetPasswChange = (e) => {
    e.preventDefault();
    // Set Reset State
    setResetPasswState({
      ...resetPasswState,
      [e.target.id]: e.target.value
    });
    // Reset Errors State
    setModalErrors({});
  }

  const handleResetPasswSubmit = (e) => {
    e.preventDefault();

    const ResetPassword = async() => {
      await fetch(`/api/user/${user.id}/resetPassword`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
        },
        body: JSON.stringify(resetPasswState)
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            resetModals();
            setFlashModal({
              view: true,
              logout: true,
              state: 'success',
              title: '  ',
              message: '    !,     .'
            });
          } else {
            if (data.code === 'INVALID_REQUEST'){
              setModalErrors({
                Request: {
                  name: "",
                  error: "   ",
                  type: "danger"
                }
              })

            }
            if (data.code === 'MISSING_VALUES'){
              setModalErrors({
                Values: {
                  name: "",
                  error: "   ,    .",
                  type: "warning"
                }
              })
            }
            if (data.code === 'WRONG_PASSWORD'){
              setModalErrors({
                old_password: {
                  name: "  ",
                  error: "    ,      .",
                  type: "danger"
                }
              })
            }
            if (data.code === 'NO_USER'){
              resetModals();
              setFlashModal({
                view: true,
                logout: true,
                state: 'error',
                title: '  .',
                message: '  ,      ,    .'
              });
            }
            if (data.code === 'SERVER'){
              setModalErrors({
                error: {
                  name: "",
                  error: "  ,     .",
                  type: "danger"
                }
              })
              
            }
            
          }
        })
      })

    }

    if (resetPasswState.password.length < 8){
      setModalErrors({
        ...modalErrors,
        password: {
          name: ' ',
          error: '      8   .',
          type: 'warning'
        }
      })
    } 
    else if (resetPasswState.password !== resetPasswState.confirm_password) {
      setModalErrors({
        ...modalErrors,
        password: {
          name: ' ',
          error: " ' '     '  '.",
          type: 'warning'
        }
      })
    } 
    else {
      ResetPassword();
    }
  }

  /* Modals */


  const loadEditForm = () => {

    return(

      <div className="container">

        <div className="row justify-content-center">
          
          <form onSubmit={submitFullname} className="col-sm-10 text-right">
            <label htmlFor="fullname" className="font-weight-bold text-right">
              <i className="fa fa-font ml-2"/>
                : 
            </label>
            <div className="input-group">
              <input onChange={handleChange} required id="fullname" type="text" placeholder={user.fullname || " "} className={"form-control " + (errors['fullname'] ? 'is-invalid' : '')}/>
              <div className="input-group-append">
                <button type="submit" className="form-control btn-md btn-outline-info"></button>
              </div>
            </div>
          </form>

        </div>

        <hr/>

        <div className="row justify-content-center">
          
          <form onSubmit={submitLocation} className="col-sm-12 text-right">
            
            <div className="row justify-content-center">
            
              {/* <div className="form-group col-sm-6">
                <label htmlFor="country" className="font-weight-bold text-right">
                  <i className="fa fa-globe-africa ml-2"/>
                   {user.country ? `(${user.country})` : null}: 
                </label>
                <div className="input-group">
                  <select onChange={handleChange} required id="country" className="custom-select">
                    <option value="" defaultValue> </option>
                    {basic.map((data, index) => {
                      if (data.key === 'country'){
                        return(
                          <option key={index} value={data.values.country.ar}>{data.values.country.ar}</option>
                        )
                      }
                    })}
                  </select>
                </div>
              </div> */}

              <div className="col-sm-4">
                <p className="font-weight-bold">
                  <i className="fa fa-globe-africa ml-2"/>  :
                </p>
                <p className="font-weight-bold text-muted mr-3">
                  {user.country}
                </p>
              </div>

              <div className="form-group col-sm-6">
                <label htmlFor="city" className="font-weight-bold text-right">
                  <i className="fa fa-city ml-2"/>
                   {user.city ? `(${user.city})` : null}: 
                </label>
                <div className="input-group">
                  <select onChange={handleChange} required id="city" className="custom-select">
                    <option value="" defaultValue> </option>
                    {cities ? cities.map((city, index) => (
                      <option value={city} key={index}>{city}</option>
                    )) : null}
                  </select>
                  <div className="input-group-append">
                    <button type="submit" className="form-control btn-md btn-outline-info"></button>
                  </div>
                </div>
              </div>

            </div>

            {/* <div className="input-group justify-content-right">
              <button type="submit" className="btn btn-md btn-outline-info font-weight-bold"></button>
            </div> */}

          </form>

        </div>

        <hr/>

        <div className="row justify-content-center">

          <form onSubmit={submitImage} encType="multipart/form-data" className="col-sm-12 text-right">
              
            <label htmlFor="profile_img" className="font-weight-bold float-right">
              <i className="fa fa-image ml-2"/>
                (300*300  ) : 
            </label>
            <div className="input-group">
              
              <div className="custom-file">
                <input onChange={ImageChange} type="file" className="custom-file-input col-sm-12" id="profile_img" name="profile_img" accept="image/*"/>
                <label className="custom-file-label col-sm-12" htmlFor="profile_img"/>
              </div>
        
            </div>

            <div className="input-group justify-content-right mt-2">
              <button id="UploadBtn" type="submit" className="btn btn-md btn-outline-grey font-weight-bold"> </button>
            </div>

          </form>

        </div>

        <hr/>

        <div className="row mt-5">

          <div className="col-sm-12">
            <span className="display-8 text-danger">  : </span>
          </div>

          <div className="card mt-4 col-sm-12 bg-light-grey border-danger shadow-none p-4">
            
            <div className="row ">

              <div className="input-group justify-content-center col-sm-12">
                <button onClick={() => {setEditEmail(true)}} className="btn btn-md col-sm-6 btn-outline-primary font-weight-bold">
                    
                </button>
              </div>

              {/* <div className="input-group justify-content-center col-sm-12">
                <button onClick={() => {setEditPhone(true)}} className="btn btn-md col-sm-6 btn-outline-primary font-weight-bold">
                    
                </button>
              </div> */}

              {
                localStorage.maidan_user_access === 'PROVIDER' ? null : (
                  <div className="input-group justify-content-center col-sm-12">
                    <button onClick={ValidateNoOpenTickets} className="btn btn-md col-sm-6 btn-outline-grey font-weight-bold">
                         
                    </button>
                  </div>
                )
              }

              <div className="input-group justify-content-center col-sm-12">
                <button onClick={() => {setResetPassw(true)}} className="btn btn-md col-sm-6 btn-outline-secondary font-weight-bold">
                    
                </button>
              </div>

              <div className="input-group justify-content-center col-sm-12">
                <button className="btn btn-md col-sm-6 btn-danger font-weight-bold">
                   
                </button>
              </div>

            </div>

          </div>

        </div>

      </div>

    )
  }

  const loadUserErrors = () => {
    if (Object.keys(userErrors).length !== 0 && userErrors.constructor === Object){
      return (
        <div>
          {userErrors ? Object.entries(userErrors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  const loadErrors = () => {
    if (Object.keys(errors).length !== 0 && errors.constructor === Object){
      return (
        <div>
          {errors ? Object.entries(errors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  const loadModalErrors = () => {
    if (Object.keys(modalErrors).length !== 0 && modalErrors.constructor === Object){
      return (
        <div>
          {modalErrors ? Object.entries(modalErrors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  const resetModals = () => {
    // Reset State & Errors
    setModalState();
    setModalErrors({});

    // Set Modals To False
    setEditProvider(false);
    setEditEmail(false);
    setEditPhone(false);
    setResetPassw(false);

    setResetPasswState({
      email: null,
      old_password: null,
      password: null,
      confirm_password: null
    });
    setFlashModal({view: false, state: null, message: null});

  }

  const resetAndLogout = () => {
    resetModals();
    LogoutUser();
  }

  if (redirect){
    return <Redirect to="/"/>
  }

  return(
    <div className="mb-5 pb-5 pt-5">
      
      <div className="container mt-5 pt-5">
        
        <div className="row justify-content-center">
          <div className="col-sm-10">
            
            <div className="card bg-light-grey shadow-sm p-4">
              <span className="display-7 card-title text-right pr-5">   : </span>
            
              <div className="card-body text-center">
                
                <div className="mt-2">
                  {loadUserErrors()}
                </div>
                
                <div className="row mt-4">

                  <div className="col-sm-12">
                    <img src={user.profile_img ? (`https://s3.us-east-2.amazonaws.com/maidancom/${localStorage.profile_img}`) : PH} alt="PI" width="125" height="125" className="rounded-circle"/>
                  </div>

                  <div className="col-sm-12">
                    <span className="display-8 card-text-head text-info">{user.username}</span>
                  </div>

                </div>

                <div className="mt-2">
                  {loadErrors()}
                </div>

                <div className="mt-5">
                  {loadEditForm()}
                </div>
              
              </div>

            </div>
            
          </div>
        </div>

      </div>
      
      {/* Change Email Modal */}
      <Modal show={isEmail} onHide={resetModals}>
        
        <Modal.Header>
          <Modal.Title>  </Modal.Title>
          <Link to="#" onClick={resetModals}>
            <i className="fa fa-times text-warning"/>
          </Link>
        </Modal.Header>

        <Modal.Body>

          <div className="mt-2">
            {loadModalErrors()}
          </div>

          <form onSubmit={sendResetEmailToken}>
            <div className="row">

              <p className="text-muted font-info mr-2">
                <i className="fa fa-star-of-life fa-xs"/>        .
              </p>

              <div className="col-sm-12 text-right">
                <label htmlFor="email" className="ml-sm-4 font-weight-bold">   : </label>
                <div className="input-group">
                  <input onChange={handleModalChange} disabled={tokenForm} required id="email" type="email" placeholder=" " className={"form-control col-sm-8 " + (modalErrors.email ? 'is-invalid' : '')}/>
                </div>
              </div>

              <div className="input-group mr-2">
                <button disabled={tokenForm} className="btn btn-md btn-outline-primary font-weight-bold">  </button>
              </div>


            </div>
          </form>
          
          <div className="row mt-2">
            
            <div className={tokenForm ? 'col-sm-12' : 'd-none'}>
              <div className={"text-right mt-2"}>
              
              <label htmlFor="token" className="ml-sm-4 font-weight-bold">  : </label>
                <div className="input-group">
                  <input onChange={handleModalChange} required id="token" type="text" placeholder=" " className={"form-control col-sm-8 " + (modalErrors.resetToken ? 'is-invalid' : '')}/>
                </div>
              </div>
            </div>
          
          </div>

          <Modal.Footer className="mt-4">

            <Button variant="grey" onClick={resetModals}>
              
            </Button>
            <Button onClick={validateResetEmailToken} variant="success" disabled={tokenForm ? false : true}>
              
            </Button>

          </Modal.Footer>

        </Modal.Body>

      </Modal>

      {/* Change Phone Modal */}
      <Modal show={isPhone} onHide={resetModals}>
        
        <Modal.Header>
          <Modal.Title>  </Modal.Title>
          <Link to="#" onClick={resetModals}>
            <i className="fa fa-times text-warning"/>
          </Link>
        </Modal.Header>

        <Modal.Body>

          <div className="mt-2">
            {loadModalErrors()}
          </div>

          <form>
            <div className="row">

              <p className="text-muted font-info mr-2">
                <i className="fa fa-star-of-life fa-xs"/>        .
              </p>

              <div className="col-sm-12 text-right">
                <label htmlFor="phone" className="ml-sm-4 font-weight-bold">   : </label>
                <div className="input-group">

                  <input onChange={handleModalChange} dir='ltr' required id="phone" type="text" placeholder="05" className={"form-control col-sm-6 " + (modalErrors.phone ? "is-invalid" : '')}/>
                  
                  <div className="btn-group">
                    <Link dir='ltr' id="pre_phone_selector" className={"form-control btn-white dropdown-toggle " + (modalErrors.pre_phone ? 'is-invalid' : '')} data-toggle="dropdown" to="#"></Link>
                    <ul className="dropdown-menu font-weight-bold">
                      {
                        basic.map((data, index) => {
                          if (data.key === 'country'){
                            return(
                              <li key={index}>
                                <Link to="#" className="text-dark ml-2" id="pre_phone" name={`+${data.values.telecode}`} onClick={handleModalChange}>
                                  {getCountryCallingCode(data.label)}+ <img src={`http://catamphetamine.gitlab.io/country-flag-icons/3x2/${data.label}.svg`} width="24" alt={data.label}/>
                                </Link>
                              </li>
                            )
                          }
                        })
                      }
                    </ul>
                  </div>

                </div>
              </div>

              <div className="input-group mr-2">
                <button disabled={tokenForm} className="btn btn-md btn-outline-primary font-weight-bold">  </button>
              </div>


            </div>
          </form>
          
          <div className="row mt-2">
            
            <div className={tokenForm ? 'col-sm-12' : 'd-none'}>
              <div className={"text-right mt-2"}>
              
              <label htmlFor="token" className="ml-sm-4 font-weight-bold">  : </label>
                <div className="input-group">
                  <input onChange={handleModalChange} required id="token" type="text" placeholder=" " className={"form-control col-sm-8 " + (modalErrors.resetToken ? 'is-invalid' : '')}/>
                </div>
              </div>
            </div>
          
          </div>

          <Modal.Footer className="mt-4">

            <Button variant="grey" onClick={resetModals}>
              
            </Button>
            <Button variant="success" disabled={tokenForm ? false : true}>
              
            </Button>

          </Modal.Footer>

        </Modal.Body>

      </Modal>

      {/* Ask To Be A Provider Modal */}
      <Modal show={isProvider} onHide={resetModals}>
        
        <Modal.Header>
          <Modal.Title>   </Modal.Title>
          <Link to="#" onClick={resetModals}>
            <i className="fa fa-times text-warning"/>
          </Link>
        </Modal.Header>

        <Modal.Body>

          <div className="mt-2">
            {loadModalErrors()}
          </div>

          <div className="container mt-4">

            <form id="mainForm" onSubmit={handleProviderSubmit}>

              <div className="container">

                <div className="row text-right">

                  <div className="col-sm-12">
                    <label htmlFor="company_name" className="font-weight-bold float-right">*   : </label>
                    <div className="input-group">
                      <input onChange={handleProviderChange} required id="company_name" type="text" placeholder=": , .." className="form-control col-sm-12"/>
                    </div>
                  </div>

                  <div className="col-sm-12 mt-2">
            
                    <label htmlFor="country" className="font-weight-bold float-right">*  : </label>
                    <div className="input-group">
                      <select required onChange={handleProviderChange} id="country" className="custom-select col-sm-6 font-weight-bold">
                        <option className="font-weight-bold" value="" defaultValue> </option>
                        {basic.map((data, index) => {
                          if (data.key === 'country'){
                            return (
                              <option key={index} value={data.values.country.ar}>{data.values.country.ar}</option>
                            )
                          }
                        })}
                      </select>
                    </div>
                  
                  </div>

                </div>

              </div>

            </form>

            <form encType="multipart/form-data">

              <div className="container">

                <div className="row text-right">

                  <div className="col-sm-12 mt-4">
                    <p className="text-dark font-info font-weight-bold">
                         <Link to="#" onClick={handleDownload}>   </Link>    .
                    </p>
                  </div>

                  <div className="form-group col-sm-12">
                    <label htmlFor="pdfFile" className="font-weight-bold float-right">*   : </label>
                    <div className="custom-file">
                      <input required type="file" onChange={handlePDFFileChange} id="pdfFile" name="pdfFile" className={"custom-file-input " + (modalErrors['pdfFile'] ? 'is-invalid' : '')}/>
                      <label id="pdfFileLabel" className="custom-file-label col-sm-12" htmlFor="pdfFile"/>
                    </div>
                    <p className="font-info text-muted">   ,     zip  rar</p>
                    <div className="text-center mt-1">
                      <p id="PDFuploadStatus" className="font-info text-primary"></p>
                    </div>
                  </div>

                </div>

              </div>

            </form>

            <form encType="multipart/form-data">
              <div className="container">

                <div className="row text-right">

                  <div className="form-group col-sm-12">
                    <label htmlFor="adImage" className="font-weight-bold float-right">   (): </label>
                    <div className="custom-file">
                      <input onChange={handleMainAdImageChange} type="file" id="adImage" name="adImage" accept="image/*" className={"custom-file-input " + (modalErrors['ImageSize'] ? 'is-invalid' : '')}/>
                      <label id="adImageLabel" className="custom-file-label col-sm-12" htmlFor="adImage"/>
                    </div>
                    <div className="text-center">
                      <p id="ImageuploadStatus" className="font-info text-primary"></p>
                    </div>
                  </div>

                </div>

              </div>

            </form>

          </div>

          <Modal.Footer className="mt-4">

            <Button variant="grey" onClick={resetModals}>
              
            </Button>
            <Button id="submitBtn" type="submit" variant="success" form="mainForm">
              
            </Button>

          </Modal.Footer>

        </Modal.Body>

      </Modal>
      
      {/* Reset Password Modal */}
      <Modal show={resetPasswModal} onHide={resetModals}>
        
        <Modal.Header>
          <Modal.Title>   </Modal.Title>
          <Link to="#" onClick={resetModals}>
            <i className="fa fa-times text-warning"/>
          </Link>
        </Modal.Header>

        <Modal.Body>
          <form onSubmit={handleResetPasswSubmit}>
            
            <div className="mt-2">
              {loadModalErrors()}
            </div>
            
            <div className="row">

              <div className="col-sm-12 mt-2 text-right">
                <label htmlFor="old_password" className="ml-sm-4 font-weight-bold">   : </label>
                <div className="input-group">
                  <input onChange={handleResetPasswChange} required id="old_password" type="password" placeholder="  " className={"form-control col-sm-8 " + (modalErrors.old_password ? 'is-invalid' : '')}/>
                </div>
              </div>

              <div className="col-sm-12 mt-2 text-right">
                <label htmlFor="password" className="ml-sm-4 font-weight-bold">   : </label>
                <div className="input-group">
                  <input onChange={handleResetPasswChange} required id="password" type="password" placeholder="  " className={"form-control col-sm-8 " + (modalErrors.password ? 'is-invalid' : '')}/>
                </div>
              </div>

              <div className="col-sm-12 mt-2 text-right">
                <label htmlFor="confirm_password" className="ml-sm-4 font-weight-bold">    : </label>
                <div className="input-group">
                  <input onChange={handleResetPasswChange} required id="confirm_password" type="password" placeholder="  " className={"form-control col-sm-8 " + (modalErrors.password ? 'is-invalid' : '')}/>
                </div>
              </div>

            </div>

            <Modal.Footer className="mt-4">

              <Button variant="grey" onClick={resetModals}>
                
              </Button>
              <Button variant="primary" type="submit">
                 
              </Button>

            </Modal.Footer>
          
          </form>
        </Modal.Body>

      </Modal>
      
      {/* Flash State Modal */}
      <Modal show={flashModal.view} onHide={() => {flashModal.logout ? resetAndLogout() : resetModals()}}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">{flashModal.title}</Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">
          {flashModal.state === 'success' ? (
            <i className="fa fa-check-circle text-success fa-3x"/>
          ) : flashModal.state === 'error' ? (
            <i className="fa fa-times-circle text-danger fa-3x"/>
          ) : null}
          <p className="mt-2 lead text-muted">
            {flashModal.message}
          </p>
        </Modal.Body>
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="outline-primary" onClick={() => {flashModal.logout ? resetAndLogout() : resetModals()}}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>
   
    </div>
  )
}