import React, { useEffect, useState } from 'react';
import { Redirect, Link } from 'react-router-dom';



export const Notifications = () => {

  const [notifs, setNotifs] = useState({notifs: [], limit: 15});

  // Redirect
  const [redirect, setRedirect] = useState({
    redirect: false, to: null, flash: null
  });

  const [errors, setErrors] = useState({});

  useEffect(() => {
    fetch('/api/notifications', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok) {
          setNotifs({
            notifs: data.notifications,
            limit: 15
          });
        } else {
          if (data.code === 'NO_USER') {
            setRedirect({
              redirect: true,
              to: '/',
              flash: {
                type: 'error',
                title: '',
                message: '     .'
              }
            })
          }
        }
      })
    })
  }, []);

  
  const loadNotifs = () => {
    const getDate = (seconds) => {
      var date = new Date(1970, 0, 1);
      date.setSeconds(seconds);
      var sloppy_date = new Date(date);
      var tzOffset = sloppy_date.getTimezoneOffset();
      
      var target_date = new Date(sloppy_date.getTime() - tzOffset * 60 * 1000);
  
      var yy = target_date.getFullYear();
      var mm = target_date.getMonth() + 1;
      var dd = target_date.getDate();
  
      var h = target_date.getHours();
      var m = target_date.getMinutes();
  
      return `${yy}-${(mm>9?'':'0')+mm}-${(dd>9?'':'0')+dd} ${(h>9?'':'0')+h}:${(m>9?'':'0')+m}`
    }

    const notifs_patch = notifs.notifs && notifs.notifs.slice(0, notifs.limit);

    return (
      <tbody>
        {
          notifs_patch ? notifs_patch.map((notif, index) => {
            return(
              <tr className={notif.viewed ? "bg-light-grey" : ""} key={index}>
                <th className="table-header text-center">{index+1}</th>
                <td className="table-header">{notif.notification}</td>
                <td dir='ltr' className="table-header">{getDate(notif.date)}</td>
                <td className="table-header text-center">
                  {
                    notif.viewed ? (
                      <Link className="qsemh-orange" to="#" onClick={() => {markAsUnRead(notif.id)}}>
                        <i title="  " className="fa fa-eye-slash shadow-text-sm"/>
                      </Link>
                    ) : (
                      <Link className="qsemh-orange" to="#" onClick={() => {markAsRead(notif.id)}}>
                        <i title=" " className="fa fa-eye shadow-text-sm"/>
                      </Link>
                    )
                  }
                </td>
                <td className="table-header text-center">
                  <Link className="text-danger" to="#" onClick={() => {Delete(notif.id)}}>
                    <i title=" " className="fa fa-trash-alt"/>
                  </Link>
                </td>
              </tr>
            )
          }) : null
        }
      </tbody>
    )

  }

  const markAsRead = async(id) => {
    await fetch(`/api/notifications/${id}/read`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok) {
          window.location.reload();
        } else {
          if (data.code === 'NO_USER') {
            setRedirect({
              redirect: true,
              to: '/',
              flash: {
                type: 'error',
                title: '',
                message: '     .'
              }
            })
          }
          if (data.code === "SERVER") {
            setErrors({
              Server: {
                name: "",
                error: ' ,     .'
              }
            })
          }
          if (data.code === 'NOT_YOURS') {
            setErrors({
              Server: {
                name: "",
                error: '      .'
              }
            })
          }
          if (data.code === 'NO_MATCH_ID') {
            setErrors({
              Server: {
                name: "",
                error: '      ,    .'
              }
            })
          }
        }
      })
    })
  }
  
  const markAsUnRead = async(id) => {
    await fetch(`/api/notifications/${id}/unread`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok) {
          window.location.reload();
        } else {
          if (data.code === 'NO_USER') {
            setRedirect({
              redirect: true,
              to: '/',
              flash: {
                type: 'error',
                title: '',
                message: '     .'
              }
            })
          }
          if (data.code === "SERVER") {
            setErrors({
              Server: {
                name: "",
                error: ' ,     .'
              }
            })
          }
          if (data.code === 'NOT_YOURS') {
            setErrors({
              Server: {
                name: "",
                error: '      .'
              }
            })
          }
          if (data.code === 'NO_MATCH_ID') {
            setErrors({
              Server: {
                name: "",
                error: '      ,    .'
              }
            })
          }
        }
      })
    })
  }

  const Delete = async(id) => {
    await fetch(`/api/notifications/${id}/delete`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok) {
          window.location.reload();
        } else {
          if (data.code === 'NO_USER') {
            setRedirect({
              redirect: true,
              to: '/',
              flash: {
                type: 'error',
                title: '',
                message: '     .'
              }
            })
          }
          if (data.code === "SERVER") {
            setErrors({
              Server: {
                name: "",
                error: ' ,     .'
              }
            })
          }
          if (data.code === 'NOT_YOURS') {
            setErrors({
              Server: {
                name: "",
                error: '      .'
              }
            })
          }
          if (data.code === 'NO_MATCH_ID') {
            setErrors({
              Server: {
                name: "",
                error: '      ,    .'
              }
            })
          }
        }
      })
    })
  }

  const markAllAsRead = async() => {
    await fetch(`/api/notifications/all/read`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok) {
          window.location.reload();
        } else {
          if (data.code === 'NO_USER') {
            setRedirect({
              redirect: true,
              to: '/',
              flash: {
                type: 'error',
                title: '',
                message: '     .'
              }
            })
          }
          if (data.code === "SERVER") {
            setErrors({
              Server: {
                name: "",
                error: ' ,     .'
              }
            })
          }
          if (data.code === 'NOT_YOURS') {
            setErrors({
              Server: {
                name: "",
                error: '      .'
              }
            })
          }
          if (data.code === 'NO_MATCH_ID') {
            setErrors({
              Server: {
                name: "",
                error: '      ,    .'
              }
            })
          }
        }
      })
    })
  }

  const DeleteAll = async() => {
    await fetch(`/api/notifications/all/delete`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok) {
          window.location.reload();
        } else {
          if (data.code === 'NO_USER') {
            setRedirect({
              redirect: true,
              to: '/',
              flash: {
                type: 'error',
                title: '',
                message: '     .'
              }
            })
          }
          if (data.code === "SERVER") {
            setErrors({
              Server: {
                name: "",
                error: ' ,     .'
              }
            })
          }
          if (data.code === 'NOT_YOURS') {
            setErrors({
              Server: {
                name: "",
                error: '      .'
              }
            })
          }
          if (data.code === 'NO_MATCH_ID') {
            setErrors({
              Server: {
                name: "",
                error: '      ,    .'
              }
            })
          }
        }
      })
    })
  }

  const AddLimit = () => {
    if (notifs.limit < notifs.notifs.length){
      let new_limit = notifs.limit + 50;
      setNotifs({...notifs, limit: new_limit})
    }
  }

  const ReduceLimit = () => {
    if (notifs.limit > 50){
      let new_limit = notifs.limit - 50;
      setNotifs({...notifs, limit: new_limit})
    }
  }

  const loadErrors = () => {
    if (Object.keys(errors).length !== 0 && errors.constructor === Object){
      return (
        <div>
          {errors ? Object.entries(errors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }


  if (redirect.redirect){
    return <Redirect to={{
      pathname: redirect.to,
      state: {flash: redirect.flash}
    }}/>
  }

  return (
    <div className="mb-5 pb-5 pt-5">

      <div className="container text-center pb-5 mt-5 pt-5">
        <span className="display-7 qsemh-turq"></span>
        <hr/>
        <div className="containet mt-2">
          {loadErrors()}
        </div>
        <div className="row mt-4 text-right">
          <div className="col-sm-10">
            <div className="table-responsive mt-4">
          
              <table className="table table-sm table-bordered">
                <thead>
                  <tr className="bg-light-grey">
                    <th className="font-weight-bold text-center" scope="col">#</th>
                    <th className="font-weight-bold" scope="col"></th>
                    <th className="font-weight-bold" scope="col"> </th>
                    <th className="font-weight-bold text-center" scope="col">
                      <Link className="qsemh-turq" to="#" onClick={markAllAsRead}>
                        <i title="  " className="fa fa-eye shadow-text-sm fa-lg"/>
                      </Link>
                    </th>
                    <th className="font-weight-bold text-center" scope="col">
                      <Link className="qsemh-turq" to="#" onClick={DeleteAll}>
                        <i title=" " className="fa fa-trash-alt shadow-text-sm fa-lg"/>
                      </Link>
                    </th>
                  </tr>
                </thead>
                {loadNotifs()}
              </table>
              <div className="justify-content-center">
                {(notifs.groups && notifs.limit < notifs.notifs.length) ? (<button onClick={AddLimit} className="btn btn-sm btn-outline-primary"> </button>) : null}
                {notifs.limit > 15 ? (<button onClick={ReduceLimit} className="btn btn-sm btn-outline-secondary"> </button>) : null}
              </div>

            </div>
          </div>
          <div className="col-sm-2 mt-4">
            <div className="card shadow-none bg-light-grey">
              <div className="card-body font-smr">
                <p>   .</p>
                <p>  .</p>
              </div>
            </div>
          </div>
        </div>
        
      </div>

    </div>
  )
}