import React, { useState, useEffect } from 'react';
import { Link, Redirect } from 'react-router-dom';
// Modals
import { Modal, Button } from 'react-bootstrap';


const MyGroups = () => {

  // User
  const userData = localStorage.maidan_user
  const [User, setUser] = useState(userData ? {...JSON.parse(userData)} : {});

  const [groups, setGroups] = useState({groups: null, limit: 50});
  const [associates, setAssociates] = useState({users: null, limit: 50});
  const [coupons, setCoupons] = useState({coupons: null, limit: 50});
  
  const [errors, setErrors] = useState({});

  const [redirect, setRedirect] = useState({redirect: false, flash: null})

  // Modals
  const [deleteModal, setDeleteModal] = useState({view: false, group_id: null});
  const [settingsModal, setSettingsModal] = useState(false);
  const [settingState, setSettingState] = useState({});
  const [imagesModal, setImagesModal] = useState(false);
  const [imageState, setImageState] = useState([]);
  
  const [removeAssoc, setRemoveAssoc] = useState({view: false, id: null});
  const [associationModal, setAccosiationModal] = useState({view: false, user: null});
  const [searchUsers, setSearchUsers] = useState({});


  const [balanceModal, setBalanceModal] = useState({view: false, user_balance: null});
  
  const [imgPreviewModal, setImgPreviewModal] = useState({view: false, image: null});
  const [imgDeleteModal, setImgDeleteModal] = useState({view: false, image: null});

  /*eslint-disable */
  //suppress all warnings between comments
  useEffect(() => {
    fetch(`/api/panel/user/groups/${User.uid}`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setGroups({
            groups: data.groups,
            limit: 50
          });
          setAssociates({
            users: data.associates,
            limit: 50
          });
          setCoupons({
            coupons: data.coupons,
            limit: 50
          });
        } else {
          if (data.code === 'AUTH_ERROR'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: ' ',
                message: '     '
              }
            })
          }
          if (data.code === 'ACCESS_DENIED'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: ' ',
                message: '       .'
              }
            })
          }
          if (data.code === 'INVALID_ACCESS_LEVEL'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '     ,    .'
              }
            })
          }
          if (data.code === 'SERVER'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      ,     '
              }
            })
          }
          if (data.code === 'NO_TOKEN'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '    ,       '
              }
            })
          }
          if (data.code === 'INVALID_AUTH_HEADER'){
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '     ,    .'
              }
            })
          }
          if (data.code === 'NOT_LOGGED_IN'){
            localStorage.clear();
            sessionStorage.clear();
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '        .'
              }
            })
          }
        }
      })
    })
  }, [])
  /*eslint-enable */

  const handleChange = (e) => {
    e.preventDefault();
    setSettingState({
      ...settingState,
      [e.target.id]: e.target.value
    })
  }

  const handleSubmit = (e) => {
    e.preventDefault();

    const Update = async() => {
      await fetch(`/api/panel/user/groups/${User.uid}/${settingState.id}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
        },
        body: JSON.stringify({coupons_plus: settingState.coupons_plus})
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            setGroups({
              ...groups,
              groups: data.groups,
            });
            resetModals();
          } else {
            if (data.code === 'AUTH_ERROR'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: ' ',
                  message: '     '
                }
              })
            }
            if (data.code === 'ACCESS_DENIED'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: ' ',
                  message: '       .'
                }
              })
            }
            if (data.code === 'INVALID_ACCESS_LEVEL'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '     ,    .'
                }
              })
            }
            if (data.code === 'NO_TOKEN'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '    ,       '
                }
              })
            }
            if (data.code === 'INVALID_AUTH_HEADER'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '     ,    .'
                }
              })
            }
            if (data.code === 'NOT_LOGGED_IN'){
              localStorage.clear();
              sessionStorage.clear();
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '        .'
                }
              })
            }
            if (data.code === 'NO_MATCH_ID'){
              setErrors({
                ID: {
                  name: ' ',
                  error: '       .',
                  type: 'warning'
                }
              })
            }
            if (data.code === 'NOT_YOURS'){
              setErrors({
                Company: {
                  name: ' ',
                  error: '       .',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'SERVER'){
              setErrors({
                Server: {
                  name: '',
                  error: '      ,   .',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'EXPIRED'){
              setErrors({
                Expired: {
                  name: ' ',
                  error: '    .',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'INVALID_REQUEST'){
              setErrors({
                coupons_plus: {
                  name: ' ',
                  error: '     ',
                  type: 'danger'
                }
              })
            }
          }
        })
      })
    }

    const checkBalance = async() => {
      await fetch("/api/groups/balance", {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
        },
        body: JSON.stringify({full_price: settingState.coupon_price*(settingState.coupons_num+parseInt(settingState.coupons_plus))})
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
  
            if (data.eligible) {
              Update();
            } else {
              setBalanceModal({view: true, user_balance: data.user_balance});
            }
  
          } else {
            if (data.code === 'AUTH_ERROR'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: ' ',
                  message: '     '
                }
              })
            }
            if (data.code === 'ACCESS_DENIED'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: ' ',
                  message: '       .'
                }
              })
            }
            if (data.code === 'INVALID_ACCESS_LEVEL'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '     ,    .'
                }
              })
            }
            if (data.code === 'SERVER'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '      ,     '
                }
              })
            }
            if (data.code === 'NO_TOKEN'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '    ,       '
                }
              })
            }
            if (data.code === 'INVALID_AUTH_HEADER'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '     ,    .'
                }
              })
            }
            if (data.code === 'NOT_LOGGED_IN'){
              localStorage.clear();
              sessionStorage.clear();
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '        .'
                }
              })
            }
            if (data.code === 'INVALID_REQUEST'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '     ,    .'
                }
              })
            }
          }
        })
      })
    }

    checkBalance();
  }

  const hideGroup = (e) => {
    e.preventDefault();

    const Hide = async() => {
      await fetch(`/api/panel/user/groups/${User.uid}/${deleteModal.group_id}`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
        }
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            setGroups({
              ...groups,
              groups: data.groups,
            });
            resetModals();
          } else {
            if (data.code === 'AUTH_ERROR'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: ' ',
                  message: '     '
                }
              })
            }
            if (data.code === 'ACCESS_DENIED'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: ' ',
                  message: '       .'
                }
              })
            }
            if (data.code === 'INVALID_ACCESS_LEVEL'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '     ,    .'
                }
              })
            }
            if (data.code === 'NO_TOKEN'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '    ,       '
                }
              })
            }
            if (data.code === 'INVALID_AUTH_HEADER'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '     ,    .'
                }
              })
            }
            if (data.code === 'NOT_LOGGED_IN'){
              localStorage.clear();
              sessionStorage.clear();
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '        .'
                }
              })
            }
            if (data.code === 'NO_MATCH_ID'){
              setErrors({
                ID: {
                  name: ' ',
                  error: '     ,     .',
                  type: 'warning'
                }
              })
            }
            if (data.code === 'NOT_YOURS'){
              setErrors({
                Company: {
                  name: ' ',
                  error: '       .',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'SERVER'){
              setErrors({
                Server: {
                  name: '',
                  error: '     ,   .',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'NOT_EXPIRED'){
              setErrors({
                Expired: {
                  name: '',
                  error: '     .',
                  type: 'danger'
                }
              })
            }
          }
        })
      })
    }

    Hide();
  }

  /* Ads Images */

  const handleImagesChange = (e) => {
    let fileToUpload = e.target.files
    let id = e.target.id

    if (e.target.value) {

      // Reset File Errors
      let prev_errors = {...errors};
      delete prev_errors['ImageSize']
      delete prev_errors['Images']
      setErrors(prev_errors)
      
      // Image Size Validation
      let reader = new FileReader();

      // Check File Type.
      var filename = fileToUpload[0].name;

      var ex = filename.split('.').reverse()[0];

      let acceptedTypes = ['image/png', 'image/jpg', 'image/jpeg']
      let acceptedEx = ['png', 'jpg', 'jpeg']

      if (acceptedTypes.includes(fileToUpload[0].type) || acceptedEx.includes(ex)){
        reader.readAsDataURL(fileToUpload[0]);

        reader.onload = function(e) {

          let image = new Image();
          image.src = e.target.result;

          // Validate Width & Height

          image.onload = function() {
            let height = this.height;
            let width = this.width;
            // Min Width : 800 Max Width : 1280
            // Min Height : 450 Max Height 1280
            if (width < 0 || height < 0){
              fileToUpload[0].value = '';
              setErrors({
                ...errors,
                ImageSize: {
                  name: '',
                  error: `  (${fileToUpload[0].name})  ,        (450*800)`,
                  type: 'warning'
                }
              });
              // Reset Image
              document.getElementById(id).src = null;
              document.getElementById(`${id}-label`).textContent = '';
            } else if (width > 12800 || height > 12800) {
              fileToUpload[0].value = '';
              setErrors({
                ...errors,
                ImageSize: {
                  name: '',
                  error: `     ,      (1280*1280)`,
                  type: 'warning'
                }
              });
              document.getElementById(id).src = null;
              document.getElementById(`${id}-label`).textContent = '';
            } else {
              document.getElementById(id).src = URL.createObjectURL(fileToUpload[0]);
              document.getElementById(`${id}-label`).textContent = filename;
              
              // Set Images
              let images = imageState;
              images.push(id);
              setImageState(images)
            }
          }

        }
      } else {
        setErrors({
          ...errors,
          imageType: {
            name: "",
            error: "      [png, jpg, jpeg]",
            type: "danger"
          }
        });
        // Reset Images
        document.getElementById(id).src = null;
        document.getElementById(`${id}-label`).textContent = null;
      }
    
    }
  }

  const handleImagesSubmit = (e) => {
    e.preventDefault();
    const formData = new FormData();

    // Add images to formData
    for (let i = 0; i < imageState.length; i++) {
      let image = document.getElementById(imageState[i]).files[0];
      formData.append(imageState[i], image);
    }

    const UploadImages = async() => {
      await fetch('/api/images', {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
        },
        body: formData
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            localStorage.setItem("maidan_user", JSON.stringify(data.user));
            setUser({...JSON.parse(localStorage.maidan_user)});
            resetModals();
          } else {
            document.getElementById('UploadBtn').disabled = false;
            if (data.code === 'SERVER'){
              setErrors({
                ...errors,
                ImageUpload: {
                  name: '',
                  error: '      ,    .',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'INVALID_REQUEST'){
              setErrors({
                ...errors,
                ImageUpload: {
                  name: '',
                  error: '       .',
                  type: 'warning'
                }
              })
            }
          }
        })
      })
    }
    document.getElementById('UploadBtn').disabled = true;
    UploadImages();
  }

  const validateAdImages = (e) => {
    e.preventDefault();

    var images = User.ad_images || []
    if (User.main_ad_image) {
      images.push(User.main_ad_image);
    }

    if (images.length > 0){
      window.location.href = "/admin/groups/new";
    }
    else {
      setImagesModal(true);
      setErrors({
        ...errors,
        ImagePrimary: {
          name: ' ',
          error: '        ',
          type: 'danger'
        }
      });
    }
    
  }

  const handleImageDelete = (e) => {
    e.preventDefault();

    const DeleteImage = async() => {
      await fetch(`/api/images/${imgDeleteModal.image}`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
        }
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            localStorage.setItem("maidan_user", JSON.stringify(data.user));
            setUser({...JSON.parse(localStorage.maidan_user)});
            setErrors({});
          }
          else {
            if (data.code === "MAIN_AD_IMAGE") {
              setErrors({
                ImageDelete: {
                  name: " ",
                  error: "    ,   .",
                  type: "danger"
                }
              })
            } else {
              setErrors({
                ImageDelete: {
                  name: " ",
                  error: "     .",
                  type: "danger"
                }
              })
            }
          }
        })
      })
    }

    DeleteImage();
    // Reset Delete Modal
    setImgDeleteModal({view: false, image: null});
  }

  /* Ads Images */

  const LoadGroups = () => {

    const getDate = (seconds) => {
      var date = new Date(1970, 0, 1);
      date.setSeconds(seconds);
      var sloppy_date = new Date(date);
      var tzOffset = sloppy_date.getTimezoneOffset();
      
      var target_date = new Date(sloppy_date.getTime() - tzOffset * 60 * 1000);
  
      var yy = target_date.getFullYear();
      var mm = target_date.getMonth() + 1;
      var dd = target_date.getDate();
  
      var h = target_date.getHours();
      var m = target_date.getMinutes();
  
      return `${yy}-${(mm>9?'':'0')+mm}-${(dd>9?'':'0')+dd} ${(h>9?'':'0')+h}:${(m>9?'':'0')+m}`
    }

    const getStatus = (status) => {
      let types = {
        canceled: ['', 'warning'],
        available: ['', 'success'],
        expired: ['', 'danger'],
        finished: [' ', 'secondary']
      }

      return types[status]
    }

    const getDistributeType = (type) => {
      let types = {
        display: [' ', 'dark'],
        request: [' ', 'primary']
      }

      return types[type]
    }

    const handleSettings = (group) => {

      setSettingState(group)
      setSettingsModal(true);

    }

    const groups_patch = groups.groups && groups.groups.slice(0, groups.limit);

    return(
      <tbody>
        {
          groups_patch ? groups_patch.map((group, index) => {
            let status = getStatus(group.status)
            let disType = getDistributeType(group.distribute_type)
            return(
              <tr className="" key={index}>
                <th className="table-header">
                  <Link className="text-primary" to={`/ads/${group.id}`}>{group.id}</Link>
                </th>
                <td className="table-header">{group.coupon_code}</td>
                <td className="table-header">{group.coupons_num}</td>
                <td className="table-header">{group.coupon_price}$</td>
                <td className="table-header">{group.full_price}$</td>
                <td className="table-header">{group.coupons_left}</td>
                <td className={`table-header text-${disType[1]}`}>{disType[0]}</td>
                <td className={`table-header text-${status[1]}`}>{status[0]}</td>
                <td className="table-header">{group.views}</td>
                {/* <td dir='ltr' className="table-header">{getDate(group.start_date)}</td> */}
                <td dir='ltr' className="table-header">{getDate(group.expire_date)}</td>
                {
                  ['expired', 'finished', 'canceled'].includes(group.status) ? (
                    <td className="table-body">
                      <button onClick={() => {setDeleteModal({view: true, group_id: group.id})}} className="btn btn-sm btn-outline-danger font-weight-bold"></button>
                    </td>
                  ) : (
                    <td className="table-body">
                      <button onClick={() => {handleSettings(group)}} className="btn btn-sm btn-outline-grey font-weight-bold"></button>
                    </td>
                  )
                }
            </tr>
            )
          }) : null
        }
      </tbody>
    )
  }

  const LoadAssociates = () => {
    
    const associates_patch = associates.users && associates.users.slice(0, associates.limit);

    return(
      <tbody>
        {
          associates_patch ? associates_patch.map((user, index) => {
            return(
              <tr className="" key={index}>
                <th className="table-header">{user.id}</th>
                <td className="table-header">{user.username}</td>
                <td className="table-header">{user.email}</td>
                <td className="table-header">
                  <button onClick={() => {setRemoveAssoc({view: true, id:user.id})}} className="btn btn-sm btn-outline-warning"></button>
                </td>
            </tr>
            )
          }) : null
        }
      </tbody>
    )
  }

  const LoadCoupons = () => {

    const getDate = (seconds) => {
      var date = new Date(1970, 0, 1);
      date.setSeconds(seconds);
      var sloppy_date = new Date(date);
      var tzOffset = sloppy_date.getTimezoneOffset();
      
      var target_date = new Date(sloppy_date.getTime() - tzOffset * 60 * 1000);
  
      var yy = target_date.getFullYear();
      var mm = target_date.getMonth() + 1;
      var dd = target_date.getDate();
  
      var h = target_date.getHours();
      var m = target_date.getMinutes();
  
      return `${yy}-${(mm>9?'':'0')+mm}-${(dd>9?'':'0')+dd} ${(h>9?'':'0')+h}:${(m>9?'':'0')+m}`
    }
    
    const coupons_patch = coupons.coupons && coupons.coupons.slice(0, coupons.limit);

    return(
      <tbody>
        {
          coupons_patch ? coupons_patch.map((coupon, index) => {
            return(
              <tr className="" key={index}>
                <th className="table-header">{coupon.id}</th>
                <td className="table-header">
                  <Link to={`/ads/${coupon.group_id}`} className="text-primary">#{coupon.group_id}</Link>
                </td>
                <td className="table-header">#{coupon.user_id}</td>
                <td className="table-header">{coupon.qr_code}</td>
                <td className="table-header">{coupon.coupon_price}$</td>
                <td className="table-header">#{coupon.matcher.id}</td>
                <td className="table-header">{coupon.matcher.username}</td>
                <td dir='ltr' className="table-header">{getDate(coupon.redeem_date)}</td>
            </tr>
            )
          }) : null
        }
      </tbody>
    )
  }

  /* Associates */

  const fetchUsersResualts = async(e) => {
    var value = e.target && e.target.value;
    setAccosiationModal({
      ...associationModal,
      user: null
    });
    await fetch('/api/panel/search/associates', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      },
      body: JSON.stringify({searchQuery: value})
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setErrors({});
          if (data.length === 0 && value){
            setErrors({
              users: {
                name: '',
                error: '     ',
                type: 'warning'
              }
            })
          }
          setSearchUsers({
            users: data.users,
            length: data.length
          });
        } else {
          setErrors({
            users: {
              name: '',
              error: '      ,     .',
              type: 'danger'
            }
          })
        }
      })
    })
  }

  const handleUserSelection = (id) => {

    setErrors({}); // Reset Errors

    // Remove Color from old Selected, and remove Selected Sign.
    var oldElemnt = document.getElementById(`user-card-${associationModal.user}`);
    var oldSelectedElement = document.getElementById(`user-select-${associationModal.user}`);
    if (oldElemnt){
      oldElemnt.classList.remove('bg-light-grey');
      oldElemnt.classList.add('bg-white');
    }
    if (oldSelectedElement){
      oldSelectedElement.classList.remove('fa-check-circle')
    }

    // Changing Color of new Selected, and add Selected Sign.
    var newElemnt = document.getElementById(`user-card-${id}`);
    var newSelectedElemnt = document.getElementById(`user-select-${id}`);
    newElemnt.classList.remove('bg-white');
    newElemnt.classList.add('bg-light-grey');
    newSelectedElemnt.classList.add('fa-check-circle')

    // Adding Selected User as `user` in `Accosiation`
    setAccosiationModal({
      ...associationModal,
      user: id
    });
  }

  const handleAssociateSubmit = () => {

    const setAssociate = async() => {
      await fetch(`/api/panel/association/${User.id}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
        },
        body: JSON.stringify({accosiate: associationModal.user})
      }).then(resp => {
        resp.json().then(_ => {
          if (resp.ok) {
            resetModals();
            window.location.reload()
          } else {
            setErrors({
              users: {
                name: '',
                error: '     ,     .',
                type: 'danger'
              }
            })
          }
        })
      })
    }

    if (!associationModal.user){
      setErrors({
        Select: {
          name: ' ',
          error: '       .',
          type: 'warning'
        }
      });
    } else {
      setAssociate();
    }
  }

  const removeAssociate = async() => {
    await fetch(`/api/panel/association/${User.id}/remove/${removeAssoc.id}`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      },
    }).then(resp => {
      resp.json().then(_ => {
        if (resp.ok) {
          resetModals();
          window.location.reload()
        } else {
          setErrors({
            Delete: {
              name: ' ',
              error: '     .',
              type: 'danger'
            }
          })
        }
      })
    })
  }

  const AddLimit = (e) => {
    e.preventDefault();
    if (e.target.id === 'groups') {
      
      if (groups.limit < groups.groups.length){
        let new_limit = groups.limit + 50;
        setGroups({...groups, limit: new_limit})
      }

    } else if (e.target.id === 'coupons') {

      if (coupons.limit < coupons.coupons.length){
        let new_limit = coupons.limit + 50;
        setCoupons({...coupons, limit: new_limit})
      }

    } else if (e.target.id === 'associates') {

      if (associates.limit < associates.users.length){
        let new_limit = associates.limit + 50;
        setAssociates({...associates, limit: new_limit})
      }

    }
    
  }

  const ReduceLimit = (e) => {
    e.preventDefault();

    if (e.target.id === 'groups') {
      
      if (groups.limit > 50){
        let new_limit = groups.limit - 50;
        setGroups({...groups, limit: new_limit})
      }

    } else if (e.target.id === 'coupons') {
      
      if (coupons.limit > 50){
        let new_limit = coupons.limit - 50;
        setCoupons({...coupons, limit: new_limit})
      }

    } else if (e.target.id === 'associates') {
      
      if (associates.limit > 50){
        let new_limit = associates.limit - 50;
        setAssociates({...associates, limit: new_limit})
      }

    }
    
  }

  const loadErrors = () => {
    if (Object.keys(errors).length !== 0 && errors.constructor === Object){
      return (
        <div>
          {errors ? Object.entries(errors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  if (redirect.redirect){
    return <Redirect to={{
      pathname: '/',
      state: {flash: redirect.flash}
    }}/>
  }

  const resetModals = () => {

    // Reset States
    setErrors({});
    setSettingState({});
    setImageState([]);
    setDeleteModal({view: false, group_id: null});
    setRemoveAssoc({view: false, id: null});
    setAccosiationModal({view: false, user: null});
    setSearchUsers({});

    // Reset Modals
    setSettingsModal(false);
    setImagesModal(false);
  }

  return(
    <div className="mb-5 pb-5 pt-5">

      <div className="container text-center mt-5 pt-5">

        <div className="container mt-4">

          <span className="display-7">  ({User.company})</span>
          <div className="container-fluid text-center mt-4">
            <button className="btn btn-sm btn-outline-info fixed-width" onClick={() => {setImagesModal(true)}}> </button>
            <button className="btn btn-sm btn-outline-grey fixed-width" onClick={validateAdImages}> </button>
            <button className="btn btn-sm btn-outline-danger fixed-width" onClick={() => {setAccosiationModal({view: true, user: null})}}> </button>
            <Link className="btn btn-sm btn-outline-primary fixed-width" to="/admin/redeem"> </Link>
          </div>
          <hr/>

          <div className="container mt-4">
            <span className="display-8"></span>
            <hr/>
            <div className="table-responsive mt-4">
              <table className="table table-sm table-bordered">
                <thead>
                  <tr className="bg-light-grey">
                    <th className="font-weight-bold" scope="col">ID</th>
                    <th className="font-weight-bold" scope="col"> </th>
                    <th className="font-weight-bold" scope="col"> </th>
                    <th className="font-weight-bold" scope="col"></th>
                  </tr>
                </thead>
                {LoadAssociates()}
              </table>
              
              <div className="justify-content-center">
                {(associates.users && associates.limit < associates.users.length) ? (<button id="associates" onClick={AddLimit} className="btn btn-sm btn-outline-primary"> </button>) : null}
                {associates.limit > 50 ? (<button id="associates" onClick={ReduceLimit} className="btn btn-sm btn-outline-secondary"> </button>) : null}
              </div>
              
            </div>
          </div>

          <div className="container mt-5">
            <span className="display-8"></span>
            <hr/>
            <div className="table-responsive mt-4">
              <table className="table table-sm table-bordered">
                <thead>
                  <tr className="bg-light-grey">
                    <th className="font-weight-bold" scope="col">ID</th>
                    <th className="font-weight-bold" scope="col"> </th>
                    <th className="font-weight-bold" scope="col"></th>
                    <th className="font-weight-bold" scope="col"></th>
                    <th className="font-weight-bold" scope="col"></th>
                    <th className="font-weight-bold" scope="col"></th>
                    <th className="font-weight-bold" scope="col"> </th>
                    <th className="font-weight-bold" scope="col"></th>
                    <th className="font-weight-bold" scope="col"></th>
                    {/* <th className="font-weight-bold" scope="col"></th> */}
                    <th className="font-weight-bold" scope="col"></th>
                    <th className="font-weight-bold" scope="col"></th>
                  </tr>
                </thead>
                {LoadGroups()}
              </table>
              
              <div className="justify-content-center">
                {(groups.groups && groups.limit < groups.groups.length) ? (<button id="groups" onClick={AddLimit} className="btn btn-sm btn-outline-primary"> </button>) : null}
                {groups.limit > 50 ? (<button id="groups" onClick={ReduceLimit} className="btn btn-sm btn-outline-secondary"> </button>) : null}
              </div>
              
            </div>
          </div>

          <div className="container mt-5">
            <span className="display-8"> </span>
            <hr/>
            <div className="table-responsive mt-4">
              <table className="table table-sm table-bordered">
                <thead>
                  <tr className="bg-light-grey">
                    <th className="font-weight-bold" scope="col">ID</th>
                    <th className="font-weight-bold" scope="col"> </th>
                    <th className="font-weight-bold" scope="col"> </th>
                    <th className="font-weight-bold" scope="col">QRCode</th>
                    <th className="font-weight-bold" scope="col"></th>
                    <th className="font-weight-bold" scope="col"> </th>
                    <th className="font-weight-bold" scope="col"></th>
                    <th className="font-weight-bold" scope="col"> </th>
                  </tr>
                </thead>
                {LoadCoupons()}
              </table>
              
              <div className="justify-content-center">
                {(coupons.coupons && coupons.limit < coupons.coupons.length) ? (<button id="coupons" onClick={AddLimit} className="btn btn-sm btn-outline-primary"> </button>) : null}
                {coupons.limit > 50 ? (<button id="coupons" onClick={ReduceLimit} className="btn btn-sm btn-outline-secondary"> </button>) : null}
              </div>
              
            </div>
          </div>
          
        </div>

      </div>

      {/* Add Associates Modal */}
      <Modal show={associationModal.view} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8"> </Modal.Title>
        </Modal.Header>
        
        <Modal.Body className="text-center">
          <span className="font-weight-bold text-muted">        : </span>
          <div className="mt-2">
            <p className="font-info text-muted">
              <i className="fa fa-star-of-life fa-s ml-1"/>      .
              <br/>
              <i className="fa fa-star-of-life fa-s ml-1"/>     .
            </p>
          </div>

          <div className="container mt-4">

            <div className="row justify-content-center">

              <div className="col-sm-8">

                <div className="input-group">
                  <input id="users" onChange={fetchUsersResualts} type="text" placeholder=" /" className={"form-control col-sm-12 " + (errors['users'] ? 'is-invalid' : '')}/>
                </div>

              </div>

            </div>

          </div>
          
          <div className="container mt-2">
            {loadErrors()}
          </div>
          
          {
            searchUsers.users ? (
              
              <div className="container mt-4 text-right">
                <span className="font-weight-bold"> ({searchUsers.length}) : </span>
                {searchUsers.users.map(user => (
                  <Link key={user.id} to="#" onClick={() => {handleUserSelection(user.id)}}>
                    <div id={`user-card-${user.id}`} className="card shadow bg-white mt-2">
                      <div className="card-body">
                        <i id={`user-select-${user.id}`} className="fa text-info float-left"/>
                        <p className="card-text font-weight-bold">
                            : {user.username}
                        </p>
                        <p className="card-text font-weight-bold">
                            : {user.email}
                        </p>
                      </div>
                    </div>
                  </Link>
                ))}
              </div>
              
            ) : null
          }
        </Modal.Body>
        
        <Modal.Footer>
          <div className="text-center">
            <p className="font-info font-weight-bold">
                   .
              <br/>
                  <Link to="#" onClick={() => window.open('/terms')} className="text-danger">  </Link>       .
            </p>
          </div>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={resetModals}>
              
            </Button>
            <Button variant="secondary" onClick={handleAssociateSubmit}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>
      
      {/* Remove Associate Modal */}
      <Modal show={removeAssoc.view} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">  (#{removeAssoc.id})</Modal.Title>
        </Modal.Header>
        
        <Modal.Body className="text-center">

          <div className="container mt-2">
            {loadErrors()}
          </div>

          <div className="container mt-4">
            
            <p className="font-weight-bold text-danger">
                    
            </p>

          </div>

        </Modal.Body>
        
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={resetModals}>
              
            </Button>
            <Button variant="danger" onClick={removeAssociate}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal> 

      {/* Settings Modal */}
      <Modal show={settingsModal} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">  (#{settingState.id})</Modal.Title>
        </Modal.Header>
        
        <Modal.Body className="text-center">

          <div className="container text-right mt-4">

            <div className="container mt-2">
              {loadErrors()}
            </div>

            <form id="UpdateForm" onSubmit={handleSubmit}>

              <p className="font-info text-primary">
                <i className="fa fa-star-of-life fa-s ml-2"/>
                     .
              </p>

              <p className="info-weight text-muted">
                -   () : {settingState.coupons_num}
              </p>

              <p className="info-weight text-muted">
                -    () : {settingState.coupons_left}
              </p>

              <p className="info-weight text-muted">
                -   () : {settingState.full_price} $
              </p>

              <div className="container mt-4">
                     
                <div className="row mt-4">
                  
                  <div className="col-sm-10">
                    
                    <label htmlFor="coupons_plus" className="font-weight-bold">*   () : </label>
                    <div className="input-group">
                      <input required onChange={handleChange} id="coupons_plus" type="number" min="0" placeholder=" " className={"form-control col-sm-12 " + (errors['coupons_plus'] ? 'is-invlaid' : '')}/>
                    </div>
                    
                    <p className="font-sm text-muted">
                      <i className="fa fa-star-of-life fa-xs ml-2"/>
                              ,       .
                    </p>

                  </div>

                </div>
              
              </div>
            
            </form>
            
          </div>

        </Modal.Body>
        
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={resetModals}>
              
            </Button>
            <Button type='submit' form="UpdateForm" variant="primary">
              
            </Button>
          </div>
        </Modal.Footer>
      
      </Modal>

      {/* Delete Modal */}
      <Modal show={deleteModal.view} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">  (#{deleteModal.group_id})</Modal.Title>
        </Modal.Header>
        
        <Modal.Body className="text-center">

          <div className="container mt-2">
            {loadErrors()}
          </div>

          <div className="container mt-4">
            
            <p className="font-weight-bold text-danger">
                    
            </p>

            <p className="font-info">
                   ,      .
            </p>

          </div>

        </Modal.Body>
        
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={resetModals}>
              
            </Button>
            <Button variant="danger" onClick={hideGroup}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal> 

      {/* Images Modal */}
      <Modal show={imagesModal && !imgDeleteModal.view} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">  </Modal.Title>
        </Modal.Header>
        
        <Modal.Body className="text-center">

          <div className="container mt-2">
            {loadErrors()}
          </div>

          <form id="ImagesUpload" onSubmit={handleImagesSubmit} encType="multipart/form-data">
        
            <p className="font-weight-lighter font-info text-muted text-right mt-4">
              <i className="fa fa-star-of-life fa-xs"/>      .
              <br/>
              <i className="fa fa-star-of-life fa-xs"/>   (450*800)  .
            </p>

            <div className="row mt-2 text-right">

              <div className="form-group col-sm-10">
                <label htmlFor="imagePrimary" className="font-weight-bold float-right">*    : </label>
                <div className="custom-file">
                  <input required={User.main_ad_image ? false : true} onChange={handleImagesChange} type="file" className="custom-file-input" id="imagePrimary" name="imagePrimary" accept="image/*"/>
                  <label id="imagePrimary-label" className="custom-file-label col-sm-12" htmlFor="imagePrimary"/>
                </div>
                {
                  User.main_ad_image ? (
                    <div>
                      <span className="float-right">
                        <Link to="#" onClick={() => {setImgPreviewModal({view: true, image: User.main_ad_image})}} className="font-info text-primary">  </Link>
                      </span>
                    </div>
                    ) : (
                      <p className="font-info text-muted">  </p>
                    )
                }

              </div>

            </div>
            
            <div className="row mt-2 text-right">

              <div className="form-group col-sm-10">
                <label htmlFor="images1" className="font-weight-bold float-right">   : </label>
                <div className="custom-file">
                  <input onChange={handleImagesChange} type="file" className="custom-file-input" id="images1" name="images1" accept="image/*"/>
                  <label id="images1-label" className="custom-file-label col-sm-12" htmlFor="images1"/>
                </div>
                {
                  User.ad_images && User.ad_images.length >= 1 ? (
                    <div>
                      <span className="float-right">
                        <Link to="#" onClick={() => {setImgPreviewModal({view: true, image: User.ad_images[0]})}} className="font-info text-primary">   1</Link>
                      </span>
                      <span className="float-left">
                        <Link to="#" onClick={() => {setImgDeleteModal({view: true, image: User.ad_images[0]})}} className="font-info text-danger">   1</Link>
                      </span>
                    </div>
                    ) : (
                      <p className="font-info text-muted">  </p>
                    )
                }

              </div>

            </div>
            
            <div className="row mt-2 text-right">

              <div className="form-group col-sm-10">
                <label htmlFor="images2" className="font-weight-bold float-right">   : </label>
                <div className="custom-file">
                  <input onChange={handleImagesChange} type="file" className="custom-file-input" id="images2" name="images2" accept="image/*"/>
                  <label id="images2-label" className="custom-file-label col-sm-12" htmlFor="images2"/>
                </div>
                {
                  User.ad_images && User.ad_images.length >= 2 ? (
                    <div>
                      <span className="float-right">
                        <Link to="#" onClick={() => {setImgPreviewModal({view: true, image: User.ad_images[1]})}} className="font-info text-primary">   2</Link>
                      </span>
                      <span className="float-left">
                        <Link to="#" onClick={() => {setImgDeleteModal({view: true, image: User.ad_images[1]})}} className="font-info text-danger">   2</Link>
                      </span>
                    </div>
                    ) : (
                      <p className="font-info text-muted">  </p>
                    )
                }

              </div>

            </div>   
            
            <div className="row mt-2 text-right">

              <div className="form-group col-sm-10">
                <label htmlFor="images3" className="font-weight-bold float-right">   : </label>
                <div className="custom-file">
                  <input onChange={handleImagesChange} type="file" className="custom-file-input" id="images3" name="images3" accept="image/*"/>
                  <label id="images3-label" className="custom-file-label col-sm-12" htmlFor="images3"/>
                </div>
                {
                  User.ad_images && User.ad_images.length >= 3 ? (
                    <div>
                      <span className="float-right">
                        <Link to="#" onClick={() => {setImgPreviewModal({view: true, image: User.ad_images[2]})}} className="font-info text-primary">   3</Link>
                      </span>
                      <span className="float-left">
                        <Link to="#" onClick={() => {setImgDeleteModal({view: true, image: User.ad_images[2]})}} className="font-info text-danger">   3</Link>
                      </span>
                    </div>
                    ) : (
                      <p className="font-info text-muted">  </p>
                    )
                }

              </div>

            </div>

          </form>

        </Modal.Body>
        
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={resetModals}>
              
            </Button>
            <Button id="UploadBtn" form="ImagesUpload" type="submit" variant="success">
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal> 
      
      {/* Open Image Modal */}
      <Modal show={imgPreviewModal.view} onHide={() => {setImgPreviewModal({view: false, image: null})}}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8"> </Modal.Title>
        </Modal.Header>

        <Modal.Body className="text-center">
          <div className="container">
            <div className="row justify-content-center">
              <div className="col-sm-12">
                <img src={`https://maidancom.s3.us-east-2.amazonaws.com/${imgPreviewModal.image}`} alt="img_preview" className="img-fluid"/>
              </div>
            </div>
          </div>
        </Modal.Body>

        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="outline-grey" onClick={() => {setImgPreviewModal({view: false, image: null})}}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>
      
      {/* Delete Image Modal */}
      <Modal show={imgDeleteModal.view} onHide={() => {setImgDeleteModal({view: false, image: null})}}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8"> </Modal.Title>
        </Modal.Header>

        <Modal.Body className="text-center">
          <div className="container">
            <p className="font-weight-bold text-danger">
                   
            </p>
            <div className="row mt-4 justify-content-center">
              <div className="col-sm-8">
                <img src={`https://maidancom.s3.us-east-2.amazonaws.com/${imgDeleteModal.image}`} alt="img_preview" className="img-fluid"/>
              </div>
            </div>
          </div>
        </Modal.Body>

        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="outline-grey" onClick={() => {setImgDeleteModal({view: false, image: null})}}>
              
            </Button>
            <Button variant="danger" onClick={handleImageDelete}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>
      
      {/* Balance Modal */}
      <Modal show={balanceModal.view} onHide={() => {setBalanceModal({view: false, user_balance: null})}}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8"> </Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">
          <i className="fa fa-times-circle text-danger fa-3x"/>
          <p className="font-weight-bold">
              : {balanceModal.user_balance}$
            <br/>
                    ({settingState.coupon_price*(parseInt(settingState.coupons_plus) + settingState.coupons_num)}$).
          </p>
        </Modal.Body>
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={() => {setBalanceModal({view: false, user_balance: null})}}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>

    </div>
  )
}

export default MyGroups