import React, {useState, useEffect} from 'react';

// Modals
import { Modal, Button } from 'react-bootstrap';
import { Link, Redirect } from 'react-router-dom';


export const ViewOffers = () => {

  const User = localStorage.maidan_user && JSON.parse(localStorage.maidan_user);
  
  const [redirect, setRedirect] = useState({redirect: false, flash: null});
  const [offers, setOffers] = useState({offers: [], limit: 10});

  const [errors, setErrors] = useState({});
  const [modalErrors, setModalErrors] = useState({});

  // Modals
  const [flash, setFlash] = useState({});
  const [isFlash, setFlashModal] = useState(false);
  const [isBranches, setBranchesModal] = useState({view: false, branches: []});
  const [isBuy, setBuyModal] = useState({view: false, offer_id: null})

  useEffect(() => {
    fetch('/api/offers', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setOffers({
            offers: data.offers,
            limit: 10
          });
        } else {
          setErrors({
            Server: {
              name: '',
              error: '       ,   .',
              type: 'danger'
            }
          })
        }
      })
    })
  }, [])


  const handleBuy = (e) => {
    e.preventDefault();
    
    const BuyCoupon = async() => {
      await fetch(`/api/user/coupon/${isBuy.offer_id}/buy`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
        }
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            resetModals();
            setOffers({
              ...offers,
              offers: data.offers
            });
            setFlashModal(true);
            setFlash({
              title: ' ',
              message: '  ,      !',
              type: 'success',
              reload: true
            });
          } else {
            if (data.code === 'AUTH_ERROR'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: ' ',
                  message: '    ,   '
                }
              })
            }
            if (data.code === 'ACCESS_DENIED'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: ' ',
                  message: '    ,   '
                }
              })
            }
            if (data.code === 'INVALID_ACCESS_LEVEL'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '     ,    .'
                }
              })
            }
            if (data.code === 'NO_TOKEN'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '    ,       '
                }
              })
            }
            if (data.code === 'INVALID_AUTH_HEADER'){
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '     ,    .'
                }
              })
            }
            if (data.code === 'NOT_LOGGED_IN'){
              localStorage.clear();
              sessionStorage.clear();
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '        .'
                }
              })
            }
            if (data.code === 'DISABLED'){
              localStorage.removeItem('maidan_jwt_$m3');
              localStorage.removeItem('maidan_user');
              localStorage.removeItem('logged');
              localStorage.removeItem('maidan_user_access');
              localStorage.removeItem('profile_img');
              sessionStorage.clear();
              setRedirect({
                redirect: true,
                flash: {
                  type: 'error',
                  title: '',
                  message: '   ,    ,    .'
                }
              })
            }
            if (data.code === 'SERVER'){
              resetModals();
              setFlashModal(true);
              setFlash({
                title: '',
                message: '     ,   .',
                type: 'error'
              })
            }
            if (data.code === 'NO_MATCH_ID'){
              resetModals();
              setFlashModal(true);
              setFlash({
                title: '',
                message: '        .',
                type: 'error'
              })
            }
            if (data.code === 'SOLD_OFFER'){
              resetModals();
              setFlashModal(true);
              setFlash({
                title: '',
                message: ',    ,     .',
                type: 'error'
              })
            }
            if (data.code === 'EXPIRED'){
              resetModals();
              setFlashModal(true);
              setFlash({
                title: '',
                message: ',       .',
                type: 'error'
              })
            }
            if (data.code === 'NO_COUPON_FOUND'){
              resetModals();
              setFlashModal(true);
              setFlash({
                title: '',
                message: '     ,    ',
                type: 'error'
              })
            }
            if (data.code === 'LOWER_CREDIT'){
              resetModals();
              setFlashModal(true);
              setFlash({
                title: ' ',
                message: `  (${User.balance}$)    ,   ,   .`,
                type: 'error'
              })
            }
            if (data.code === 'ITS_YOUR_OFFER'){
              resetModals();
              setFlashModal(true);
              setFlash({
                title: '',
                message: '   ,      ().',
                type: 'error'
              })
            }
          }
        })
      })
    }

    BuyCoupon();

  }

  const OpenBranchMap = (e) => {
    e.preventDefault();
    window.open(e.target.id)
  }

  const getDate = (seconds) => {
    var date = new Date(1970, 0, 1);
    date.setSeconds(seconds);
    var sloppy_date = new Date(date);
    var tzOffset = sloppy_date.getTimezoneOffset();
    
    var target_date = new Date(sloppy_date.getTime() - tzOffset * 60 * 1000);

    var yy = target_date.getFullYear();
    var mm = target_date.getMonth() + 1;
    var dd = target_date.getDate();

    var h = target_date.getHours();
    var m = target_date.getMinutes();

    return `${yy}-${(mm>9?'':'0')+mm}-${(dd>9?'':'0')+dd} ${(h>9?'':'0')+h}:${(m>9?'':'0')+m}`
  }

  const loadOffers = () => {
    const offers_patch = offers.offers && offers.offers.slice(0, offers.limit)


    return (
      <tbody>
        {
          offers_patch ? offers_patch.map((offer, index) => {
            return(
              <tr key={index}>
                
                <th className="table-header">{index+1}</th>
                <th className="table-header">
                  <Link className="text-primary" to={`/ads/${offer.group_id}`}>{offer.group_id}</Link>
                </th>
                <td className="table-header">{offer.seller}</td>
                <td className="table-header">{offer.coupon_type}</td>
                <td className="table-header">{offer.company}</td>
                <td className="table-header">
                  <button onClick={() => {setBranchesModal({view: true, branches: offer.branches})}} className="btn btn-sm btn-outline-primary"></button>
                </td>
                <td className="table-header text-primary">${offer.coupon_price}</td>
                <td dir='ltr' className="table-header text-danger">{getDate(offer.expire_date)}</td>
                <td className="table-header">${offer.price}</td>
                <td className="table-header">
                  <button onClick={() => {setBuyModal({view: true, offer_id: offer.id})}} className="btn btn-sm btn-secondary"></button>
                </td>
              </tr>
            )
          }) : null
        }
      </tbody>
    )
  }

  const AddLimit = () => {
    if ((offers.limit < offers.offers.length)){
      let new_limit = offers.limit + 10;
      setOffers({...offers, limit: new_limit})
    }
  }

  const ReduceLimit = () => {
    
    if ((offers.limit > 10)){
      let new_limit = offers.limit - 10;
      setOffers({...offers, limit: new_limit})
    }
  }

  const loadErrors = () => {
    if (Object.keys(errors).length !== 0 && errors.constructor === Object){
      return (
        <div>
          {errors ? Object.entries(errors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  const loadModalErrors = () => {
    if (Object.keys(modalErrors).length !== 0 && modalErrors.constructor === Object){
      return (
        <div>
          {modalErrors ? Object.entries(modalErrors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  const resetModals = () => {

    if (flash.reload){
      window.location.reload();
    }

    // Reset State, Errors
    setFlash({});
    setModalErrors({});

    // Reset Modals
    setFlashModal(false);
    setBranchesModal({view: false, branches: []});
    setBuyModal({view: false, offer_id: null})

  }

  if (redirect.redirect){
    return <Redirect to={{
      pathname: '/',
      state: {flash: redirect.flash}
    }}/>
  }

  return(
    <div className="mb-5 pb-5">

      <div className="pt-4">
        
        <div id="carousel-example-1z" className="mt-5 pt-5 pr-md-2 pl-md-2 carousel slide carousel-fade" data-ride="carousel">

          <ol className="carousel-indicators">
            <li data-target="#carousel-example-1z" data-slide-to="0" className="active"></li>
            <li data-target="#carousel-example-1z" data-slide-to="1"></li>
            {/* <li data-target="#carousel-example-1z" data-slide-to="2"></li> */}
          </ol>

          <div className="carousel-inner" role="listbox">

            <div className="carousel-item carousel-sitem active">
              <img loading="lazy"  className="w-100 h-100 img-fluid" src="https://sites.google.com/site/moh897/banner1.png"
                alt="First slide"/>
            </div>

            <div className="carousel-item carousel-sitem">
              <img loading="lazy"  className="w-100 h-100 img-fluid" src="https://sites.google.com/site/moh897/banner2.png"
                alt="Second slide"/>
            </div>

          </div>

          <a className="carousel-control-prev" href="#carousel-example-1z" role="button" data-slide="prev">
            {/* <span className="carousel-control-prev-icon" aria-hidden="true"></span> */}
            <i className="fa fa-arrow-alt-circle-left carousel-control-prev-icons" aria-hidden="true"></i>
            <span className="sr-only"></span>
          </a>

          <a className="carousel-control-next" href="#carousel-example-1z" role="button" data-slide="next">
            {/* <span className="carousel-control-next-icon" aria-hidden="true"></span> */}
            <i className="fa fa-arrow-alt-circle-right carousel-control-next-icons" aria-hidden="true"></i>
            <span className="sr-only"></span>
          </a>

        </div> 
        
      </div>

      <div className="container mt-5 pt-5">
        
        <div className="container mt-2">
          {loadErrors()}
        </div>

        <div className="container text-center mt-2">
          <span className="display-6 header-shadow qsemh-turq">
             
          </span>
          <hr/>
          <p className="font-weight-bold mt-2">
              : {User ? `${User.balance}$` : '0$'}
          </p>
          <div className="table-responsive">

            <table className="table table-bordered mt-4">
              <thead>
                <tr className="bg-light-grey">
                  <th className="table-header" scope="col">#</th>
                  <th className="table-header" scope="col"># </th>
                  <th className="table-header" scope="col"></th>
                  <th className="table-header" scope="col"> </th>
                  <th className="table-header" scope="col"></th>
                  <th className="table-header" scope="col"></th>
                  <th className="table-header" scope="col"> </th>
                  <th className="table-header" scope="col"></th>
                  <th className="table-header" scope="col"> </th>
                  <th className="table-header" scope="col"></th>
                </tr>
              </thead>
              {loadOffers()}
            </table>
          </div>
          <div className="justify-content-center">
            {(offers.offers && offers.limit < offers.offers.length) ? (<button onClick={AddLimit} className="btn btn-sm btn-outline-primary"> </button>) : null}
            {offers.limit > 10 ? (<button onClick={ReduceLimit} className="btn btn-sm btn-outline-secondary"> </button>) : null}
          </div>
        </div>

      </div>
      
      {/* Flash Message Modal */}
      <Modal show={isFlash} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">{flash.title}</Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">
          {flash.type === 'success' ? (
            <i className="fa fa-check-circle text-success fa-3x"/>
          ) : flash.type === 'error' ? (
            <i className="fa fa-times-circle text-danger fa-3x"/>
          ) : null}
          <p className="mt-2 lead text-muted">
            {flash.message}
          </p>  
        </Modal.Body>
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="outline-success" onClick={resetModals}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>
      
      {/* Branches Modal */}
      <Modal show={isBranches.view} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8"></Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">
          <p className="text-dark">
            {isBranches.branches.map((branch, index) => {
              if (isBranches.branches.length === (index+1)){
                return(<span key={index}><Link to="#" id={branch.map} onClick={OpenBranchMap}>{branch.city}-{branch.name}</Link>.</span>)
              } else {
                return(<span key={index}><Link to="#" id={branch.map} onClick={OpenBranchMap}>{branch.city}-{branch.name}</Link>, </span>)
              }
            })}
          </p>
        </Modal.Body>
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={resetModals}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>
      
      {/* Buy Modal */}
      <Modal show={isBuy.view} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">  </Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">
          <div className="container mt-2">
            {loadModalErrors()}
          </div>
          <div className="container mt-2">
            <p className="font-weight-bold text-secondary">
                      
            </p>
            <p className="font-info text-info">
                    .
            </p>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={resetModals}>
              
            </Button>
            <Button variant="secondary" onClick={handleBuy}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>
      
    </div>
  )
}