import React, { useState, useEffect } from 'react';
import { Link, Redirect } from 'react-router-dom';

// Modals
import { Modal, Button } from 'react-bootstrap';


const ToProviderTable = () => {

  const [requests, setRequests] = useState({requests: null, limit: 50});

  const [errors, setErrors] = useState({});
  const [redirect, setRedirect] = useState({redirect: false, flash: null})

  // Modals
  const [openImage, setOpenImage] = useState({open: false, image: null})

  const [isOk, setOk] = useState({ok: false, id: null});
  const [isBad, setBad] = useState({bad: false, id: null});

  useEffect(() => {
    fetch('/api/panel/applications', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setRequests({
            requests: data.requests,
            limit: 50
          })
        } else {
          if ([
            'INVALID_AUTH_HEADER', 
            'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
            'ACCESS_DENIED', 'AUTH_ERROR'
          ].includes(data.code)) {
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
          if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
            localStorage.clear();
            sessionStorage.clear();
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
        }
      })
    })
  }, [])

  const LoadRequests = () => {

    const handleDownloadFile = async(e) => {
      e.preventDefault();
      let id = e.target.id;
      // Disable Download Link To Prevent Multi-Calls
      document.getElementById(id).classList.add('disabled');
      await fetch(`/api/panel/files/${id}`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        }
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            // Re-Enable Download Link.
            document.getElementById(id).classList.remove('disabled');
            // Download File
            window.open(data.download_link);
          }
        })
      })
    }

    const getStatus = (status) => {
      let types = {
        open: ['secondary', ' '],
        close: ['dark', ''],
        rejected: ['danger', ''],
        accepted: ['success', '']
      }

      return types[status]
    }

    const requests_patch = requests.requests && requests.requests.slice(0, requests.limit);

    return(
      <tbody>
        {
          requests_patch ? requests_patch.map((req, index) => {
            let status = getStatus(req.status);
            return(
              <tr className="" key={index}>
                <th className="table-header">{req.id}</th>
                <td className="table-header">{req.title}</td>
                <td className="table-body">{req.content}</td>
                <td className="table-header">{req.company_name}</td>
                <td className="table-header">
                  {req.mainAdImage ? (
                    <button onClick={() => {setOpenImage({open: true, image: req.mainAdImage})}} className="btn btn-sm btn-outline-grey font-weight-bold"> </button>
                  ) : ' '}
                </td>
                <td className="table-header">
                  <Link to="#" id={req.providerAgreement} onClick={handleDownloadFile} className="text-primary font-weight-bold"> </Link>
                </td>
                <td className={`table-header text-${status[0]}`}>{status[1]}</td>
                {
                  req.status === 'open' ? (
                    <td className="table-header">
                      <button onClick={() => {setBad({bad: true, id: req.id})}} className="btn btn-sm btn-outline-danger font-weight-bold"></button>
                      <button onClick={() => {setOk({ok: true, id: req.id})}} className="btn btn-sm btn-outline-success font-weight-bold"></button>
                    </td>
                  ) : (
                    <td className="table-header text-grey">.</td>
                  )
                }
              </tr>
            )
          }) : null
        }
      </tbody>
    )
  }

  const AddLimit = () => {
    if (requests.limit < requests.requests.length){
      let new_limit = requests.limit + 50;
      setRequests({...requests, limit: new_limit})
    }
  }

  const ReduceLimit = () => {
    if (requests.limit > 50){
      let new_limit = requests.limit - 50;
      setRequests({...requests, limit: new_limit})
    }
  }

  const resetModals = () => {
    // Reset State & Errors
    setErrors({});
    // Reset Modals
    setOpenImage({open: false, image: null});
    setOk({ok: false, id: null});
    setBad({bad: false, id: null});
  }

  const handleApproved = async(e) => {
    e.preventDefault();

    await fetch(`/api/panel/applications/${isOk.id}/approve`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          // Reset Modals
          resetModals();
          // Update Requests
          setRequests({
            ...requests,
            requests: data.requests
          })
        } else {
          if (data.code === 'SERVER'){
            setErrors({
              Server: {
                name: '',
                error: '     ',
                type: 'danger'
              }
            })
          }
          if (data.code === 'NO_MATCH_ID'){
            setErrors({
              Server: {
                name: ' ',
                error: '     ',
                type: 'danger'
              }
            })
          }
          if (data.code === 'INVALID_TYPE'){
            setErrors({
              Server: {
                name: '',
                error: '   ',
                type: 'danger'
              }
            })
          }
          if (data.code === 'NO_USER'){
            setErrors({
              Server: {
                name: '',
                error: '     ,    .',
                type: 'danger'
              }
            })
          }
          if ([
            'INVALID_AUTH_HEADER', 
            'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
            'ACCESS_DENIED', 'AUTH_ERROR'
          ].includes(data.code)) {
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
          if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
            localStorage.clear();
            sessionStorage.clear();
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
        }
      })
    })
    
  }

  const handleReject = async(e) => {
    e.preventDefault();

    await fetch(`/api/panel/applications/${isBad.id}/reject`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': `Bearer ${localStorage.maidan_jwt_$m3}`
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          // Reset Modals
          resetModals();
          // Update Requests
          setRequests({
            ...requests,
            requests: data.requests
          })
        } else {
          if (data.code === 'SERVER'){
            setErrors({
              Server: {
                name: '',
                error: '     ',
                type: 'danger'
              }
            })
          }
          if (data.code === 'NO_MATCH_ID'){
            setErrors({
              Server: {
                name: ' ',
                error: '     ',
                type: 'danger'
              }
            })
          }
          if (data.code === 'INVALID_TYPE'){
            setErrors({
              Server: {
                name: '',
                error: '   ',
                type: 'danger'
              }
            })
          }
          if ([
            'INVALID_AUTH_HEADER', 
            'NO_TOKEN', 'INVALID_ACCESS_LEVEL', 
            'ACCESS_DENIED', 'AUTH_ERROR'
          ].includes(data.code)) {
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
          if (['NOT_LOGGED_IN', 'LOGIN_REQUIRED'].includes(data.code)){
            localStorage.clear();
            sessionStorage.clear();
            setRedirect({
              redirect: true,
              flash: {
                type: 'error',
                title: '',
                message: '      .'
              }
            })
          }
        }
      })
    })

  }

  const loadErrors = () => {
    if (Object.keys(errors).length !== 0 && errors.constructor === Object){
      return (
        <div>
          {errors ? Object.entries(errors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  if (redirect.redirect){
    return <Redirect to={{
      pathname: '/',
      state: {flash: redirect.flash}
    }}/>
  }

  return(
    <div className="table-responsive mt-4">
      
      <table className="table table-sm table-bordered">
        <thead>
          <tr className="bg-light-grey">
            <th className="font-weight-bold" scope="col">ID</th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col">  </th>
            <th className="font-weight-bold" scope="col">  </th>
            <th className="font-weight-bold" scope="col"> </th>
            <th className="font-weight-bold" scope="col"> / </th>
          </tr>
        </thead>
        {LoadRequests()}
      </table>
      <div className="justify-content-center">
        {(requests.requests && requests.limit < requests.requests.length) ? (<button onClick={AddLimit} className="btn btn-sm btn-outline-primary"> </button>) : null}
        {requests.limit > 50 ? (<button onClick={ReduceLimit} className="btn btn-sm btn-outline-secondary"> </button>) : null}
      </div>
    
      {/* Open Image Modal */}
      <Modal show={openImage.open} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">  </Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">
          <div className="container">
            <img src={`https://maidancom.s3.us-east-2.amazonaws.com/${openImage.image}`} alt={openImage.image} className="img-fluid img-thumbnail"/>
          </div>
          {/* <button className="btn btn-sm btn-outline-primary mt-2"> </button> */}
        </Modal.Body>
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="outline-grey" onClick={resetModals}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>

      {/* Bad Modal */}
      <Modal show={isBad.bad} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">  (#{isBad.id})</Modal.Title>
        </Modal.Header>
        
        <Modal.Body className="text-center mt-2">
          {loadErrors()}

          <p className="font-weight-bold text-danger">
                  
          </p>
          
        </Modal.Body>
        
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={resetModals}>
              
            </Button>
            <Button onClick={handleReject} variant="danger">
              
            </Button>
          </div>
        </Modal.Footer>

      </Modal>

      {/* Ok Modal */}
      <Modal show={isOk.ok} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">  (#{isOk.id})</Modal.Title>
        </Modal.Header>
        
        <Modal.Body className="text-center mt-2">
          {loadErrors()}

          <p className="font-weight-bold text-success">
                  
          </p>
          
        </Modal.Body>
        
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={resetModals}>
              
            </Button>
            <Button onClick={handleApproved} variant="success">
              
            </Button>
          </div>
        </Modal.Footer>

      </Modal>

   </div>
  )
}


export default ToProviderTable