import React, { useState, useEffect } from 'react';
import { Link, Redirect } from 'react-router-dom';

// Modals
import { Modal, Button } from 'react-bootstrap';

// Icons
import PaypalIcon from '../../static/payment/PayPalBtn.jpg';
import MadaIcon from '../../static/payment/MadaBtn.jpg';
import BankIcon from '../../static/payment/BankBtn.jpg';
import PayPalBtn from './PayPalBtn';




export const Balance = () => {

  
  const [modalErrors, setModalErrors] = useState({});
  const [errors, setErrors] = useState({});
  const [balance, setBalance] = useState(0);

  // Pull Form
  const [pullstate, setPullState] = useState({paypal_email: null, amount: null});
  // const [pullErrors, setPullErrors] = useState({});

  // Modals
  const [isFlash, setFlash] = useState(false);
  const [flashMessage, setFlashMessage] = useState({});
  
  const [transferState, setTransferState] = useState({
    user_id: null,
    method: null,
    verified: false
  });
  // Pay Using Bank Transfer
  const [isBank, setBank] = useState(false);
  // Pay Using Paypal
  const [isPaypal, setPaypal] = useState(false);
  const [confirmAmount, setConfirm] = useState(false);
  
  const [isMada, setMada] = useState(false);

  // Redirect
  const [redirect, setRedirect] = useState(false);

  useEffect(() => {
    // Check If User is Logged-in
    if (!localStorage.maidan_jwt_$m3){
      setRedirect(true);
    }

    fetch('/api/user/balance', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }
    }).then(resp => {
      resp.json().then(data => {
        if (resp.ok){
          setTransferState({
            user_id: data.user_id,
            method: null,
            verified: false
          })
          setBalance(data.balance)
          setPullState({
            amount: null,
            paypal_email: data.email
          })
        } else {
          setErrors({
            FetchError: {
              name: '',
              error: '      ,    .',
              type: 'danger'
            }
          })
        }
      })
    })

    
  }, [])


  const handleTransferSubmit = (e) => {
    e.preventDefault();
    const TransferBalance = async() => {
      await fetch(`/api/user/balance/${transferState.user_id}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        }, body: JSON.stringify(transferState)
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            resetModals();
            setFlash(true);
            setFlashMessage({
              title: '  ',
              message: '   ,    ,       .',
              state: 'success'
            });
          } else {
            if (data.code === 'NO_MATCH_ID'){
              resetModals();
              setFlash(true);
              setFlashMessage({
                title: '  ',
                message: '   ,    ,       .',
                state: 'error',
                logout: true
              })
            }
            if (data.code === 'INVALID_REQUEST'){
              resetModals();
              setFlash(true);
              setFlashMessage({
                title: '  ',
                message: '     ,    ,   .',
                state: 'error'
              })
            }
            if (data.code === 'MISSING_VALUES'){
              setModalErrors({
                Values: {
                  name: '',
                  error: '    .',
                  type: 'warning'
                }
              })
            }
            if (data.code === 'INVALID_METHOD'){
              setModalErrors({
                Method: {
                  name: ' ',
                  error: '      .',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'SERVER'){
              setModalErrors({
                Server: {
                  name: '',
                  error: '    ,     .',
                  type: 'danger'
                }
              })
            }
            if (data.code === 'DISABLED'){
              resetModals();
              setFlash(true);
              setFlashMessage({
                title: '',
                message: '   ,    .',
                state: 'error',
                logout: true
              })
            }
          }
        })
      })
    }

    TransferBalance();
  }

  const handleTransferChange = (e) => {
    e.preventDefault();

    setTransferState({
      ...transferState,
      [e.target.id]: e.target.value
    })
  }

  const submitAmount = (e) => {
    e.preventDefault();

    const CheckUser = async() => {
      const user = localStorage.maidan_user && JSON.parse(localStorage.maidan_user);
      await fetch(`/api/user/${user.id}/is_active`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        }
      }).then(resp => {
        resp.json().then(data => {
          if (resp.ok){
            setConfirm(true);
          } else {
            if (data.code === 'NO_MATCH_ID'){
              resetModals();
              setFlash(true);
              setFlashMessage({
                title: '  ',
                message: '   ,    ,       .',
                state: 'error',
                logout: true
              })
            }
            if (data.code === 'DISABLED'){
              resetModals();
              setFlash(true);
              setFlashMessage({
                title: '',
                message: '   ,    .',
                state: 'error',
                logout: true
              })
            }
          }
        })
      })
    }

    CheckUser();
  }

  const resetAmount = (e) => {
    e.preventDefault();
    setConfirm(false);
  }

  /* Methods */
  
  const BankMethod = () => {
    setTransferState({
      ...transferState,
      method: 'bank',
      verified: false
    });
    setBank(true);
  }

  const PaypalMethod = () => {
    setTransferState({
      ...transferState,
      method: 'paypal',
      verified: true
    });
    setPaypal(true);
  }

  const MadaMethod = () => {
    setTransferState({
      ...transferState,
      method: 'mada',
      verified: true
    });
    setMada(true);
  }

  const BalanceNPayment = () => {

    return(
      <div className="container">
        
        <div className="row justify-content-center">

          <div className="col-sm-12">
            <span className="display-8 font-weight-normal text-muted">  : </span>
            <p className="font-weight-bold large-link mt-2">
              <i className="fa fa-dollar-sign ml-1"/>
              {parseFloat(balance).toFixed(2)}
            </p>
          </div>

        </div>

        <hr/>

        <div className="row justify-content-center">

          <div className="col-sm-12">
            <span className="display-8 font-weight-bold qsemh-turq">
                 : 
            </span>

            <div className="row mt-5">
              <div className="col-sm-4">
                <button title=" " onClick={PaypalMethod} className="payBtn">
                  <img loading="lazy" src={PaypalIcon} alt=" " className="img-fluid payment-icon"/>
                </button>
              </div>

              <div className="col-sm-4 pt-mobile-lg">
                <button title="   " onClick={BankMethod} className="payBtn">
                  <img loading="lazy" src={BankIcon} alt="   " className="img-fluid payment-icon"/>
                </button>
              </div>

              <div className="col-sm-4 pt-mobile-lg">
                <button title="  " onClick={MadaMethod} className="payBtn">
                  <img loading="lazy" src={MadaIcon} alt="  " className="img-fluid payment-icon"/>
                </button>
              </div>

            </div>

          </div>

        </div>

      </div>
    )
  }

  /* Methods */

  const PullBalanceForm = () => {

    const redirectToPaypal = () => {
      window.open("https://www.paypal.com")
    }

    return(
      <form action="">

        <div className="container text-right">

          <p className="info-weight text-muted">
            <i className="fa fa-star-of-life fa-xs"/>       <Link to="#" onClick={redirectToPaypal}>PayPal</Link>,     .
          </p>

          <div className="row mt-5">

            <div className="col-sm-12">

              <label htmlFor="paypal_email" className="font-weight-bold text-muted">  PayPal : </label>
              <div className="input-group">
                <input dir='ltr' type="email" id="paypal_email" defaultValue={pullstate.paypal_email} className="form-control col-sm-8"/>
              </div>

            </div>

            <div className="col-sm-12 mt-4">

              <label htmlFor="amount" className="font-weight-bold text-muted">  : </label>
              <div className="input-group">
                <input type="number" step="0.01" min="0" max={parseFloat(balance).toFixed(2)} defaultValue="0.00" className="form-control col-sm-8" aria-describedby="amountHelpInline"/>
              </div>
              <p id="amountHelpInline" className="font-weight-light font-info text-muted">
                                 PayPal.  0   .
              </p>
            </div>

          </div>

          <div className="input-group mt-2">
            <button className="btn btn-md btn-primary"> </button>
          </div>

        </div>

      </form>
    )
  }

  const loadErrors = () => {
    if (Object.keys(errors).length !== 0 && errors.constructor === Object){
      return (
        <div>
          {errors ? Object.entries(errors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  const loadModalErrors = () => {
    if (Object.keys(modalErrors).length !== 0 && modalErrors.constructor === Object){
      return (
        <div>
          {modalErrors ? Object.entries(modalErrors).map((e, id) => {
            return (
              <div key={id} className={`alert alert-${e[1].type} text-right`}>
                <p className="font-weight-bold">
                  {e[1].name} : {e[1].error}
                </p>
              </div>
            )
          }): null}
        </div>
      )
    }
  }

  const resetModals = () => {
    if (flashMessage.logout){
      localStorage.removeItem('maidan_jwt_$m3');
      localStorage.removeItem('maidan_user');
      localStorage.removeItem('logged');
      localStorage.removeItem('maidan_user_access');
      localStorage.removeItem('profile_img');
      sessionStorage.clear();
      window.location.href = window.location.origin
    }
    // Reset States & Errors
    setTransferState({
      ...transferState,
      fullname: null,
      email: null,
      bank_account: null,
      amount: null,
      method: null,
      verified: false
    });
    setModalErrors({});
    setFlashMessage({});

    // Reset Modals
    setConfirm(false);
    setPaypal(false);
    setFlash(false);
    setBank(false);
    setMada(false);
  }

  if (redirect){
    return <Redirect to="/"/>
  }

  return(
    <div className="mb-5 pb-5 pt-5">

      <div className="container mt-5 pt-5">

      <div className="row justify-content-center">
        
        <div className="col-sm-10">

          <div className="card bg-light-grey text-center shadow-sm p-4">
            <span className="display-8 card-title text-right pr-2">  : </span>

            <div className="card-body text-center">
                
              <div className="mt-2">
                {loadErrors()}
              </div>

              <div className="mt-5">
                {BalanceNPayment()}
              </div>
                
            </div>

          </div>
        
        </div>

        <div className="col-sm-10 mt-3">
          
          <div className="card bg-light-grey text-center shadow-sm p-4">
            <span className="display-8 card-title text-right pr-2">    : </span>

            <div className="card-body text-center">
                
              <div className="mt-2">
                {/* {loadFormErrors()} */}
              </div>

              <div className="mt-5">
                {PullBalanceForm()}
              </div>
                
            </div>

          </div>

        </div>

      </div>

      </div>

      {/* Flash Message Modal */}
      <Modal show={isFlash} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">{flashMessage.title}</Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">
          {flashMessage.state === 'success' ? (
            <i className="fa fa-check-circle text-success fa-3x"/>
          ) : flashMessage.state === 'error' ? (
            <i className="fa fa-times-circle text-danger fa-3x"/>
          ) : null}
          <p className="mt-2 lead text-muted">
            {flashMessage.message}
          </p>
        </Modal.Body>
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="outline-primary" onClick={resetModals}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>

      {/* Bank Payment Modal */}
      <Modal show={isBank} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">    </Modal.Title>
        </Modal.Header>
        
        <Modal.Body className="text-center">
        
          <div className="container">
            <p className="font-info font-weight-bold text-muted">
              <i className="fa fa-star-of-life fa-xs ml-2"/>
                 (    )    :
            </p>
            <p className="font-info font-weight-bold text-dark">
              000000000000000
            </p>
          </div>

          <div className="container mt-2">
            {loadModalErrors()}
          </div>

          <form id="mainForm" onSubmit={handleTransferSubmit}>
            <div className="container mt-4">

              <div className="row text-right">

                <div className="col-sm-10">
                  <label htmlFor="fullname" className="font-weight-bold">*   : </label>
                  <div className="input-group">
                    <input onChange={handleTransferChange} required id="fullname" type="text" placeholder="  " className="form-control col-sm-12"/>
                  </div>
                </div>

                <div className="col-sm-10 mt-3">
                  <label htmlFor="email" className="font-weight-bold">  : </label>
                  <div className="input-group">
                    <input onChange={handleTransferChange} id="email" type="email" placeholder="  ()" className="form-control col-sm-12"/>
                  </div>
                  <p className="font-sm text-muted">
                    <i className="fa fa-star-of-life fa-xs ml-2"/>
                            .
                    (           )
                  </p>
                </div>

                <div className="col-sm-10 mt-2">
                  <label htmlFor="bank_account" className="font-weight-bold">*   : </label>
                  <div className="input-group">
                    <input onChange={handleTransferChange} required id="bank_account" type="text" placeholder=" " className="form-control col-sm-12"/>
                  </div>
                </div>

                <div className="col-sm-10 mt-3">
                  <label htmlFor="amount" className="font-weight-bold">*   : </label>
                  <div className="input-group">
                    <input onChange={handleTransferChange} required id="amount" type="number" min="0" step="0.01" placeholder="  ()" className="form-control col-sm-12"/>
                  </div>
                </div>

              </div>

            </div>
          </form>
          
        </Modal.Body>
        
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={resetModals}>
              
            </Button>
            <Button type="submit" form="mainForm" variant="success">
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>

      {/* PayPal Payment Modal */}
      <Modal show={isPaypal} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">   </Modal.Title>
        </Modal.Header>
        
        <Modal.Body className="text-center">
        
          <div className="container">
            <p className="font-info font-weight-bold text-muted">
              <i className="fa fa-star-of-life fa-xs ml-2"/>
                  ($)   .
            </p>
          </div>
          
          <form onSubmit={submitAmount}>
            <div className="container text-right mt-4">

              <div className="row">

                <div className="col-sm-10">
                  <label htmlFor="fullname" className="font-weight-bold">  : </label>
                  <div className="input-group">
                    <input disabled={confirmAmount} onChange={handleTransferChange} id="fullname" type="text" placeholder="  ()" className="form-control col-sm-12"/>
                  </div>
                </div>

                <div className="col-sm-10 mt-3">
                  <label htmlFor="amount" className="font-weight-bold">*  : </label>
                  <div className="input-group">
                    <input disabled={confirmAmount} onChange={handleTransferChange} required id="amount" type="number" min="0" step="0.01" placeholder=" ( $)" className="form-control col-sm-12"/>
                    <div className="input-group-append">
                      <button disabled={confirmAmount} type="submit" className={"form-control btn-md " + (confirmAmount ? "btn-outline-grey" : "btn-outline-info")}></button>
                    </div>
                  </div>
                </div>

              </div>

              <p hidden={!confirmAmount} className="font-info mt-2"><Link to="#" onClick={resetAmount}></Link></p>

            </div> 
          </form>
          
          <div className="container">
            {confirmAmount ? <PayPalBtn user_id={transferState.user_id} amount={transferState.amount} fullname={transferState.fullname} /> : null}
          </div>

        </Modal.Body>
        
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={resetModals}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>

      {/* Mada Payment Modal */}
      <Modal show={isMada} onHide={resetModals}>
        <Modal.Header className="justify-content-center">
          <Modal.Title className="display-8">    </Modal.Title>
        </Modal.Header>
        
        <Modal.Body className="text-center">

          <div className="container">
            <p className="font-info text-primary">
              <i className="fa fa-star-of-life fa-xs ml-2"/>
                     .
            </p>
          </div>
        
          <div className="container mt-4">
            <button onClick={() => {window.location = "https://secure.telr.com/gateway/ql/MaidanHerokuapp_123683.html"}} className="btn btn-md btn-success font-weight-bold">
               .
            </button>
          </div>

        </Modal.Body>
        
        <Modal.Footer>
          <div className="input-group justify-content-center">
            <Button variant="grey" onClick={resetModals}>
              
            </Button>
          </div>
        </Modal.Footer>
      </Modal>

    </div>
  )
}